import React from 'react'
import { Field } from 'formik'
import { FormikSelectComponent } from '../../../utilities/formUtils'
    

const MiscOperationType = ({ name, isTxn, isLabel, label, isSelection, userProps }, props) => {
    const opt = [{
        key : "QTY(KG)",
        value : "QTY(KG)",
        text : "QTY(KG)"
    },{
        key : "Milk Price",
        value : "Milk Price",
        text : "Milk Price"
    },{
        key : "FAT/SNF(KG)",
        value : "FAT/SNF(KG)",
        text : "FAT/SNF(KG)"
    }]


    return (
        <Field name={name} isLabel={isLabel} isTxn={isTxn} label={label} isSelection={isSelection} component={FormikSelectComponent} userProps={{ options: opt }}> </Field>
    )

}


export default MiscOperationType;
