import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { getParty, getPartyParams, getSavedPartyId, getNotification } from '../data/selectors'
import { createParty, editParty, setNotifyDoneParty } from '../data/actions'
import { partySchema } from '../data/model'
import { Container, Header, Form, Button, Grid, Table, Icon } from 'semantic-ui-react'
import { Formik, Field, Form as FormikForm } from 'formik'
import { FormikAmountComponent, FormikInputComponent, FormikTextAreaComponent } from '../../../utilities/formUtils'
import PlantSelect from '../../party/components/plantSelect'
import GroupSelect from '../../group/components/groupSelect'
import BankSelect from '../../bank/components/bankSelect'
import GroupPopup from '../../group/components/groupPopup'
import BankPopup from '../../bank/components/bankPopup'
import Notification from '../../../utilities/notificationUtils'


const PartyForm = (props) => {

    const party = useSelector(state => getParty(state, props))
    const savedPartyId = useSelector(state => getSavedPartyId(state, props))
    const params = useSelector(state => getPartyParams(state, props))
    const dispatch = useDispatch()

    console.log("params-------", params)

    const saveParty = (values, resetForm) => {
        if (props.match.path === '/party/create') {
            let bankMst = { id: values.bankM !== undefined && values.bankM !== "" && values.bankM !== 0 ? values.bankM : null }
            values.bankMaster = bankMst
            let groupMst = { id: values.groupM !== undefined && values.groupM !== "" && values.groupM !== 0 ? values.groupM : null }
            values.groupMaster = groupMst
            delete values.bankM
            delete values.groupM
            dispatch(createParty(values))
            resetForm()
        }
        if (props.match.path === '/party/edit/:id') {
            let bankMst = { id: values.bankM !== undefined && values.bankM !== "" && values.bankM !== 0 ? values.bankM : null }
            values.bankMaster = bankMst
            let groupMst = { id: values.groupM !== undefined && values.groupM !== "" && values.groupM !== 0 ? values.groupM : null }
            values.groupMaster = groupMst
            delete values.bankM
            delete values.groupM
            dispatch(editParty(values))
            props.history.push(`/party/list`)
        }
    }

    return (
        <Container>
            <Grid columns='equal' className="borderButtomTable">
                <Grid.Row>
                    <Grid.Column width={4}>
                        <Header as='h1' className="VerticalAlign" align='left'>{params.title}</Header>
                    </Grid.Column>
                    <Grid.Column width={6} floated={'right'} align='right'></Grid.Column>
                </Grid.Row>
            </Grid>
            <Container className='formContainer'>
                <Formik id="partyForm" size="large" width={5}
                    initialValues={party}
                    validationSchema={partySchema}
                    onSubmit={(values, { resetForm }) => saveParty(values, resetForm)}
                    render={({ handleSubmit, errors, onChange, values, handleChange, setFieldValue }) => (
                        <Form as={FormikForm} size="small" className="CustomeForm" width={5} onSubmit={handleSubmit}>
                            <Table>
                                <Table.Body>
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}><label className="boldText">Plant: </label></Table.Cell>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <Form.Group widths={2}>
                                                <PlantSelect name="plantNo" isLabel={false} label=" " isSelection={true} userProps={{ setField: setFieldValue, setName: 'plantNo' }} type='' />
                                            </Form.Group>
                                        </Table.Cell>
                                        <Table.Cell colSpan={4} style={{ width: '60%' }}>
                                            <Form.Group widths={2}>
                                                <Field name="plantName" label=' ' component={FormikInputComponent} />
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}><label className="boldText">Name: </label></Table.Cell>
                                        <Table.Cell colSpan={5} style={{ width: '80%' }}>
                                            <Form.Group widths={2}>
                                                <Field name="partyName" label=' ' component={FormikInputComponent} />
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}><label className="boldText">Address: </label></Table.Cell>
                                        <Table.Cell colSpan={5} style={{ width: '80%' }}>
                                            <Form.Group widths={2}>
                                                <Field name="address" label=' ' component={FormikTextAreaComponent} userProps={{ maxLength: 400, displayCount: true, height: 3 }} focus={true} />
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}><label className="boldText">Phone: </label></Table.Cell>
                                        <Table.Cell colSpan={5} style={{ width: '80%' }}>
                                            <Form.Group widths={2}>
                                                <Field name="phone" label=' ' component={FormikInputComponent} />
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}><label className="boldText">Bank: </label></Table.Cell>
                                        <Table.Cell colSpan={4} style={{ width: '70%' }}>
                                            <Form.Group widths={2}>
                                                <BankSelect name="bankM" isLabel={false} label=" " isSelection={true} userProps={{ setField: setFieldValue, setName: 'bankM' }} type='' />
                                            </Form.Group>
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '10%' }}>
                                            <BankPopup setFieldVal={setFieldValue} name='bankPopup' />
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}><label className="boldText">A/C No: </label></Table.Cell>
                                        <Table.Cell colSpan={5} style={{ width: '80%' }}>
                                            <Form.Group widths={2}>
                                                <Field name="partyBankAccNo" label=' ' component={FormikInputComponent} />
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}><label className="boldText">Balance: </label></Table.Cell>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <Form.Group widths={2}>
                                                <Field name="balance" label=' ' component={FormikAmountComponent} />
                                            </Form.Group>
                                        </Table.Cell>
                                        <Table.Cell colSpan={4} style={{ width: '60%' }}>
                                            <label className="tipText">Tip: ( Negative value for credit balance )</label>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}><label className="boldText">Nick Name: </label></Table.Cell>
                                        <Table.Cell colSpan={5} style={{ width: '80%' }}>
                                            <Form.Group widths={2}>
                                                <Field name="nickName" label=' ' component={FormikInputComponent} />
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}><label className="boldText">Group: </label></Table.Cell>
                                        <Table.Cell colSpan={4} style={{ width: '70%' }}>
                                            <Form.Group widths={2}>
                                                <GroupSelect name="groupM" isLabel={false} label=" " isSelection={true} userProps={{ setField: setFieldValue, setName: 'groupM' }} type='' />
                                            </Form.Group>
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '10%' }}>
                                            <GroupPopup setFieldVal={setFieldValue} name='groupPopup' />
                                        </Table.Cell>
                                    </Table.Row>
                                </Table.Body>
                            </Table>
                            {console.log("text----------------" + params.submitButtonText)}
                            <Button type="submit" icon labelPosition='right' className="CustomeBTN" color='vk' style={{ float: 'right' }}>
                                {params.submitButtonText}
                                <Icon name={props.match.path === '/party/create' ? 'add circle' : 'arrow alternate circle up outline'} />
                            </Button>
                            <br /><br /><br /><br />
                            {savedPartyId && savedPartyId !== 0 && props.match.path === '/party/create' ?
                                <Notification id={savedPartyId} notifySelector={getNotification} notifyDoneAction={setNotifyDoneParty} type='save' />
                                :
                                null}
                        </Form>
                    )}
                />
            </Container>
        </Container>
    )
}

export default PartyForm;