import * as type from './types'

export function createParty(party) {
    return {
        type: type.CREATE_PARTY,
        payload: party,
        txn: 'initiated',
    };
}

export function savedParty(party) {
    return {
        type: type.SAVED_PARTY,
        payload: party.data.data,
        txn: party.data.type,
        msg: party.data.msg,
        diff: true
    };
}

export function editParty(party) {
    return {
        type: type.EDIT_PARTY,
        payload: party,
        txn: 'initiated',
    };
}

export function fetchParty() {
    return {
        type: type.FETCH_PARTY
    };
}

export function fetchedParty(party) {
    return {
        type: type.FETCHED_PARTY,
        payload: party
    };
}


export function fetchPartyById(party) {
    return {
        type: type.FETCH_PARTY_BY_ID,
        payload: party
    };
}

export function fetchedPartyById(party) {
    return {
        type: type.FETCHED_PARTY_BY_ID,
        payload: party
    };
}

export function setNotifyDoneParty(props) {
    return {
        type: type.NOTIFICATION_DONE_PARTY,
        payload: props
    };
}


export function deleteParty(party) {
    return {
        type: type.DELETE_PARTY,
        payload: party
    };
}

export function deletedParty(party) {
    return {
        type: type.DELETED_PARTY,
        payload: party
    };
}

export function partyPrint(){
    return {
        type: type.PRINT_PARTY
    };
}