import { createSelector } from 'reselect'
import { startsWith, cloneDeep } from 'lodash';
import { party } from './model'

export const getIsFetchingParty = (state, props) => state.party.params.isFetching
export const getIsPartyFetched = (state, props) => state.party.params.partyFetched
export const getSavedPartyId = (state, props) => state.party.params.savedPartyId
export const getPartyList = (state, prop) => state.party.byId
export const getPartyObjById = (state, id) => state.party.byId[id]
export const getNotification = (state, id) => state.party.notifications[id]

export const getParty = (state, props) => {
    if (props.match.path === '/party/create') {
        return party()
    }
    if (props.match.path === '/party/edit/:id') {
        let id = props.match.params.id
        let obj = cloneDeep(state.party.byId[id])
        console.log(obj)
        let bankId = obj.bankMaster ? obj.bankMaster.id : 0
        obj.bankM = bankId
        let groupId = obj.groupMaster ? obj.groupMaster.id : 0
        obj.groupM = groupId
        return obj
    }
}

export const getPartyParams = (state, props) => {
    const params = {}
    if (props.match.path === '/party/create') {
        params.title = state.party.params.createTitle
        params.submitButtonText = state.party.params.createSubmitButtonText
    } else if (props.match.path === '/party/edit/:id') {
        params.title = state.party.params.editTitle
        params.submitButtonText = state.party.params.editSubmitButtonText
    }
    return params
}


export const selectParty = createSelector(
    getPartyList,
    party => {
        const keys = Object.keys(party)
        const obj = keys.map((key) => { return { key: key, value: parseInt(key, 10), text: party[key].partyName } })
        return obj
    }
)