import { combineReducers } from 'redux';
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr';
import moment from 'moment'

// Define a finYears schema
const finYearSchema = new schema.Entity('finYears',{}, { idAttribute: 'id' });
const finYearListSchema = [finYearSchema];


const defaultFinYearParams = {
  createTitle: '',
  createSubmitButtonText: 'Save',
  editTitle: '',
  editSubmitButtonText: 'Update',
  finYearFetched: false,
  savedFinYearId: 0
}


function byId(state = {}, action) {
    if (action.type === 'SAVED_FINYEAR') {
      const finYearID = action.payload.id;
      return {...state, 
        [finYearID]: action.payload
      }
    }
    if (action.type === 'FETCHED_FINYEAR') { 
        const normalizedFinYear = normalize(action.payload, finYearListSchema);
        return merge({},normalizedFinYear.entities.finYears)
    } 
    if (action.type === 'FETCHED_FINYEAR_BY_ID') { 
      const normalizedFinYear = normalize(action.payload, finYearSchema);
      return merge({},normalizedFinYear.entities.finYears)
    } 
    else {
      return state
    }
  }


  function byListId(state = {}, action) {
    if (action.type === 'SAVED_FINYEAR') {
      const finYearID = action.payload.id;
      return {...state, 
        [finYearID]: action.payload
      }
    }
    if (action.type === 'FETCHED_FINYEAR') { 
        const normalizedFinYear = normalize(action.payload, finYearListSchema);
        return merge({},normalizedFinYear.entities.finYears)
    } 
    else {
      return state
    }
  }

  

  function notifications(state = {}, action) {
    if (action.type === 'SAVED_FINYEAR') { 
      return {...state, 
        [action.payload.id]: { ...state[action.payload.id],
          save: { 
            status : action.txn === "success" ? 'success' : 'error',
            msg : action.msg
          }
        }
      }
    } 
    if(action.type === 'NOTIFICATION_DONE_FINYEAR'){
      const {id, type} = action.payload
      // Remove the 'id' element from state
      const { [id]: idValue, ...restOfState } = state;
      // Remove the 'type' from the 'id' element
      const { [type]: removedValue, ...restOfId } = idValue;
      // Merge back together
      const finalState = { ...restOfState, [id]: restOfId };     
      return finalState
  
    } else {
      return state
    }
  }

  function params(state = defaultFinYearParams, action) {
    if (action.type === 'FETCH_FINYEAR') { 
        return {...state, 
          searchParams: action.payload
        }
    } 
    if (action.type === 'SAVED_FINYEAR') { 
        return {...state, 
          savedFinYearId: action.payload.id
        }
    } 
    if (action.type === 'NOTIFICATION_DONE_FINYEAR') { 
      return {...state, 
        savedFinYearId: action.payload.id
      }
    } 
    if (action.type === 'FETCHED_FINYEAR') { 
      return {...state, 
        finYearFetched: true
      }
    }
    if (action.type === 'FETCHED_FINYEAR_BY_ID') { 
        return {...state,
          searchParams: {}
        }
    } 
    else {
        return state
    }
  }



const finYear = combineReducers({
    byId,
    byListId,
    params,
    notifications
});


export default finYear;
