import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { milkRatesSchema } from '../data/model'
import { Container, Header, Form, Button, Grid, Table, Icon } from 'semantic-ui-react'
import { Formik, Field, Form as FormikForm } from 'formik'
import { FormikAmountComponent } from '../../../utilities/formUtils'
import TypeSelect from './typeSelect'
import * as select from '../data/selectors'
import { editMilkRates, setNotifyDoneMilkRates } from '../data/actions'
import Notification from '../../../utilities/notificationUtils'


const MilkRatesForm = (props) => {
    
    const milkRates = useSelector(state => select.getMilkRates(state, props))
    const savedMilkRatesId = useSelector(state => select.getSavedMilkRatesId(state, props))
    const dispatch = useDispatch()

    const saveMilkRates = (values, resetForm) => {
        dispatch(editMilkRates(values))
        resetForm()
    }

    return (
        <Container>
            <Grid columns='equal' className="borderButtomTable">
                <Grid.Row>
                    <Grid.Column width={4}>
                        <Header as='h1' className="VerticalAlign" align='left'>Set Milk Price / Rates</Header>
                    </Grid.Column>
                    <Grid.Column width={6} floated={'right'} align='right'></Grid.Column>
                </Grid.Row>
            </Grid>
            <Container className='formContainer'>
                <Formik id="standardForm" size="large" width={5}
                    initialValues={milkRates}
                    validationSchema={milkRatesSchema}
                    onSubmit={(values, { resetForm }, ) => saveMilkRates(values, resetForm)}
                    render={({ handleSubmit, errors, onChange, values, handleChange, setFieldValue }) => (
                        <Form as={FormikForm} size="small" className="CustomeForm" width={5} onSubmit={handleSubmit}>
                            <Table>
                                <Table.Body>
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}><label className="boldText">Type: </label></Table.Cell>
                                        <Table.Cell colSpan={2} style={{ width: '80%' }}>
                                            <Form.Group widths={2}>
                                                <TypeSelect name="type" isLabel={false} label=" " isSelection={true} userProps={{ setField: setFieldValue }} type='' />
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <label className="boldText">
                                                Quantity
                                            </label>
                                        </Table.Cell>
                                        <Table.Cell textAlign="center" style={{ width: '40%' }}>
                                            <label className="boldText">
                                                Fat
                                            </label>
                                        </Table.Cell>
                                        <Table.Cell textAlign="center" style={{ width: '40%' }}>
                                            <label className="boldText">
                                                SNF
                                            </label>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}><label className="boldText">Good </label></Table.Cell>
                                        <Table.Cell style={{ width: '40%' }}>
                                            <Form.Group widths={2}>
                                                <Field name="goodFat" label=' ' component={FormikAmountComponent} />
                                            </Form.Group>
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '40%' }}>
                                            <Form.Group widths={2}>
                                                <Field name="goodSnf" label=' ' component={FormikAmountComponent} />
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}><label className="boldText">Bad </label></Table.Cell>
                                        <Table.Cell style={{ width: '40%' }}>
                                            <Form.Group widths={2}>
                                                <Field name="badFat" label=' ' component={FormikAmountComponent} />
                                            </Form.Group>
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '40%' }}>
                                            <Form.Group widths={2}>
                                                <Field name="badSnf" label=' ' component={FormikAmountComponent} />
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                </Table.Body>
                            </Table>
                            <Button type="submit" icon labelPosition='right' className="CustomeBTN" color='vk' style={{ float: 'right' }}>
                                Update
                                <Icon name='arrow alternate circle up outline' />
                            </Button>
                            <br /><br /><br /><br />
                            {savedMilkRatesId && savedMilkRatesId !== 0 ? 
                            <Notification id={savedMilkRatesId} notifySelector={select.getNotification} notifyDoneAction={setNotifyDoneMilkRates} type='save'/>
                            :
                            null}
                        </Form>
                    )}
                />
            </Container>
        </Container>
    )
}

export default MilkRatesForm;