import { createSelector } from 'reselect'
import { startsWith, cloneDeep } from 'lodash';
import { finYear } from './model'

export const getIsFetchingFinYear = (state, props) => state.finYear.params.isFetching
export const getIsFinYearFetched = (state, props) => state.finYear.params.finYearFetched
export const getSavedFinYearId = (state, props) => state.finYear.params.savedFinYearId
export const getFinYearList = (state, prop) => state.finYear.byId
export const getNotification = (state, id) => state.finYear.notifications[id]

export const getFinYear = (state, props) => {
    if(props.match.path==='/finYear/create'){
        return finYear()
    }
    if(props.match.path==='/finYear/edit/:id'){
        let _id = props.match.params.id
        let obj = cloneDeep(state.finYear.byListId[_id]);
        return obj
    }
}

export const getFinYearParams = (state, props) => {
    const params =  {}

    if(startsWith(state.router.location.pathname,'/finYear/create')){ 
        params.title = state.finYear.params.createTitle
        params.submitButtonText = state.finYear.params.createSubmitButtonText        
    };

    if(startsWith(state.router.location.pathname,'/finYear/edit/')){ 
        params.title = state.finYear.params.editTitle
        params.submitButtonText = state.finYear.params.editSubmitButtonText        
    };

    return params
}

export const selectFinYearList = createSelector(
    getFinYearList,
    finYear => { 
        const keys = Object.keys(finYear)
        // const obj = keys.map( (key) => { return { value : key, label : year[key].name }})
        const obj = keys.map( (key) => { return { key : key,  value : key, text : finYear[key].sessionName }})
        return obj
    })