import { combineReducers } from 'redux';
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr'

// Define a purchases schema
const purchaseSchema = new schema.Entity('purchases',{}, { idAttribute: 'id' });
const purchaseListSchema = [purchaseSchema];


const defaultPurchaseParams = {
  createTitle: 'Create Milk Purchase',
  createSubmitButtonText: 'Create',
  editTitle: 'Update Milk Purchase',
  editSubmitButtonText: 'Update',
  purchaseFetched: false,
  savedPurchaseId: 0
}


function byId(state = {}, action) {
    if (action.type === 'SAVED_PURCHASE') {
      const purchaseID = action.payload.id;
      return {...state, 
        [purchaseID]: action.payload
      }
    }
    if (action.type === 'EDITED_PURCHASE') {
      const purchaseID = action.payload.id
      const removeID = action.removeid
      let finalState = {...state, 
        [purchaseID]: action.payload
      }
      delete finalState[removeID]
      return finalState
    }
    if (action.type === 'FETCHED_PURCHASE_BY_ID') { 
      const normalizedPurchase = normalize(action.payload, purchaseSchema);
      return merge({},normalizedPurchase.entities.purchases)
    }
    if (action.type === 'DELETED_PURCHASE') {
      if(action.payload.type === "success"){
        const purchaseID = action.payload.id
        let finalState = {...state}
        delete finalState[purchaseID]
        return finalState
      }else{
        return state
      }
    } else {
      return state
    }
  }


  function byListId(state = {}, action) {
    if (action.type === 'SAVED_PURCHASE') {
      const purchaseID = action.payload.id;
      return {...state, 
        [purchaseID]: action.payload
      }
    }
    if (action.type === 'EDITED_PURCHASE') {
      const purchaseID = action.payload.id
      const removeID = action.removeid
      let finalState = {...state, 
        [purchaseID]: action.payload
      }
      delete finalState[removeID]
      return finalState
    }
    if (action.type === 'FETCHED_PURCHASE') { 
        const normalizedPurchase = normalize(action.payload, purchaseListSchema);
        return merge({}, normalizedPurchase.entities.purchases)
    } 
    if (action.type === 'DELETED_PURCHASE') {
      if(action.payload.type === "success"){
        const purchaseID = action.payload.id
        let finalState = {...state}
        delete finalState[purchaseID]
        return finalState
      }else{
        return state
      }
    } else {
      return state
    }
  }

  

  function notifications(state = {}, action) {
    if (action.type === 'DELETED_PURCHASE') {
      return {...state, 
        [action.payload.id]: { ...state[action.payload.id],
          delete: { 
            status : action.payload.type === "success" ? 'success' : 'error',
            msg : action.payload.msg
          }
        }
      }
    } 
    if (action.type === 'SAVED_PURCHASE') { 
      return {...state, 
        [action.payload.id]: { ...state[action.payload.id],
          save: { 
            status : action.txn === "success" ? 'success' : 'error',
            msg : action.msg
          }
        }
      }
    } 
    if(action.type === 'NOTIFICATION_DONE_PURCHASE'){
      const {id, type} = action.payload
      // Remove the 'id' element from state
      const { [id]: idValue, ...restOfState } = state;
      // Remove the 'type' from the 'id' element
      const { [type]: removedValue, ...restOfId } = idValue;
      // Merge back together
      const finalState = { ...restOfState, [id]: restOfId };     
      return finalState
  
    } else {
      return state
    }
  }

  function params(state = defaultPurchaseParams, action) {
    if (action.type === 'FETCH_PURCHASE') { 
        return {...state, 
          searchParams: action.payload
        }
    }
    if (action.type === 'SAVED_PURCHASE') { 
      return {...state, 
        savedPurchaseId: action.payload.id
      }
    }
    if (action.type === 'EDITED_PURCHASE') { 
      return {...state, 
        savedPurchaseId: action.payload.id
      }
    }
    if(action.type === 'NOTIFICATION_DONE_PURCHASE'){
      return {...state, 
        savedPurchaseId: action.payload.id
      }
    }
    if (action.type === 'FETCHED_PURCHASE') { 
      return {...state, 
        purchaseFetched: true
      }
    }  
    if (action.type === 'FETCHED_PURCHASE_BY_ID') { 
        return {...state,
          searchParams: {}
        }
    } 
    else {
        return state
    }
  }



const purchase = combineReducers({
    byId,
    byListId,
    params,
    notifications
});


export default purchase;
