import { combineReducers } from 'redux';
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr';
import moment from 'moment'

// Define a standards schema
const standardSchema = new schema.Entity('standards',{}, { idAttribute: 'type' });
const standardListSchema = [standardSchema];


const defaultStandardParams = {
  createTitle: '',
  createSubmitButtonText: 'Save',
  editTitle: '',
  editSubmitButtonText: 'Update',
  savedStandardId: 0
}


function byId(state = {}, action) {
    if (action.type === 'SAVED_STANDARD') {
      const standardID = action.payload.type;
      return {...state, 
        [standardID]: action.payload
      }
    }    
    if (action.type === 'FETCHED_STANDARD') { 
      const normalizedStandard = normalize(action.payload, standardListSchema);
      return merge({},normalizedStandard.entities.standards)
    } 
    if (action.type === 'FETCHED_STANDARD_BY_ID') { 
      const normalizedStandard = normalize(action.payload, standardSchema);
      return merge({},normalizedStandard.entities.standards)
    } 
    else {
      return state
    }
  }


  function byListId(state = {}, action) {
    if (action.type === 'SAVED_STANDARD') {
      const standardID = action.payload.type;
      return {...state, 
        [standardID]: action.payload
      }
    }
    if (action.type === 'FETCHED_STANDARD') { 
        const normalizedStandard = normalize(action.payload, standardListSchema);
        return merge({},normalizedStandard.entities.standards)
    } 
    else {
      return state
    }
  }

  

  function notifications(state = {}, action) {
    if (action.type === 'SAVED_STANDARD') { 
      return {...state, 
        [action.payload.id]: { ...state[action.payload.id],
          save: { 
            status : action.txn === "success" ? 'success' : 'error',
            msg : action.msg
          }
        }
      }
    } 
    if(action.type === 'NOTIFICATION_DONE_STANDARD'){
      const {id, type} = action.payload
      // Remove the 'id' element from state
      const { [id]: idValue, ...restOfState } = state;
      // Remove the 'type' from the 'id' element
      const { [type]: removedValue, ...restOfId } = idValue;
      // Merge back together
      const finalState = { ...restOfState, [id]: restOfId };     
      return finalState
  
    } else {
      return state
    }
  }

  function params(state = defaultStandardParams, action) {
    if (action.type === 'FETCH_STANDARD') { 
        return {...state, 
          searchParams: action.payload
        }
    }
    if (action.type === 'SAVED_STANDARD') { 
        return {...state, 
          savedStandardId: action.payload.id
        }
    } 
    if (action.type === 'NOTIFICATION_DONE_STANDARD') { 
      return {...state, 
        savedStandardId: action.payload.id
      }
    } 
    if (action.type === 'FETCHED_STANDARD_BY_ID') { 
        return {...state,
          searchParams: {}
        }
    } 
    else {
        return state
    }
  }



const standard = combineReducers({
    byId,
    byListId,
    params,
    notifications
});


export default standard;
