import * as type from './types'

export function postSummaryDetails(register) {
    return {
        type: type.POST_REGISTER_SUMMARY,
        payload: register,
        txn: 'initiated',
    };
}

export function postAllPartyDetails(register) {
    return {
        type: type.POST_REGISTER_ALLPARTY,
        payload: register,
        txn: 'initiated',
    };
}

export function postSinglePartyDetails(register) {
    return {
        type: type.POST_REGISTER_ALLPARTY,
        payload: register,
        txn: 'initiated',
    };
}
