import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import { createBank } from '../data/actions'
import { bankSchema, bank } from '../data/model'
import { Modal, Form, Button, Table, Icon } from 'semantic-ui-react'
import { Formik, Field, Form as FormikForm } from 'formik'
import { FormikInputComponent } from '../../../utilities/formUtils'

// import UserDropdown from '../../users/components/userDropdown'

const BankPopup = ({ name, setFieldVal , ...props}) => {

    const dispatch = useDispatch()
    const [displayStatus, setDisplayStatus] = useState(false)

    const saveBank = (values) => {
        dispatch(createBank(values))
        setDisplayStatus(false)
    }
    return (
        <Modal size='tiny' open={displayStatus} trigger={<Button type='button' icon size='small' color='vk' title='Add Bank' onClick={()=>setDisplayStatus(true)}><Icon name='add circle'/></Button>} onClose={() => setDisplayStatus(false)}>
            <Modal.Header>Create Bank</Modal.Header>
            <Modal.Content>
            <Formik id="bankPopupForm" size="large" width={5}
                    initialValues={bank()}
                    validationSchema={bankSchema}
                    onSubmit={(values, { resetForm }) => saveBank(values, resetForm)}
                    render={({ handleSubmit, errors, onChange, values, handleChange, setFieldValue }) => (
                        <Form as={FormikForm} size="small" className="CustomeForm" width={5} onSubmit={handleSubmit}>
                            <Table>
                                <Table.Body>
                                    <Table.Row>
                                        <Table.Cell style={{width: '25%'}}><label className="boldText">Bank Name: </label></Table.Cell>
                                        <Table.Cell style={{width: '75%'}}>
                                            <Form.Group widths={2}>
                                                <Field name="bankName" label=' ' component={FormikInputComponent} />
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                </Table.Body>
                            </Table>
                            <Button type='button' onClick={handleSubmit} icon labelPosition='right' className="CustomeBTN" color='vk' style={{float: 'right'}}>
                                Create
                                <Icon name='add circle' />
                            </Button>
                            <br/><br/><br/><br/>
                        </Form>
                    )}
                />
            </Modal.Content>
        </Modal>
    )
}

export default BankPopup;