import { combineReducers } from 'redux'
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr'

// Define a milkRates schema
const milkRatesSchema = new schema.Entity('milkRates',{}, { idAttribute: 'type' });
const milkRatesListSchema = [milkRatesSchema];


const defaultMilkRatesParams = {
  createTitle: '',
  createSubmitButtonText: 'Save',
  editTitle: '',
  editSubmitButtonText: 'Update',
  savedMilkRatesId: 0
}


function byId(state = {}, action) {
  if (action.type === 'SAVED_MILKRATES') {
    const milkRatesID = action.payload.type
    return {...state, 
      [milkRatesID]: action.payload
    }
  }
    if (action.type === 'FETCHED_MILKRATES') {
      console.log("--action.payload--", action.payload) 
      const normalizedMilkRates = normalize(action.payload, milkRatesListSchema);
      return merge({},normalizedMilkRates.entities.milkRates)
    } 
    if (action.type === 'FETCHED_MILKRATES_BY_ID') { 
      const normalizedMilkRates = normalize(action.payload, milkRatesSchema);
      return merge({},normalizedMilkRates.entities.milkRates)
    } 
    else {
      return state
    }
  }


  function byListId(state = {}, action) {
    if (action.type === 'SAVED_MILKRATES') {
      const milkRatesID = action.payload.type
      return {...state, 
        [milkRatesID]: action.payload
      }
    }
    if (action.type === 'FETCHED_MILKRATES') { 
        const normalizedMilkRates = normalize(action.payload, milkRatesListSchema);
        return merge({},normalizedMilkRates.entities.milkRates)
    } 
    else {
      return state
    }
  }

  

  function notifications(state = {}, action) {
    if (action.type === 'SAVED_MILKRATES') { 
      return {...state, 
        [action.payload.id]: { ...state[action.payload.id],
          save: { 
            status : action.txn === "success" ? 'success' : 'error',
            msg : action.msg
          }
        }
      }
    } 
    if(action.type === 'NOTIFICATION_DONE_MILKRATES'){
      const {id, type} = action.payload
      // Remove the 'id' element from state
      const { [id]: idValue, ...restOfState } = state;
      // Remove the 'type' from the 'id' element
      const { [type]: removedValue, ...restOfId } = idValue;
      // Merge back together
      const finalState = { ...restOfState, [id]: restOfId };     
      return finalState
  
    } else {
      return state
    }
  }

  function params(state = defaultMilkRatesParams, action) {
    if (action.type === 'FETCH_MILKRATES') { 
        return {...state, 
          searchParams: action.payload
        }
    } 
    if (action.type === 'SAVED_MILKRATES') { 
        return {...state, 
          savedMilkRatesId: action.payload.id
        }
    } 
    if (action.type === 'NOTIFICATION_DONE_MILKRATES') { 
      return {...state, 
        savedMilkRatesId: action.payload.id
      }
    } 
    if (action.type === 'FETCHED_MILKRATES_BY_ID') { 
        return {...state,
          searchParams: {}
        }
    } 
    else {
        return state
    }
  }



const milkRates = combineReducers({
    byId,
    byListId,
    params,
    notifications
});


export default milkRates;
