import React, { useState } from 'react'
import store from 'store2'
import { useSelector, useDispatch } from 'react-redux'
import { Container, Button, Table, Icon, Dropdown, Modal } from 'semantic-ui-react'
import * as select from '../data/selectors'
import { createFinYear, setNotifyDoneFinYear } from '../data/actions'
import Notification from '../../../utilities/notificationUtils'

const FinYearForm = (props) => {

    const [displayStatus, setDisplayStatus] = useState(false)
    const [year, setYear] = useState(false)
    const dispatch = useDispatch()
    
    const finYearFetched = useSelector(state => select.getIsFinYearFetched(state, props))
    const savedFinYearId = useSelector(state => select.getSavedFinYearId(state, props))
    const options = useSelector(state => select.selectFinYearList(state, props))
    
    const onchangeDD = (value) => {
        setYear(value)
    }

    const addNewFinYear = () => {
        setDisplayStatus(true)
    }

    const addFinYear = () => {
        dispatch(createFinYear({}))
        setDisplayStatus(false)
    }

    const setSessionAndGoToApp = () => {}

    return (
        <Container className='formContainer'>
            <Table>
                <Table.Body>
                    <Table.Row>
                        <Table.Cell style={{ width: '20%' }}><label className="boldText">Financial Year: </label></Table.Cell>
                        <Table.Cell colSpan={2} style={{ width: '80%' }}>
                        {
                            !finYearFetched && options.length > 0 ? 
                                <>Loading</> 
                            : 
                                <Dropdown clearable options={options} selection style={{float: 'right', width: '85%'}} onChange={(e, { value }) => onchangeDD(value)}/>
                        }
                        </Table.Cell>
                    </Table.Row>
                </Table.Body>
            </Table>
            <Modal open={displayStatus} size='mini' trigger={<Button type="button" icon labelPosition='right' className="CustomeBTN" color='vk' style={{ float: 'left' }} onClick={() => addNewFinYear()}>Add Year<Icon name='add circle' /></Button>} onClose={() => setDisplayStatus(false)}>
                <Modal.Header>Confirmation</Modal.Header>
                <Modal.Content>
                    <p>Do you want to add new Financial Year?</p>
                </Modal.Content>
                <Modal.Actions>
                    <Button onClick={() => addFinYear(false)} positive labelPosition='right' icon='checkmark' content='Yes'/>
                    <Button onClick={() => setDisplayStatus(false)} negative labelPosition='right' icon='close' content='No'/>
                </Modal.Actions>
            </Modal>
            <Button type="button" icon labelPosition='right' className="CustomeBTN" color='vk' style={{ float: 'right' }} onClick={() => setSessionAndGoToApp()}>
                Go
                <Icon name='arrow alternate circle right' />
            </Button>
            <br /><br /><br /><br />
            {savedFinYearId && savedFinYearId !== 0 ? 
                <Notification id={savedFinYearId} notifySelector={select.getNotification} notifyDoneAction={setNotifyDoneFinYear} type='save'/>
            :
            null}
        </Container>
    )
}

export default FinYearForm;