import * as type from './types'

export function createPurchase(purchase) {
    return {
        type: type.CREATE_PURCHASE,
        payload: purchase,
        txn: 'initiated',
    };
}

export function savedPurchase(purchase) {
    console.log("call saved purchase")
    return {
        type: type.SAVED_PURCHASE,
        payload: purchase.data.data,
        txn: purchase.data.type,
        msg: purchase.data.msg,
        diff: true
    };
}

export function editPurchase(purchase) {
    return {
        type: type.EDIT_PURCHASE,
        payload: purchase,
        txn: 'initiated',
    };
}

export function updatedPurchase(purchase) {
    console.log("call edited purchase")
    return {
        type: type.EDITED_PURCHASE,
        payload: purchase.data.data,
        txn: purchase.data.type,
        msg: purchase.data.msg,
        removeid: purchase.removeid,
        diff: true
    };
}

export function fetchPurchase(link) {
    return {
        type: type.FETCH_PURCHASE,
        payload: link
    };
}

export function fetchedPurchase(purchase) {
    return {
        type: type.FETCHED_PURCHASE,
        payload: purchase
    };
}


export function fetchPurchaseById(purchase) {
    return {
        type: type.FETCH_PURCHASE_BY_ID,
        payload: purchase
    };
}

export function fetchedPurchaseById(purchase) {
    return {
        type: type.FETCHED_PURCHASE_BY_ID,
        payload: purchase
    };
}


export function setNotifyDonePurchase(props) {
    return {
        type: type.NOTIFICATION_DONE_PURCHASE,
        payload: props
    };
}


export function deletePurchase(purchase) {
    return {
        type: type.DELETE_PURCHASE,
        payload: purchase
    };
}

export function deletedPurchase(purchase) {
    return {
        type: type.DELETED_PURCHASE,
        payload: purchase
    };
}

export function postPrintPurchase(id) {
    return {
        type: type.PRINT_PURCHASE,
        payload: id
    };
}
