import React, { useState, useEffect } from 'react';
import { ErrorMessage } from 'formik'
import moment from 'moment'
import DatePicker from "react-datepicker";
import NumberFormat from 'react-number-format';
import { isObject, startCase, toLower, get } from "lodash";
import { Form, Radio, Input, Label, Dropdown, TextArea, Loader } from 'semantic-ui-react';
import "react-datepicker/dist/react-datepicker.css";
import { checkValidDateFormats, getDateInYYYYMMDD } from './dateUtils'
import { debounce } from 'lodash'


export function getValueFromEvent(e) {
  const { target } = e;
  let newValues;
  if (target) {
    const value = (target.type === "checkbox") ? target.checked : target.value;
    newValues = { [target.name]: value }
  }
  else if (isObject(e)) { newValues = e; }
  return newValues;
}


export const FormikInputComponent = ({
  validateInput, isLabel, label, isTxn, placeholder, focus, disabled, isReadonly,
  field, // { name, value, onChange, onBlur }
  form: { touched, errors, values, handleChange, submitCount }, // also values, setXXXX, handleXXXX, dirty, isValid, status, etc.
  ...props
}) => (
    // <Form.Field error={errors[field.name] && touched[field.name]}>
    <Form.Field style={{width: '100%'}} error={get(errors,field.name) && get(touched,field.name)} validate={validateInput ? debounce(validateInput, 1000) : ""}>
      {errors[field.name] && touched[field.name] && !isTxn ?
        <label>{errors[field.name]}</label> :
        <label>{isLabel ? null : label ? label: startCase(toLower(field.name))}</label>}
      <Input name={field.name}
        type="text"
        placeholder={placeholder}
        onChange={handleChange}
        value={field.value}
        autoFocus={focus ? focus : false}
        style={{width: '100%'}}
        readOnly = {isReadonly ? isReadonly : false}
        disabled = {disabled ? disabled : false}
      />
      {isTxn ? <ErrorMessage name={field.name} render= { msg => <Label size="mini" color='red' basic pointing prompt>{msg}</Label> }/>: null }
    </Form.Field>
  );


  export const FormikDateComponent = ({
    isLabel, label, placeholder, width, focus, isTxn, setDateManually,
    field, // { name, value, onChange, onBlur }
    form: { touched, errors, values, handleChange, setFieldValue, dirty }, // also values, setXXXX, handleXXXX, dirty, isValid, status, etc.
    ...props
  }) => {
  
  const [dt, setDt] = useState((setDateManually && setDateManually !== undefined && setDateManually !== "") ? moment(setDateManually).toDate() : field.value ? moment(field.value).toDate() : null)
  let rawEvent = ''
  useEffect(() => {
    console.log("_-------------done-------------_", setDateManually)
    if(setDateManually !== undefined && setDateManually !== "" && setDateManually !== 0){
      setFieldValue(field.name, setDateManually)
    }else{
      if(setDateManually !== undefined && setDateManually !== ""){
        setFieldValue(field.name, setDateManually)
      }
    }
  }, [setDateManually])

  const handleDateChange = (e) => {
    if (rawEvent.type === 'change'){
        let str = rawEvent.target.value
        const dt1 = checkValidDateFormats(str)
        handleChange(dt1)
        setFieldValue(field.name, getDateInYYYYMMDD(dt1))
        setDt(moment(getDateInYYYYMMDD(dt1)).toDate())  
    } else {
        setDt(e)
        const dt = moment(e)
        handleChange(dt.format("DD-MM-YYYY"))
        setFieldValue(field.name, dt.format("YYYY-MM-DD"))
    }
  }

  return (
    // <Form.Field error={errors[field.name] && touched[field.name]}>
      <Form.Field style={{width: '100%'}} width={width ? width : null} error={get(errors,field.name) && get(touched,field.name)}>
        {errors[field.name] && touched[field.name] && !isTxn ?
          <label>{errors[field.name]}</label> :
          <label>{isLabel ? null : label ? label: startCase(toLower(field.name))}</label>}
        <DatePicker
          name={field.name}
          placeholderText={placeholder}
          dateFormat="dd-MM-yyyy"
          scrollableYearDropdown
          selected={(setDateManually && setDateManually !== undefined && setDateManually !== "") ? moment(setDateManually).toDate() : field.value ? dt : null}
          onChange={handleDateChange}
          onChangeRaw={e=> rawEvent = e}
          autoComplete='off'
          autoFocus={focus ? focus : false}
          style={{width: '100%'}}
        />
        {isTxn ? <ErrorMessage name={field.name} render= { msg => <Label size="mini" color='red' basic pointing prompt>{msg}</Label> }/>: null }
      </Form.Field>
  )
};

export const FormikAmountComponent = ({
  isLabel, label, saveOnBlur, decimal, compute, isTxn, path, placeholder, focus, setValue, disabled, setStateValue, isReadonly, onChangeFunc, onchangeset,
  field, // { name, value, onChange, onBlur }
  index,
  form: { touched, errors, values, handleSubmit, handleBlur, setFieldValue, submitCount }, // also values, setXXXX, handleXXXX, dirty, isValid, status, etc.
  ...props
}) =>  {

  useEffect(() => {
    if(setValue !== undefined && setValue !== "" && setValue !== 0){
      setFieldValue(field.name, setValue)
    }else{
      if(setValue !== undefined && setValue !== ""){
        setFieldValue(field.name, setValue)
      }
    }
  }, [setValue])

  const setValueChange = (setFieldValue, val) => {
    setFieldValue(field.name, val)
    if(setStateValue){
      setStateValue(val !== undefined && val !== "" ? val : 0)
    }
    if(onChangeFunc){
      onChangeFunc(val !== undefined && val !== "" ? val : 0, setFieldValue)
    }
    if(onchangeset){
      onchangeset(values, setFieldValue)
    }
  }

  return(
    // <Form.Field error={errors[field.name] && touched[field.name]}>
    <Form.Field style={{width: '100%'}} error={get(errors,field.name) && get(touched,field.name)}>
      {errors[field.name] && touched[field.name] && !isTxn ?
        <label>{errors[field.name]}</label> :
        <label>{isLabel ? null : label ? label: startCase(toLower(field.name))}</label>}
        
      <NumberFormat 
        thousandSeparator={true} 
        thousandsGroupStyle="lakh"
        decimalScale = {(decimal) ? decimal : 2 }
        placeholder={placeholder}
        name={field.name}
        value={field.value}
        onValueChange={(val) => setValueChange(setFieldValue, val.floatValue)}
        autoFocus={focus ? focus : false}
        disabled = {disabled ? disabled : false}
        style={{textAlign: "right", width: '100%'}}
        readOnly={isReadonly ? isReadonly : false}
        onBlur={(e) =>{
          // e.persist()
          // handleBlur(e)
          if (compute) compute(values, index, setFieldValue)
        }}
      />
      {isTxn ? <ErrorMessage name={field.name} render= { msg => <Label size="mini" color='red' basic pointing prompt>{msg}</Label> }/>: null }
    </Form.Field>
  )
}


export const FormikCheckBoxComponent = ({
  userProps, focus,
  field, // { name, value, onChange, onBlur }
  form: { touched, errors, values, handleChange, setFieldValue }, // also values, setXXXX, handleXXXX, dirty, isValid, status, etc.
  ...props
}) => (
    // <Form.Field error={errors[field.name] && touched[field.name]}>
    <Form.Field error={get(errors,field.name) && get(touched,field.name)}>
      <Radio
      name={field.name}
      id={userProps.id}
      value={userProps.id}
      checked={userProps.id === field.value}
      onChange={field.onChange}
      autoFocus={focus ? focus : false}
    />&nbsp;&nbsp;<b>{userProps.id}</b>
      {/* {errors[field.name] && touched[field.name] ?
        <label>{errors[field.name]}</label> :
        <label>{userProps.isLabel ? null : startCase(toLower(field.name))}</label>} */}
    </Form.Field>
  );  

export const FormikSelectComponent = ({
  userProps, isLabel, label, isTxn, isSelection, focus, placeholder,
  field, // { name, value, onChange, onBlur }
  form: { touched, errors, values, handleChange, setFieldValue, submitCount }, // also values, setXXXX, handleXXXX, dirty, isValid, status, etc.
  ...props
}) => {

const onChange = (value) => {
  setFieldValue(field.name, value)
  if (userProps.getValue)
    userProps.getValue(value);
}

useEffect(() => {
  if(userProps.setValue && userProps.setField){
    userProps.setField(field.name, userProps.setValue)
  }
}, [userProps.setValue])

return (
  // <Form.Field error={errors[field.name] && touched[field.name]}>
  <Form.Field style={{width: '100%'}} error={get(errors,field.name) && get(touched,field.name)}>
    {errors[field.name] && touched[field.name] && !isTxn ?
      <label>{errors[field.name]}</label> :
      <label>{isLabel ? null : label ? label: startCase(toLower(field.name))}</label>}
    <Dropdown
      search
      selection={isSelection ? true : false}
      id={field.id}
      name={field.name}
      placeholder={placeholder}
      options={userProps.options}
      value={userProps ? userProps.isLowerCaseEnable ? toLower(field.value) : field.value : field.value}
      onChange={(e, { value }) => onChange(value)}
      autoFocus={focus ? focus : false}
      clearable
      disabled={userProps ? userProps.isDisable ? userProps.isDisable : null : null}
      style={{width: '100%'}}
      />     
    {isTxn ? <ErrorMessage name={field.name} render= { msg => <Label size="mini" color='red' basic pointing prompt>{msg}</Label> }/>: null }
    </Form.Field>
)};  


