import React from 'react';
import { Route, withRouter } from 'react-router';
import BankList from "./components/bankList";
import BankForm from "./components/bankForm";
import { Container } from 'semantic-ui-react'


const Bank = () => {
    return (
        <Container>
            <Route path="/bank/list" component={withRouter(BankList)} />
            <Route path="/bank/edit/:id" component={BankForm} />
            <Route path="/bank/create" component={BankForm} />
        </Container>
    )
};

export default Bank;

