import * as type from './types'

export function createCombination(combination) {
    return {
        type : type.CREATE_COMBINATION,
        payload : combination,
        txn : 'initiated',
    };
}

export function savedCombination(combination) {
    return {
        type : type.SAVED_COMBINATION,
        payload : combination.data.data,
        txn: combination.data.type,
        msg: combination.data.msg,
        diff : true
    };
}

export function editCombination(combination) {
    return {
        type : type.EDIT_COMBINATION,
        payload : combination,
        txn : 'initiated',
    };
}

export function fetchCombination(params) {
    return {
        type : type.FETCH_COMBINATION,
        payload : params
    };
}

export function fetchedCombination(combination) {
    return {
        type : type.FETCHED_COMBINATION,
        payload : combination
    };
}


export function fetchCombinationById(combination) {
    return {
        type : type.FETCH_COMBINATION_BY_ID,
        payload : combination
    };
}

export function fetchedCombinationById(combination) {
    return {
        type : type.FETCHED_COMBINATION_BY_ID,
        payload : combination
    };
}

export function setNotifyDoneCombination(props) {
    return {
        type : type.NOTIFICATION_DONE_COMBINATION,
        payload: props
    };
}