import { call, takeEvery, all, put, select } from 'redux-saga/effects'
import { savedMilkRates, fetchedMilkRates, fetchedMilkRatesById } from './actions'
import axios from 'axios';
import { handlError, parseError } from '../../app/serverError'
import URL from '../../../store/path'

//#region Saga for Create MilkRates 

function* createMilkRates() {
    yield takeEvery('CREATE_MILKRATES',saveMilkRates)
  }

function* saveMilkRates(action){
  const { response, error } = yield call(saveMilkRatesAPI, action.payload)
  if (response) yield put (savedMilkRates({ data: response.data, msg: response.msg, type: response.type }))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function saveMilkRatesAPI(data) {
    return axios.post(URL+'/milkRates/add', data, {crossDomain: true})
      .then(response => ({ response }))
      .catch(error => ({ error }))
  }

//#endregion


//#region Saga for Edited MilkRates 

function* editMilkRates() {
  yield takeEvery('EDIT_MILKRATES',saveEditedMilkRates)
}

function* saveEditedMilkRates(action){
  const { response, error } = yield call(editMilkRatesAPI, action.payload)  
  if (response) yield put (savedMilkRates({ data: response.data, msg: response.msg, type: response.type }))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function editMilkRatesAPI(data) {
  return axios.post(URL+'/stdmilkrate/update', data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for Getting MilkRates by ID

function* fetchMilkRatesById() {
  yield takeEvery('FETCH_MILKRATES_BY_ID',saveFetchedMilkRatesById)
}

function* saveFetchedMilkRatesById(action){
  const { response, error } = yield call(fetchMilkRatesByIdAPI, action.payload)  
  if (response) yield put (fetchedMilkRatesById(response.data))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function fetchMilkRatesByIdAPI(data) {
  let _id = data
  return axios.post(URL+'/milkRates/'+ _id, data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion

//#region Saga for List Tasks 

function* fetchMilkRates() {

  yield takeEvery('FETCH_MILKRATES',requestMilkRates)

}
  
function* requestMilkRates(action){

  let { data } = yield call(requestMilkRatesAPI, action.payload)
  yield put (fetchedMilkRates(data.data))

}

function requestMilkRatesAPI(data) {
  return axios.get(URL+'/stdmilkrate/fetchbypagename/'+ data, data, {crossDomain: true})
}

//#endregion

const sagaErrorMessage = (error, action) => {
    console.group("Saga Error:"+action.type)
    console.log(error)
    console.groupEnd()
}

export default function* milkRatesSaga() {
    yield all([
        createMilkRates(),
        fetchMilkRates(),
        editMilkRates(),
        fetchMilkRatesById()
    ])
  }  