import React from 'react';
import {Route} from 'react-router';
import { Link } from "react-router-dom";
import { Provider } from 'react-redux';
import { ConnectedRouter} from 'connected-react-router'
import { history } from '../../configureStore'
import { menu } from './menu'

import Party from '../party/index'
import Bank from '../bank/index'
import Group from '../group/index'
import MilkRates from '../milkRates/index'
import Standard from '../standard/index'
import Misc from '../misc/index'
import FinYear from '../finyear/index'
import Purchase from '../purchase/index'
import Register from '../register/index'
import Combination from '../combination/index'

import 'semantic-ui-less/semantic.less'
import { Container, Dropdown, Menu, Header, Icon } from 'semantic-ui-react'

const App = ({ stores }) => (
  <Provider store = {stores}>
    <ConnectedRouter history={history} >
      <Container className="paddingTopOnly widhtSection">
        <Menu attached='top'>
          <Menu.Item as={Link} to="/"><Icon name='home'/></Menu.Item>
          <Dropdown item icon='list' simple>
            <Dropdown.Menu>
              { menu.map((m, index) => {
                return(
                    <Dropdown.Item>
                      <Icon name='dropdown' />
                      <span className='text'>{m.title}</span>
                      <Dropdown.Menu>
                        { m.submenu.map((s, sindex) => {
                          return(
                          <Dropdown.Item text={s.title} key={sindex} as={Link} to={s.list}/>
                          )
                        })}
                      </Dropdown.Menu>
                    </Dropdown.Item>
                  )
              })}
            </Dropdown.Menu>    
          </Dropdown>
          <Menu.Menu position='right'>
            <Container className='headerMenuRight'>
              <Header as='h4' className="VerticalAlign topTen" align='right'>Mayurakshi Co-operative Milk Producers' Union Ltd</Header>
            </Container>
            <Menu.Item as={Link} to="/" onClick={() => window.location = "http://localhost:8080/dairyfarm/logout"}><Icon className="logoutRed" name='power off'/></Menu.Item>
          </Menu.Menu>
        </Menu>
        <Route path="/party" component={Party} />
        <Route path="/bank" component={Bank} />
        <Route path="/group" component={Group} />
        <Route path="/milkRates" component={MilkRates} />
        <Route path="/standard" component={Standard} />
        <Route path="/misc" component={Misc} />
        <Route path="/finyear" component={FinYear} />
        <Route path="/purchase" component={Purchase} />
        <Route path="/register" component={Register} />
        <Route path="/combination" component={Combination} />
      </Container>
    </ConnectedRouter>
  </Provider>
);

export default App;

