import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import { getPartyObjById } from '../data/selectors'
import { Button, Modal, Table, Icon } from 'semantic-ui-react'
import { getGroupById } from '../../group/data/selectors'
import { getBankById } from '../../bank/data/selectors'


const PartyDisplayView = (props) => {

    const [displayStatus, setDisplayStatus] = useState(false)

    const partyObj = useSelector(state => getPartyObjById(state, props.partyId))
    const group = useSelector(state => getGroupById(state, partyObj ? partyObj.groupMaster ? partyObj.groupMaster.id : 0 : 0))
    const bank = useSelector(state => getBankById(state, partyObj ? partyObj.bankMaster ? partyObj.bankMaster.id : 0 : 0))

    return (
        <Modal size='large' closeIcon open={displayStatus} trigger={<Button type='button' icon size='small' color='vk' title='Add Bank' onClick={() => setDisplayStatus(true)}><Icon name='eye' /></Button>} onClose={() => setDisplayStatus(false)}>
            <Modal.Header>Details</Modal.Header>
            <Modal.Content>
                {partyObj ?
                    <Table>
                        <Table.Body>
                            <Table.Row>
                                <Table.Cell style={{ width: '20%' }}><label className="boldText">Plant: </label></Table.Cell>
                                <Table.Cell style={{ width: '20%' }}>
                                    {partyObj.plantNo}
                                </Table.Cell>
                                <Table.Cell style={{ width: '20%' }}><label className="boldText">Plant Name: </label></Table.Cell>
                                <Table.Cell colSpan={3} style={{ width: '60%' }}>
                                    {partyObj.plantName}
                                </Table.Cell>
                            </Table.Row>
                            <Table.Row>
                                <Table.Cell style={{ width: '20%' }}><label className="boldText">Name: </label></Table.Cell>
                                <Table.Cell colSpan={5} style={{ width: '80%' }}>
                                    {partyObj.partyName}
                                </Table.Cell>
                            </Table.Row>
                            <Table.Row>
                                <Table.Cell style={{ width: '20%' }}><label className="boldText">Address: </label></Table.Cell>
                                <Table.Cell colSpan={5} style={{ width: '80%' }}>
                                    {partyObj.address}
                                </Table.Cell>
                            </Table.Row>
                            <Table.Row>
                                <Table.Cell style={{ width: '20%' }}><label className="boldText">Phone: </label></Table.Cell>
                                <Table.Cell colSpan={5} style={{ width: '80%' }}>
                                    {partyObj.phone}
                                </Table.Cell>
                            </Table.Row>
                            <Table.Row>
                                <Table.Cell style={{ width: '20%' }}><label className="boldText">Bank: </label></Table.Cell>
                                <Table.Cell colSpan={5} style={{ width: '80%' }}>
                                    {bank ? bank.bankName === undefined ? '' : bank.bankName : ''}
                                </Table.Cell>
                            </Table.Row>
                            <Table.Row>
                                <Table.Cell style={{ width: '20%' }}><label className="boldText">A/C No: </label></Table.Cell>
                                <Table.Cell colSpan={5} style={{ width: '80%' }}>
                                    {partyObj.partyBankAccNo}
                                </Table.Cell>
                            </Table.Row>
                            <Table.Row>
                                <Table.Cell style={{ width: '20%' }}><label className="boldText">Balance: </label></Table.Cell>
                                <Table.Cell style={{ width: '20%' }}>
                                    {partyObj.balance}
                                </Table.Cell>
                                <Table.Cell colSpan={4} style={{ width: '60%' }}>
                                    <label className="tipText">Tip: ( Negative value for credit balance )</label>
                                </Table.Cell>
                            </Table.Row>
                            <Table.Row>
                                <Table.Cell style={{ width: '20%' }}><label className="boldText">Nick Name: </label></Table.Cell>
                                <Table.Cell colSpan={5} style={{ width: '80%' }}>
                                    {partyObj.nickName}
                                </Table.Cell>
                            </Table.Row>
                            <Table.Row>
                                <Table.Cell style={{ width: '20%' }}><label className="boldText">Group: </label></Table.Cell>
                                <Table.Cell colSpan={5} style={{ width: '80%' }}>
                                    {group ? group.groupName === undefined ? '' : group.groupName : ''}
                                </Table.Cell>
                            </Table.Row>
                        </Table.Body>
                    </Table>
                    : null}
            </Modal.Content>
        </Modal>
    )
}

export default PartyDisplayView;