import React, { useState } from 'react'
import {Table, TableBody } from 'semantic-ui-react'
import "react-datepicker/dist/react-datepicker.css"
import moment from 'moment'
import store from 'store2'


export const VdetailsNonEditable = ({
  name, move, swap, push, insert, unshift, remove, pop, setField, // { name, value, onChange, onBlur }
  form: { initialValues, values, errors }, // also values, setXXXX, handleXXXX, dirty, isValid, status, etc.
  ...props
}) => {

  let arrKeys = [];
  const fieldName = name;
  if (values[fieldName] && values[fieldName].length) {
    arrKeys = Object.keys(values[fieldName]);
  }

  const [isActive, setIsActive] = useState(0)

  const onclickRow = (index) => {
    store.set("setIndex", index)
    setIsActive(index)
  }

  return (
    <>
        <Table basic='very' selectable className='purchaseHead'>
            <Table.Header>
                <Table.Row>
                    <Table.HeaderCell title="Date">Date</Table.HeaderCell>
                    <Table.HeaderCell title="Qty">Qty</Table.HeaderCell>
                    <Table.HeaderCell title="CLR">CLR</Table.HeaderCell>
                    <Table.HeaderCell title="FAT(%)">FAT(%)</Table.HeaderCell>
                    <Table.HeaderCell title="SNF(%)">SNF(%)</Table.HeaderCell>
                    <Table.HeaderCell title="KGFat">KGFat</Table.HeaderCell>
                    <Table.HeaderCell title="KGSNF">KGSNF</Table.HeaderCell>
                    <Table.HeaderCell title="FatRate">FatRate</Table.HeaderCell>
                    <Table.HeaderCell title="SnfRate">SnfRate</Table.HeaderCell>
                    <Table.HeaderCell title="FatAmt">FatAmt</Table.HeaderCell>
                    <Table.HeaderCell title="SnfAmt">SnfAmt</Table.HeaderCell>
                    <Table.HeaderCell title="MilkPrice">MilkPrice</Table.HeaderCell>
                    <Table.HeaderCell title="MilkRate">MilkRate</Table.HeaderCell>
                    <Table.HeaderCell title="R.Qty">R.Qty</Table.HeaderCell>
                    <Table.HeaderCell title="S.Qty">S.Qty</Table.HeaderCell>
                    <Table.HeaderCell width={3} title="Remarks">Remarks</Table.HeaderCell>
                </Table.Row>
            </Table.Header>
            <TableBody>
            {values[fieldName] && values[fieldName].length > 0 ? (
                arrKeys.map((index) => (
                    <Table.Row key={`${fieldName}[${index}]`} active={isActive === index ? true : false} className="VInputPanel" onClick={() => onclickRow(index)}>
                        <Table.Cell>
                            {moment(values[fieldName][index].date).format('DD-MM-YYYY')}
                        </Table.Cell>
                        <Table.Cell>
                            {values[fieldName][index].qty ? parseFloat(values[fieldName][index].qty).toFixed(3) : ''}
                        </Table.Cell>
                        <Table.Cell>
                            {values[fieldName][index].clrP ? parseFloat(values[fieldName][index].clrP).toFixed(2) : ''}
                        </Table.Cell>
                        <Table.Cell>
                            {values[fieldName][index].fatP ? parseFloat(values[fieldName][index].fatP).toFixed(2) : ''}
                        </Table.Cell>
                        <Table.Cell>
                            {values[fieldName][index].snfP ? parseFloat(values[fieldName][index].snfP).toFixed(2) : ''}
                        </Table.Cell>
                        <Table.Cell>
                            {values[fieldName][index].kgFat ? parseFloat(values[fieldName][index].kgFat).toFixed(3) : ''}
                        </Table.Cell>
                        <Table.Cell>
                            {values[fieldName][index].kgSnf ? parseFloat(values[fieldName][index].kgSnf).toFixed(3) : ''}
                        </Table.Cell>
                        <Table.Cell>
                            {values[fieldName][index].fatRate ? parseFloat(values[fieldName][index].fatRate).toFixed(2) : ''}
                        </Table.Cell>
                        <Table.Cell>
                            {values[fieldName][index].snfRate ? parseFloat(values[fieldName][index].snfRate).toFixed(2) : ''}
                        </Table.Cell>
                        <Table.Cell>
                            {values[fieldName][index].fatAmount ? parseFloat(values[fieldName][index].fatAmount).toFixed(2) : ''}
                        </Table.Cell>
                        <Table.Cell>
                            {values[fieldName][index].snfAmount ? parseFloat(values[fieldName][index].snfAmount).toFixed(2) : ''}
                        </Table.Cell>
                        <Table.Cell>
                            {values[fieldName][index].milkPrice ? parseFloat(values[fieldName][index].milkPrice).toFixed(2) : ''}
                        </Table.Cell>
                        <Table.Cell>
                            {values[fieldName][index].milkRate ? parseFloat(values[fieldName][index].milkRate).toFixed(2) : ''}
                        </Table.Cell>
                        <Table.Cell>
                            {values[fieldName][index].rejectedQty ? parseFloat(values[fieldName][index].rejectedQty).toFixed(3) : ''}
                        </Table.Cell>
                        <Table.Cell>
                            {values[fieldName][index].sourQty ? parseFloat(values[fieldName][index].sourQty).toFixed(3) : ''}
                        </Table.Cell>
                        <Table.Cell>
                            {values[fieldName][index].remarks}
                        </Table.Cell>
                    </Table.Row>
                    )
                )) : null}
            </TableBody>
        </Table>
    </>
  )
}

export default VdetailsNonEditable;