import * as type from './types'

export function createFinYear(finYear) {
    return {
        type : type.CREATE_FINYEAR,
        payload : finYear,
        txn : 'initiated',
    };
}

export function savedFinYear(finYear) {
    return {
        type : type.SAVED_FINYEAR,
        payload : finYear.data.data,        
        txn: finYear.data.type,
        msg: finYear.data.msg,
        diff : true
    };
}

export function editFinYear(finYear) {
    return {
        type : type.EDIT_FINYEAR,
        payload : finYear,
        txn : 'initiated',
    };
}

export function fetchFinYear(finYear) {
    return {
        type : type.FETCH_FINYEAR,
        payload: finYear
    };
}

export function fetchedFinYear(finYear) {
    return {
        type : type.FETCHED_FINYEAR,
        payload : finYear
    };
}


export function fetchFinYearById(finYear) {
    return {
        type : type.FETCH_FINYEAR_BY_ID,
        payload : finYear
    };
}

export function fetchedFinYearById(finYear) {
    return {
        type : type.FETCHED_FINYEAR_BY_ID,
        payload : finYear
    };
}

export function setNotifyDoneFinYear(props) {
    return {
        type : type.NOTIFICATION_DONE_FINYEAR,
        payload: props
    };
}