import React, { useState } from 'react'
import { Form, Button, Modal, Table, Icon } from 'semantic-ui-react'
import { Formik, Field, Form as FormikForm } from 'formik'
import { FormikAmountComponent } from '../../../utilities/formUtils'

const AddHeadLoad = ({setItemObject, name, setField, setValueFor, afterWork, sendValue, ...props}) => {
    
    const [displayStatus, setDisplayStatus] = useState(false)

    const addHeadLoad = (values, resetForm) => {
        setDisplayStatus(false)
        setField(name, values.headLoad)
        sendValue[name] = values.headLoad
        afterWork(sendValue, setField)
        resetForm()
    }

    return (
        <Modal size='tiny' open={displayStatus} trigger={<Button type="button" icon className='iconButtonCustom' color='vk' onClick={() => setDisplayStatus(true)}><Icon name='question'/></Button>} onClose={() => setDisplayStatus(false)}>
            <Modal.Header>Add Head Load Value</Modal.Header>
            <Modal.Content>
            <Formik id="vdForm" size="large" width={5}
                onSubmit={(values, {resetForm}) => addHeadLoad(values, resetForm)}
                render={({ handleSubmit, onChange, values, handleChange, errors, setFieldValue }) => (
                    <Form key={name} as={FormikForm} size="small" className="CustomeFormPurchase" width={5} onSubmit={handleSubmit}>
                        <Table style={{width: '100%'}} className='purchasetablePop'>
                            <Table.Body>
                                <Table.Row className="VInputPanel">
                                    <Table.Cell style={{width: '100%'}}>
                                        <Field name="headLoad" label=' ' component={FormikAmountComponent} setValue={setValueFor}/>
                                    </Table.Cell>
                                </Table.Row>
                            </Table.Body>
                        </Table>
                        <br/>
                        <Button type='button' onClick={handleSubmit} size="medium" color='vk' className="CustomeBTNWithoutMargin" style={{float: 'right'}}> Add </Button>
                        <br/><br/>
                    </Form>
                )}
            />
            </Modal.Content>
        </Modal>
    )
}

export default AddHeadLoad;