import * as Yup from 'yup';
import v4 from 'uuid';
import { parseDateFromFormats } from '../../../utilities/parseDate';
import moment from 'moment'

export const milkRates =  () => ({
    "type": "",
    "goodFat": "",
    "goodSnf": "",
    "badFat": "",
    "badSnf": "",
    "pageName": ""
})


Yup.addMethod(Yup.date, 'format', parseDateFromFormats);
export const milkRatesSchema = Yup.object().shape({
    type: Yup.string()
        .required('This is a Required Field'),

    pageName: Yup.string()
        .required('This is a Required Field'),
        
    goodFat: Yup.string()
        .required('This is a Required Field'),

    goodSnf: Yup.string()
        .required('This is a Required Field'),

    badFat: Yup.string()
        .required('This is a Required Field'),

    badSnf: Yup.string()
        .required('This is a Required Field')
});
