import { configureStore, getDefaultMiddleware } from 'redux-starter-kit';
import { routerMiddleware } from 'connected-react-router'
import { createBrowserHistory } from 'history'
import createSagaMiddleware from 'redux-saga'
import { throttle } from 'lodash'

import monitorReducersEnhancer from './store/monitorReducers'
import loggerMiddleware from './store/logger'
import { txnMiddleware } from './store/txnMiddleware' //logActionDiff
import { loadState, saveState } from './store/localStorage';
import rootSaga from './store/sagas'
import rootReducer from './reducers'

import { fetchFinYear } from './modules/finyear/data/actions'
import { fetchBank } from './modules/bank/data/actions'
import { fetchGroup } from './modules/group/data/actions'
import { fetchParty } from './modules/party/data/actions'
import { fetchStandard } from './modules/standard/data/actions'
import { fetchMilkRates } from './modules/milkRates/data/actions'
import { fetchMisc } from './modules/misc/data/actions'
import { fetchCombination } from './modules/combination/data/actions'


const sagaMiddleware = createSagaMiddleware();

export const history = createBrowserHistory({
  basename: '/dairyfarm',
});

export default function configureAppStore() {

  const preloadedState = loadState();

  const store = configureStore({
    reducer: rootReducer(history),
    middleware: [sagaMiddleware,txnMiddleware,loggerMiddleware,routerMiddleware(history), ...getDefaultMiddleware()],
    preloadedState,
    enhancers: [monitorReducersEnhancer]
  })

  if (process.env.NODE_ENV !== 'production' && module.hot) {
    module.hot.accept('./reducers', () => store.replaceReducer(rootReducer(history)))
  }  

  sagaMiddleware.run(rootSaga)

  store.subscribe(throttle(() => {
    saveState({
      // loginDetails : store.getState().loginDetails,
      // messagesById: store.getState().messagesById,
      // messagesPrimary : store.getState().messagesPrimary
    });
  }), 1000);
  store.dispatch(fetchCombination())
  store.dispatch(fetchFinYear())
  store.dispatch(fetchBank())
  store.dispatch(fetchGroup())
  store.dispatch(fetchParty())
  store.dispatch(fetchStandard("SetStandard"))
  store.dispatch(fetchMilkRates("SetPriceRate"))
  store.dispatch(fetchMisc())
  console.log("call")
  return store
}



