import { call, takeEvery, all, put, select } from 'redux-saga/effects'
import { getIsBankFetched } from './selectors'
import { savedBank, fetchedBank, fetchedBankById, deletedBank } from './actions'
import axios from 'axios';
import { handlError, parseError } from '../../app/serverError'
import URL from '../../../store/path'

//#region Saga for Create Bank 

function* createBank() {
  yield takeEvery('CREATE_BANK', saveBank)
}

function* saveBank(action) {
  const { response, error } = yield call(saveBankAPI, action.payload)
  console.log("----------------------call create bank Saga")
  if (response) yield put(savedBank({ data: response.data, msg: response.msg, type: response.type }))
  else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function saveBankAPI(data) {
  console.log("----------------------call create bank link  Saga")
  return axios.post(URL + '/bankmaster/create', data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for Edited Bank 

function* editBank() {
  yield takeEvery('EDIT_BANK', saveEditedBank)
}

function* saveEditedBank(action) {
  const { response, error } = yield call(editBankAPI, action.payload)
  console.log(response)
  if (response) yield put(savedBank({ data: response.data, msg: response.msg, type: response.type }))
  else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function editBankAPI(data) {
  return axios.post(URL + '/bankmaster/update', data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for Getting Bank by ID

function* fetchBankById() {
  yield takeEvery('FETCH_BANK_BY_ID', saveFetchedBankById)
}

function* saveFetchedBankById(action) {
  const { response, error } = yield call(fetchBankByIdAPI, action.payload)
  if (response) yield put(fetchedBankById(response.data))
  else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function fetchBankByIdAPI(data) {
  let id = data
  return axios.get(URL + '/bankmaster/fetch/' + id, data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion

//#region Saga for List Tasks 

function* fetchBank() {

  yield takeEvery('FETCH_BANK', requestBank)

}

function* requestBank() {
  const isBankFetched = yield select(getIsBankFetched);
  if (!isBankFetched) {
    let { data } = yield call(requestBankAPI)
    yield put(fetchedBank(data.data))
  }

}

function requestBankAPI() {
  return axios.get(URL + '/bankmaster/fetch/all', { crossDomain: true })
}

//#endregion

//#region Saga for Delete Bank 

function* deleteBank() {
  yield takeEvery('DELETE_BANK', saveDeleteBank)
}

function* saveDeleteBank(action) {
  const { response, error } = yield call(deleteBankAPI, action.payload)
  if (response) yield put(deletedBank({ id: action.payload.id, msg: response.data.msg, type: response.data.type }))
  else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function deleteBankAPI(data) {
  return axios.post(URL + '/bankmaster/delete', data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion

const sagaErrorMessage = (error, action) => {
  console.group("Saga Error:" + action.type)
  console.log(error)
  console.groupEnd()
}

export default function* bankSaga() {
  yield all([
    createBank(),
    fetchBank(),
    editBank(),
    fetchBankById(),
    deleteBank()
  ])
}  