import React from 'react'
import { useSelector } from 'react-redux'
import { Field } from 'formik'
import { FormikSelectComponent } from '../../../utilities/formUtils'
import * as select from '../data/selectors'
    

const MiscNameTypeSelect = ({ name, isTxn, isLabel, label, isSelection, userProps }, props) => {

    const misc = useSelector(state => select.getMiscList(state, props))
    const opt = [{
        key : "Commision",
        value : "Commision",
        text : "Commision"
    }
    // ,{
    //     key : "Head Load",
    //     value : "Head Load",
    //     text : "Head Load"
    // }
    ,{
        key : "Sour Milk",
        value : "Sour Milk",
        text : "Sour Milk"
    }]

    const getSelectedValue = (values) => {
        values = values.trim()
        if(userProps && userProps !== undefined && userProps.setField && userProps.setField !== undefined){
            userProps.setField('name', values)
            userProps.setField('id', misc[values] ? misc[values].id ? misc[values].id : '' : '')
            userProps.setField('typeName', misc[values] ? misc[values].typeName ? misc[values].typeName.trim() : '' : '')
            userProps.setField('operation', misc[values] ? misc[values].operation ? misc[values].operation.trim() : '' : '')
            userProps.setField('operationOn', misc[values] ? misc[values].operationOn ? misc[values].operationOn.trim() : '' : '')
            userProps.setField('value', misc[values] ? misc[values].value ? misc[values].value : '' : '')
            userProps.setField('description', misc[values] ? misc[values].description ? misc[values].description : '' : '')
        }
    }


    return (
        <Field name={name} isLabel={isLabel} isTxn={isTxn} label={label} isSelection={isSelection} component={FormikSelectComponent} userProps={{ options: opt, getValue: getSelectedValue }}> </Field>
    )

}


export default MiscNameTypeSelect;
