import React from 'react'
import { Field } from 'formik'
import { FormikSelectComponent } from '../../../utilities/formUtils'
import store from 'store2'
    

const ShiftSelect = ({ name, isTxn, isLabel, label, isSelection, setField }, props) => {

    const opt = [{
        key : "Morning",
        value : "Morning",
        text : "Morning"
    },{
        key : "Evening",
        value : "Evening",
        text : "Evening"
    }]

    const getSelectedValue = (values) => {
        store.session("getShift", values)
        console.log("session--------", store.session("getShift"))
    }

    return (
        <Field name={name} isLabel={isLabel} isTxn={isTxn} label={label} isSelection={isSelection} component={FormikSelectComponent} userProps={{ options: opt, getValue: getSelectedValue, setField: setField, setValue: store.session("getShift") }}> </Field>
    )

}


export default ShiftSelect;
