import { startsWith, cloneDeep } from 'lodash'

export const getIsFetchingCombination = (state, props) => state.combination.params.isFetching
export const getIsCombinationFetched = (state, props) => state.combination.params.combinationFetched
export const getSavedCombinationId = (state, props) => state.combination.params.savedCombinationId
export const getCombinationList = (state, prop) => state.combination.byId
export const getCombinationById = (state, id) => state.combination.byId[id]
export const getNotification = (state, id) => state.combination.notifications[id]

export const getCombination = (state, id) => {
    let obj = cloneDeep(state.combination.byId[id])
    return obj
}

export const getCombinationParams = (state, props) => {
    const params =  {}

    if(startsWith(state.router.location.pathname,'/combination/create')){ 
        params.title = state.combination.params.createTitle
        params.submitButtonText = state.combination.params.createSubmitButtonText        
    };

    if(startsWith(state.router.location.pathname,'/combination/edit/')){ 
        params.title = state.combination.params.editTitle
        params.submitButtonText = state.combination.params.editSubmitButtonText        
    };

    return params
}

