import { call, takeEvery, all, put, select } from 'redux-saga/effects'
import { savedCombination, fetchedCombination, fetchedCombinationById } from './actions'
import axios from 'axios';
import { handlError, parseError } from '../../app/serverError'
import URL from '../../../store/path'

//#region Saga for Create Combination 

function* createCombination() {
    yield takeEvery('CREATE_COMBINATION',saveCombination)
  }

function* saveCombination(action){
  const { response, error } = yield call(saveCombinationAPI, action.payload)
  if (response) yield put (savedCombination({ data: response.data, msg: response.msg, type: response.type }))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function saveCombinationAPI(data) {
    return axios.post(URL+'/combination/add', data, {crossDomain: true})
      .then(response => ({ response }))
      .catch(error => ({ error }))
  }

//#endregion


//#region Saga for Edited Combination 

function* editCombination() {
  yield takeEvery('EDIT_COMBINATION',saveEditedCombination)
}

function* saveEditedCombination(action){
  const { response, error } = yield call(editCombinationAPI, action.payload)  
  if (response){ 
    console.log("response----------", response)
    yield put (savedCombination({ data: response.data, msg: response.msg, type: response.type }))}
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function editCombinationAPI(data) {
  return axios.post(URL+'/setcombination/update', data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for Getting Combination by ID

function* fetchCombinationById() {
  yield takeEvery('FETCH_COMBINATION_BY_ID',saveFetchedCombinationById)
}

function* saveFetchedCombinationById(action){
  const { response, error } = yield call(fetchCombinationByIdAPI, action.payload)  
  if (response) yield put (fetchedCombinationById(response.data))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function fetchCombinationByIdAPI(data) {
  let _id = data
  return axios.post(URL+'/combination/'+ _id, data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion

//#region Saga for List Tasks 

function* fetchCombination() {

  yield takeEvery('FETCH_COMBINATION',requestCombination)

}
  
function* requestCombination(action){

  let { data } = yield call(requestCombinationAPI, action.payload)
  yield put (fetchedCombination(data.data))

}

function requestCombinationAPI(data) {
  return axios.get(URL+'/setcombination/fetch/all', data, {crossDomain: true})
}

//#endregion

const sagaErrorMessage = (error, action) => {
    console.group("Saga Error:"+action.type)
    console.log(error)
    console.groupEnd()
}

export default function* combinationSaga() {
    yield all([
        createCombination(),
        fetchCombination(),
        editCombination(),
        fetchCombinationById()
    ])
  }  