import React, { useState } from 'react'
import { Form, Button, Modal, Table } from 'semantic-ui-react'
import { Formik, Field, Form as FormikForm } from 'formik'
import { FormikTextAreaComponent } from '../../../utilities/formUtils'

const AddNotesCommon = ({setItemObject, name, setField, setValueFor, ...props}) => {
    
    const [displayStatus, setDisplayStatus] = useState(false)

    const addNotes = (values, resetForm) => {
        setDisplayStatus(false)
        setField(name, values.addNote)
        resetForm()
    }

    return (
        <Modal size='tiny' open={displayStatus} trigger={<Button type='button' className='labelButtonCustom' color='vk' onClick={() => setDisplayStatus(true)}>N</Button>} onClose={() => setDisplayStatus(false)}>
            <Modal.Header>Add Note</Modal.Header>
            <Modal.Content>
            <Formik id="vdForm" size="large" width={5}
                 //initialValues={vdetails}
                // validationSchema={itemsQuickSchema}
                onSubmit={(values, {resetForm}) => addNotes(values, resetForm)}
                render={({ handleSubmit, onChange, values, handleChange, errors, setFieldValue }) => (
                    <Form key={name} as={FormikForm} size="small" className="CustomeFormPurchase" width={5} onSubmit={handleSubmit}>
                        <Table style={{width: '100%'}} className='purchasetablePop'>
                            <Table.Body>
                                <Table.Row className="VInputPanel">
                                    <Table.Cell style={{width: '100%'}}>
                                        <Field name='addNote' label=' ' component={FormikTextAreaComponent} userProps={{maxLength: 300, displayCount: false, height: 3, value: setValueFor}} focus={true}/>
                                    </Table.Cell>
                                </Table.Row>
                            </Table.Body>
                        </Table>
                        <br/>
                        <Button type='button' onClick={handleSubmit} size="medium" color='vk' className="CustomeBTNWithoutMargin" style={{float: 'right'}}> Add </Button>
                        <br/><br/>
                    </Form>
                )}
            />
            </Modal.Content>
        </Modal>
    )
}

export default AddNotesCommon;