import * as type from './types'

export function createMilkRates(milkRates) {
    return {
        type : type.CREATE_MILKRATES,
        payload : milkRates,
        txn : 'initiated',
    };
}

export function savedMilkRates(milkRates) {
    return {
        type : type.SAVED_MILKRATES,
        payload : milkRates.data.data,
        txn: milkRates.data.type,
        msg: milkRates.data.msg,
        diff : true
    };
}

export function editMilkRates(milkRates) {
    return {
        type : type.EDIT_MILKRATES,
        payload : milkRates,
        txn : 'initiated',
    };
}

export function fetchMilkRates(params) {
    return {
        type : type.FETCH_MILKRATES,
        payload : params
    };
}

export function fetchedMilkRates(milkRates) {
    return {
        type : type.FETCHED_MILKRATES,
        payload : milkRates
    };
}


export function fetchMilkRatesById(milkRates) {
    return {
        type : type.FETCH_MILKRATES_BY_ID,
        payload : milkRates
    };
}

export function fetchedMilkRatesById(milkRates) {
    return {
        type : type.FETCHED_MILKRATES_BY_ID,
        payload : milkRates
    };
}

export function setNotifyDoneMilkRates(props) {
    return {
        type : type.NOTIFICATION_DONE_MILKRATES,
        payload: props
    };
}