import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import * as select from '../data/selectors'
import { deleteBank, setNotifyDoneBank } from '../data/actions'
import { Container, Table, TableHeader, TableBody, Header, Button, Grid, Icon, Modal } from 'semantic-ui-react'
import { fetchBank } from '../data/actions'
import Notification from '../../../utilities/notificationUtils'

const Bank = props => {
    const dispatch = useDispatch();
    const isNegative = props.bank.status === 1 ? false : true;

    const [deleteBankId, setDeleteBankId] = useState(false)
    const [modalOption, setModalOption] = useState({status: false, id: 0})

    const deleteBankFromList = (values) => {
        const getVlaue = { id: values }
        setDeleteBankId(values)
        dispatch(deleteBank(getVlaue))
        setModalOption({status: false, id: 0})
    }

    return (
        <Table.Row negative={isNegative}>
            <Table.Cell>{props.index + 1}</Table.Cell>
            <Table.Cell>{props.bank.bankName}</Table.Cell>
            <Table.Cell>
                {isNegative === false ?
                    <>
                        <Link to={"/bank/edit/" + props.bank.id}>Edit</Link><span>&nbsp;&nbsp;</span>
                        <span style={{ cursor: 'pointer', color: 'red' }} onClick={() => setModalOption({status: true, id: props.bank.id})}>Delete</span>
                    </>
                    :
                    <span>Deleted</span>
                }
                <>
                {console.log(deleteBankId)}
                    { 
                        deleteBankId ?
                            <Notification id={deleteBankId} notifySelector={select.getNotification} notifyDoneAction={setNotifyDoneBank} type='delete' />
                            :
                            null
                    }
                </>
                <Modal open={modalOption.status} size='mini' onClose={() => setModalOption({status: false, id: 0})}>
                    <Modal.Header>Confirmation</Modal.Header>
                    <Modal.Content>
                        <p>Do you want to delete this bank?</p>
                    </Modal.Content>
                    <Modal.Actions>
                        <Button onClick={() => deleteBankFromList(modalOption.id)} positive labelPosition='right' icon='checkmark' content='Yes'/>
                        <Button onClick={() => setModalOption({status: false, id: 0})} negative labelPosition='right' icon='close' content='No'/>
                    </Modal.Actions>
                </Modal>
            </Table.Cell>
        </Table.Row>
    )
}

const BankList = (props) => {
    // Loading Logic if tasks not fetched
    const bank = useSelector(state => select.getBankList(state, props))
    const bankFetched = useSelector(state => select.getIsBankFetched(state, props))
    const dispatch = useDispatch()
    const addBankClick = () => {
        props.history.push('/bank/create')
    }

    useEffect(() => {
        dispatch(fetchBank())
    })

    return (
        <Container>
            <Grid columns='equal' className="borderButtomTable">
                <Grid.Row>
                    <Grid.Column width={4}>
                        <Header as='h1' className="VerticalAlign" align='left'>Bank List</Header>
                    </Grid.Column>
                    <Grid.Column width={6} floated={'right'} align='right'>
                        <Button icon labelPosition='right' className="CustomeBTNList" color='vk' onClick={() => addBankClick()}>
                            Add Bank<Icon name='add' />
                        </Button>
                    </Grid.Column>
                </Grid.Row>
            </Grid>
            <br />
            <Table selectable basic='very'>
                <TableHeader>
                    <Table.Row>
                        <Table.HeaderCell style={{ width: '10%' }}>Sl. No.</Table.HeaderCell>
                        <Table.HeaderCell style={{ width: '80%' }}>Bank Name</Table.HeaderCell>
                        <Table.HeaderCell style={{ width: '10%' }}>Action</Table.HeaderCell>
                    </Table.Row>
                </TableHeader>
                <TableBody>
                    {!bankFetched ?
                        <>
                            <Table.Row>
                                <Table.Cell style={{ width: '100%' }} textAlign='center' colSpan="3">Fetching ...</Table.Cell>
                            </Table.Row>
                        </> :
                        <>
                            {Object.keys(bank).map((key, index) =>
                                <Bank bank={bank[key]} key={bank[key].id} index={index} />)}
                        </>
                    }
                </TableBody>
            </Table>
        </Container>
    )
}

export default BankList;
