import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import * as select from '../data/selectors'
import { deleteGroup, setNotifyDoneGroup, fetchGroup } from '../data/actions'
import { Container, Table, TableHeader, TableBody, Header, Button, Grid, Icon, Modal } from 'semantic-ui-react'
import Notification from '../../../utilities/notificationUtils'

const Group = props => {
    const dispatch = useDispatch();
    const isNegative = props.group.status === 1 ? false : true;
    
    const [deleteGroupId, setDeleteGroupId] = useState(false)
    const [modalOption, setModalOption] = useState({status: false, id: 0})
    
    const deleteGroupFromList = (values) => {
        const getVlaue = {id: values}
        dispatch(deleteGroup(getVlaue))
        setDeleteGroupId(values)
        setModalOption({status: false, id: 0})
    }
    
    return(
        <Table.Row negative={isNegative}>
            <Table.Cell>{props.index + 1}</Table.Cell>
            <Table.Cell>{props.group.groupName}</Table.Cell>
            <Table.Cell>
                {isNegative === false ? 
                    <>
                        <Link to={"/group/edit/"+props.group.id}>Edit</Link><span>&nbsp;&nbsp;</span>
                        <span style={{cursor: 'pointer', color: 'red'}} onClick={() => setModalOption({status: true, id: props.group.id})}>Delete</span>
                    </>
                :
                    <span>Deleted</span>
                }
                <>
                {
                    deleteGroupId ? 
                    <Notification id={deleteGroupId} notifySelector={select.getNotification} notifyDoneAction={setNotifyDoneGroup} type='delete'/>
                    :
                    null
                }
                </>
                <Modal open={modalOption.status} size='mini' onClose={() => setModalOption({status: false, id: 0})}>
                    <Modal.Header>Confirmation</Modal.Header>
                    <Modal.Content>
                        <p>Do you want to delete this Group?</p>
                    </Modal.Content>
                    <Modal.Actions>
                        <Button onClick={() => deleteGroupFromList(modalOption.id)} positive labelPosition='right' icon='checkmark' content='Yes'/>
                        <Button onClick={() => setModalOption({status: false, id: 0})} negative labelPosition='right' icon='close' content='No'/>
                    </Modal.Actions>
                </Modal>
            </Table.Cell>
        </Table.Row>
    )
}

const GroupList = (props) => {
    // Loading Logic if tasks not fetched
    const group = useSelector(state => select.getGroupList(state, props)) 
    const groupFetched = useSelector(state => select.getIsGroupFetched(state, props))
    const dispatch = useDispatch()
    
    const addGroupClick = () => {
        props.history.push('/group/create');
    }
    
    useEffect(()=>{
        dispatch(fetchGroup())
    })      

    return (
        <Container>
            <Grid columns='equal' className="borderButtomTable">
                <Grid.Row>
                    <Grid.Column width={4}>
                        <Header as='h1' className="VerticalAlign" align='left'>Group List</Header>
                    </Grid.Column>
                    <Grid.Column width={6} floated={'right'} align='right'>
                        <Button icon labelPosition='right' className="CustomeBTNList" color='vk' onClick={() => addGroupClick()}>
                            Add Group<Icon name='add' />
                        </Button>
                    </Grid.Column>
                </Grid.Row>
            </Grid>
            <br/>
            <Table selectable basic='very'>
                <TableHeader>
                    <Table.Row>
                        <Table.HeaderCell style={{width: '10%'}}>Sl. No.</Table.HeaderCell>
                        <Table.HeaderCell style={{width: '80%'}}>Group Name</Table.HeaderCell>
                        <Table.HeaderCell style={{width: '10%'}}>Action</Table.HeaderCell>
                    </Table.Row>
                </TableHeader>
                <TableBody>
                    {!groupFetched ? 
                    <>
                        <Table.Row>
                            <Table.Cell style={{width: '100%'}} textAlign='center' colSpan="3">Fetching ...</Table.Cell>                             
                        </Table.Row>
                    </> :
                    <>
                    { Object.keys(group).map((key, index) => 
                    <Group group={group[key]} key={group[key].id} index={index}/> )}
                    </>
                    }
                </TableBody>
            </Table>
        </Container>
    )
}

export default GroupList;
