import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { miscSchema } from '../data/model'
import { Container, Header, Form, Button, Grid, Table, Icon } from 'semantic-ui-react'
import { Formik, Field, Form as FormikForm } from 'formik'
import { FormikAmountComponent, FormikTextAreaComponent } from '../../../utilities/formUtils'
import MiscNameType from './miscNameType'
import MiscOperationType from './miscOperationType'
import * as select from '../data/selectors'
import { editMisc, setNotifyDoneMisc } from '../data/actions'
import Notification from '../../../utilities/notificationUtils'


const MiscForm = (props) => {

    const misc = useSelector(state => select.getMisc(state, props))
    const savedMiscId = useSelector(state => select.getSavedMiscId(state, props))
    const dispatch = useDispatch()

    const saveMisc = (values, resetForm) => {
        console.log(values, "------------------saving values")
        dispatch(editMisc(values))
        resetForm()
    }

    return (
        <Container>
            <Grid columns='equal' className="borderButtomTable">
                <Grid.Row>
                    <Grid.Column width={4}>
                        <Header as='h1' className="VerticalAlign" align='left'>Set Misc</Header>
                    </Grid.Column>
                    <Grid.Column width={6} floated={'right'} align='right'></Grid.Column>
                </Grid.Row>
            </Grid>
            <Container className='formContainer'>
                <Formik id="miscForm" size="large" width={5}
                    initialValues={misc}
                    validationSchema={miscSchema}
                    onSubmit={(values, { resetForm }) => saveMisc(values, resetForm)}
                    render={({ handleSubmit, errors, onChange, values, handleChange, setFieldValue }) => (
                        <Form as={FormikForm} size="small" className="CustomeForm" width={5} onSubmit={handleSubmit}>
                            <Table>
                                <Table.Body>
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}><label className="boldText">Type: </label></Table.Cell>
                                        <Table.Cell style={{ width: '80%' }}>
                                            <Form.Group widths={2}>
                                                <MiscNameType name="name" isLabel={false} label=" " isSelection={true} userProps={{ setField: setFieldValue }} type='' />
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                    {values.name !== "Sour Milk" ? 
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}><label className="boldText">Operation On: </label></Table.Cell>
                                        <Table.Cell style={{ width: '80%' }}>
                                            <Form.Group widths={2}>
                                                <MiscOperationType name="operationOn" isLabel={false} label=" " isSelection={true} userProps={{ setField: setFieldValue }} type='' />
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                    : null }
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}><label className="boldText">Value (Rs.): </label></Table.Cell>
                                        <Table.Cell style={{ width: '80%' }}>
                                            <Form.Group widths={2}>
                                                <Field name="value" label=' ' component={FormikAmountComponent} />
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}><label className="boldText">Description: </label></Table.Cell>
                                        <Table.Cell style={{ width: '80%' }}>
                                            <Form.Group widths={2}>
                                                <Field name="description" label=' ' component={FormikTextAreaComponent} userProps={{maxLength: 200, displayCount: false, height: 3}} focus={true}/>
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                </Table.Body>
                            </Table>
                            <Button type="submit" icon labelPosition='right' className="CustomeBTN" color='vk' style={{ float: 'right' }}>
                                Update
                                <Icon name='arrow alternate circle up outline' />
                            </Button>
                            <br /><br /><br /><br />
                            {savedMiscId && savedMiscId !== 0 ? 
                            <Notification id={savedMiscId} notifySelector={select.getNotification} notifyDoneAction={setNotifyDoneMisc} type='save'/>
                            :
                            null}
                        </Form>
                    )}
                />
            </Container>
        </Container>
    )
}

export default MiscForm;