import { call, takeEvery, all } from 'redux-saga/effects'
import URL from '../../../store/path'


//#region Saga for Post Summary Register 

function* postSummaryRegister() {
  yield takeEvery('POST_REGISTER_SUMMARY', sendSummaryRegister)
}

function* sendSummaryRegister(action) {
  yield call(sendSummaryRegisterAPI, action.payload)
}

function sendSummaryRegisterAPI(data) {
  window.open(URL+'/report/purchasesummary/'+ data, '_blank')
}

//#endregion


//#region Saga for Post Summary Register 

function* postAllPartyRegister() {
  yield takeEvery('POST_REGISTER_ALLPARTY', sendAllPartyRegister)
}

function* sendAllPartyRegister(action) {
  yield call(sendAllPartyRegisterAPI, action.payload)
}

function sendAllPartyRegisterAPI(data) {
  window.open(URL+'/report/purchasedetail/'+data, '_blank')
}

//#endregion

//#region Saga for Post Summary Register 

function* postSinglePartyRegister() {
  yield takeEvery('POST_REGISTER_SINGLEPARTY', sendSinglePartyRegister)
}

function* sendSinglePartyRegister(action) {
  yield call(sendSinglePartyRegisterAPI, action.payload)
}

function sendSinglePartyRegisterAPI(data) {
  window.open(URL+'/report/purchasedetail/'+data, '_blank')
}

//#endregion


export default function* registerSaga() {
  yield all([
    postSummaryRegister(),
    postAllPartyRegister(),
    postSinglePartyRegister()
  ])
}  