import * as type from './types'

export function createGroup(group) {
    return {
        type: type.CREATE_GROUP,
        payload: group,
        txn: 'initiated',
    };
}

export function savedGroup(group) {
    return {
        type: type.SAVED_GROUP,
        payload: group.data.data,
        txn: group.data.type,
        msg: group.data.msg,
        diff: true
    };
}

export function editGroup(group) {
    return {
        type: type.EDIT_GROUP,
        payload: group,
        txn: 'initiated',
    };
}

export function fetchGroup() {
    return {
        type: type.FETCH_GROUP
    };
}

export function fetchedGroup(group) {
    return {
        type: type.FETCHED_GROUP,
        payload: group
    };
}


export function fetchGroupById(group) {
    return {
        type: type.FETCH_GROUP_BY_ID,
        payload: group
    };
}

export function fetchedGroupById(group) {
    return {
        type: type.FETCHED_GROUP_BY_ID,
        payload: group
    };
}


export function setNotifyDoneGroup(props) {
    return {
        type: type.NOTIFICATION_DONE_GROUP,
        payload: props
    };
}

export function deleteGroup(group) {
    return {
        type: type.DELETE_GROUP,
        payload: group
    };
}

export function deletedGroup(group) {
    return {
        type: type.DELETED_GROUP,
        payload: group
    };
}