import { createSelector } from 'reselect'
import { startsWith, cloneDeep } from 'lodash'
import { bank } from './model'

export const getIsFetchingBank = (state, props) => state.bank.params.isFetching;
export const getIsBankFetched = (state, props) => state.bank.params.bankFetched;
export const getSavedBankId = (state, props) => state.bank.params.savedBankId;
export const getBankList = (state, prop) => state.bank.byId
export const getBankById = (state, id) => state.bank.byId[id]
export const getNotification = (state, id) => state.bank.notifications[id]

export const getBank = (state, props) => {
    if(props.match.path==='/bank/create'){
        return bank()
    }
    if(props.match.path==='/bank/edit/:id'){
        let _id = props.match.params.id
        let obj = cloneDeep(state.bank.byId[_id]);
        return obj
    }
}

export const getBankParams = (state, props) => {
    const params =  {}
    if(props.match.path==='/bank/create'){ 
        params.title = state.bank.params.createTitle
        params.submitButtonText = state.bank.params.createSubmitButtonText        
    }else if(props.match.path==='/bank/edit/:id'){ 
        params.title = state.bank.params.editTitle
        params.submitButtonText = state.bank.params.editSubmitButtonText        
    }
    return params
}

export const selectBank = createSelector(
    getBankList,
    bank => { 
        const keys = Object.keys(bank)
        const obj = keys.map( (key) => { return { key : key, value : parseInt(key, 10), text : bank[key].bankName }})
        return obj
    }
)