import { combineReducers } from 'redux'
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr'

// Define a combination schema
const combinationSchema = new schema.Entity('combination',{}, { idAttribute: 'id' });
const combinationListSchema = [combinationSchema];


const defaultCombinationParams = {
  createTitle: '',
  createSubmitButtonText: 'Save',
  editTitle: '',
  editSubmitButtonText: 'Update',
  savedCombinationId: 0
}


function byId(state = {}, action) {
  if (action.type === 'SAVED_COMBINATION') {
    const combinationID = action.payload.id
    return {...state, 
      [combinationID]: action.payload
    }
  }
    if (action.type === 'FETCHED_COMBINATION') {
      console.log("--action.payload--", action.payload) 
      const normalizedCombination = normalize(action.payload, combinationListSchema);
      return merge({},normalizedCombination.entities.combination)
    } 
    if (action.type === 'FETCHED_COMBINATION_BY_ID') { 
      const normalizedCombination = normalize(action.payload, combinationSchema);
      return merge({},normalizedCombination.entities.combination)
    } 
    else {
      return state
    }
  }


  function byListId(state = {}, action) {
    if (action.type === 'SAVED_COMBINATION') {
      const combinationID = action.payload.id
      return {...state, 
        [combinationID]: action.payload
      }
    }
    if (action.type === 'FETCHED_COMBINATION') { 
        const normalizedCombination = normalize(action.payload, combinationListSchema);
        return merge({},normalizedCombination.entities.combination)
    } 
    else {
      return state
    }
  }

  

  function notifications(state = {}, action) {
    if (action.type === 'SAVED_COMBINATION') { 
      return {...state, 
        [action.payload.id]: { ...state[action.payload.id],
          save: { 
            status : action.txn === "success" ? 'success' : 'error',
            msg : action.msg
          }
        }
      }
    } 
    if(action.type === 'NOTIFICATION_DONE_COMBINATION'){
      const {id, type} = action.payload
      // Remove the 'id' element from state
      const { [id]: idValue, ...restOfState } = state;
      // Remove the 'type' from the 'id' element
      const { [type]: removedValue, ...restOfId } = idValue;
      // Merge back together
      const finalState = { ...restOfState, [id]: restOfId };     
      return finalState
  
    } else {
      return state
    }
  }

  function params(state = defaultCombinationParams, action) {
    if (action.type === 'FETCH_COMBINATION') { 
        return {...state, 
          searchParams: action.payload
        }
    } 
    if (action.type === 'SAVED_COMBINATION') { 
        return {...state, 
          savedCombinationId: action.payload.id
        }
    } 
    if (action.type === 'NOTIFICATION_DONE_COMBINATION') { 
      return {...state, 
        savedCombinationId: action.payload.id
      }
    } 
    if (action.type === 'FETCHED_COMBINATION_BY_ID') { 
        return {...state,
          searchParams: {}
        }
    } 
    else {
        return state
    }
  }



const combination = combineReducers({
    byId,
    byListId,
    params,
    notifications
});


export default combination;
