import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { getGroup, getGroupParams, getSavedGroupId, getNotification } from '../data/selectors'
import { createGroup, editGroup, setNotifyDoneGroup } from '../data/actions' 
import { groupSchema } from '../data/model'
import { Container, Header, Form, Button, Grid, Table, Icon } from 'semantic-ui-react'
import { Formik, Field, Form as FormikForm } from 'formik'
import { FormikInputComponent } from '../../../utilities/formUtils'
import Notification from '../../../utilities/notificationUtils'


const GroupForm = (props) => {

    const group = useSelector(state => getGroup(state, props))
    const savedGroupId = useSelector(state => getSavedGroupId(state, props))
    const params = useSelector(state => getGroupParams(state, props))
    const dispatch = useDispatch()

    console.log("params-------", params)

    const saveGroup = (values, resetForm) => {
        if (props.match.path === '/group/create') {
            dispatch(createGroup(values))
            resetForm()
        }
        if (props.match.path === '/group/edit/:id') {
            dispatch(editGroup(values))
            props.history.push(`/group/list`)
        }
    }

    return (
        <Container>
            <Grid columns='equal' className="borderButtomTable">
                <Grid.Row>
                    <Grid.Column width={4}>
                        <Header as='h1' className="VerticalAlign" align='left'>{params.title}</Header>
                    </Grid.Column>
                    <Grid.Column width={6} floated={'right'} align='right'></Grid.Column>
                </Grid.Row>
            </Grid>
            <Container className='formContainer'>
                <Formik id="groupForm" size="large" width={5}
                    initialValues={group}
                    validationSchema={groupSchema}
                    onSubmit={(values, { resetForm }) => saveGroup(values, resetForm)}
                    render={({ handleSubmit, errors, onChange, values, handleChange, setFieldValue }) => (
                        <Form as={FormikForm} size="small" className="CustomeForm" width={5} onSubmit={handleSubmit}>
                            <Table>
                                <Table.Body>
                                    <Table.Row>
                                        <Table.Cell style={{width: '20%'}}><label className="boldText">Group Name: </label></Table.Cell>
                                        <Table.Cell colSpan={5} style={{width: '80%'}}>
                                            <Form.Group widths={2}>
                                                <Field name="groupName" label=' ' component={FormikInputComponent} />
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                </Table.Body>
                            </Table>
                            {console.log("text----------------"+params.submitButtonText)}
                            <Button type="submit" icon labelPosition='right' className="CustomeBTN" color='vk' style={{float: 'right'}}>
                                {params.submitButtonText}
                                <Icon name={props.match.path === '/group/create' ? 'add circle' : 'arrow alternate circle up outline'} />
                            </Button>
                            <br/><br/><br/><br/>
                            {savedGroupId && savedGroupId !== 0 && props.match.path === '/group/create' ? 
                            <Notification id={savedGroupId} notifySelector={getNotification} notifyDoneAction={setNotifyDoneGroup} type='save'/>
                            :
                            null}
                        </Form>
                    )}
                />
            </Container>
        </Container>
    )
}

export default GroupForm;