import { txns } from './store/txnMiddleware'
import { combineReducers } from 'redux'
import { connectRouter } from 'connected-react-router'
import party from './modules/party/data/reducers'
import bank from './modules/bank/data/reducers'
import group from './modules/group/data/reducers'
import finYear from './modules/finyear/data/reducers'
import standard from './modules/standard/data/reducers'
import milkRates from './modules/milkRates/data/reducers'
import purchase from './modules/purchase/data/reducers'
import misc from './modules/misc/data/reducers'
import combination from './modules/combination/data/reducers';


const rootReducer = (history) => combineReducers({
    router: connectRouter(history),
    txns,
    finYear,
    party,
    bank,
    group,
    standard,
    milkRates,
    purchase,
    misc,
    combination
})

export default rootReducer