import { startsWith, cloneDeep } from 'lodash';
import { standard } from './model'

export const getIsFetchingStandard = (state, props) => state.standard.params.isFetching
export const getIsStandardFetched = (state, props) => state.standard.params.standardFetched
export const getSavedStandardId = (state, props) => state.standard.params.savedStandardId
export const getStandardList = (state, prop) => state.standard.byId
export const getNotification = (state, id) => state.standard.notifications[id]

export const getStandard = (state, props) => {
    return standard()
}

export const getStandardParams = (state, props) => {
    const params =  {}

    if(startsWith(state.router.location.pathname,'/standard/create')){ 
        params.title = state.standard.params.createTitle
        params.submitButtonText = state.standard.params.createSubmitButtonText        
    };

    if(startsWith(state.router.location.pathname,'/standard/edit/')){ 
        params.title = state.standard.params.editTitle
        params.submitButtonText = state.standard.params.editSubmitButtonText        
    };

    return params
}

