import { combineReducers } from 'redux';
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr';
import moment from 'moment'

// Define a miscs schema
const miscSchema = new schema.Entity('miscs',{}, { idAttribute: 'name' });
const miscListSchema = [miscSchema];


const defaultMiscParams = {
  createTitle: '',
  createSubmitButtonText: 'Save',
  editTitle: '',
  editSubmitButtonText: 'Update',
  savedMiscId: 0
}


function byId(state = {}, action) {
    if (action.type === 'SAVED_MISC') {
      const miscID = action.payload.name;
      return {...state, 
        [miscID]: action.payload
      }
    }    
    if (action.type === 'FETCHED_MISC') { 
      const normalizedMisc = normalize(action.payload, miscListSchema);
      return merge({},normalizedMisc.entities.miscs)
    } 
    else {
      return state
    }
  }


  function byListId(state = {}, action) {
    if (action.type === 'SAVED_MISC') {
      const miscID = action.payload.name;
      return {...state, 
        [miscID]: action.payload
      }
    }
    if (action.type === 'FETCHED_MISC') { 
        const normalizedMisc = normalize(action.payload, miscListSchema);
        return merge({},normalizedMisc.entities.miscs)
    } 
    else {
      return state
    }
  }

  

  function notifications(state = {}, action) {
    if (action.type === 'SAVED_MISC') { 
      return {...state, 
        [action.payload.id]: { ...state[action.payload.id],
          save: { 
            status : action.txn === "success" ? 'success' : 'error',
            msg : action.msg
          }
        }
      }
    } 
    if(action.type === 'NOTIFICATION_DONE_MISC'){
      const {id, type} = action.payload
      // Remove the 'id' element from state
      const { [id]: idValue, ...restOfState } = state;
      // Remove the 'type' from the 'id' element
      const { [type]: removedValue, ...restOfId } = idValue;
      // Merge back together
      const finalState = { ...restOfState, [id]: restOfId };     
      return finalState
  
    } else {
      return state
    }
  }

  function params(state = defaultMiscParams, action) {
    if (action.type === 'FETCH_MISC') { 
        return {...state, 
          searchParams: action.payload
        }
    }
    if (action.type === 'SAVED_MISC') { 
        return {...state, 
          savedMiscId: action.payload.id
        }
    } 
    if (action.type === 'NOTIFICATION_DONE_MISC') { 
      return {...state, 
        savedMiscId: action.payload.id
      }
    } 
    else {
        return state
    }
  }



const misc = combineReducers({
    byId,
    byListId,
    params,
    notifications
});


export default misc;
