import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import * as select from '../data/selectors'
import { deletePurchase, setNotifyDonePurchase, postPrintPurchase } from '../data/actions'
import { Container, Table, TableHeader, TableBody, Header, Button, Grid, Icon, Form, Modal } from 'semantic-ui-react'
import { FormikDateComponent } from '../../../utilities/formUtils'
import { fetchPurchase } from '../data/actions'
import { purchaseSearch } from '../data/model'
import Notification from '../../../utilities/notificationUtils'
import { Formik, Field, Form as FormikForm } from 'formik'
import PartySelect from '../../party/components/partySelect'
import moment from 'moment'
import { getPartyObjById } from '../../party/data/selectors'

const Purchase = props => {
    const dispatch = useDispatch();

    const [deletePurchaseId, setDeletePurchaseId] = useState(false)
    const [modalOption, setModalOption] = useState({status: false, id: 0})

    const party = useSelector(state => getPartyObjById(state, props.purchase.partyMaster.id))

    const deletePurchaseFromList = (values) => {
        const getVlaue = { id: values }
        dispatch(deletePurchase(getVlaue))
        setDeletePurchaseId(values)
        setModalOption({status: false, id: 0})
    }

    const printPurchaseFromList = (values) => {
        dispatch(postPrintPurchase(values))
    }

    return (
        <Table.Row>
            <Table.Cell></Table.Cell>
            <Table.Cell>{props.index + 1}</Table.Cell>
            <Table.Cell>{props.purchase.billNo}</Table.Cell>
            <Table.Cell>{party.partyName}</Table.Cell>
            <Table.Cell>{moment(props.purchase.billDate).isValid() ? moment(props.purchase.billDate).format("DD-MM-YYYY") : ""}</Table.Cell>
            <Table.Cell>{moment(props.purchase.periodFromDate).isValid() ? moment(props.purchase.periodFromDate).format("DD/MM") : ""} - {moment(props.purchase.periodToDate).isValid() ? moment(props.purchase.periodToDate).format("DD/MM") : ""}</Table.Cell>
            <Table.Cell>{props.purchase.totalQty ? parseFloat(props.purchase.totalQty).toFixed(3) : parseFloat(0).toFixed(3)}</Table.Cell>
            <Table.Cell>{props.purchase.totalKgfat ? parseFloat(props.purchase.totalKgfat).toFixed(3) : parseFloat(0).toFixed(3)}</Table.Cell>
            <Table.Cell>{props.purchase.totalKgsnf ? parseFloat(props.purchase.totalKgsnf).toFixed(3) : parseFloat(0).toFixed(3)}</Table.Cell>
            <Table.Cell>{props.purchase.totalAvgKg ? parseFloat(props.purchase.totalAvgKg).toFixed(3) : parseFloat(0).toFixed(3)}</Table.Cell>
            <Table.Cell>{props.purchase.totalAdditions ? parseFloat(props.purchase.totalAdditions).toFixed(2) : parseFloat(0).toFixed(2)}</Table.Cell>
            <Table.Cell>{props.purchase.totalDeductions ? parseFloat(props.purchase.totalDeductions).toFixed(2) : parseFloat(0).toFixed(2)}</Table.Cell>
            <Table.Cell>{props.purchase.billTotal ? parseFloat(props.purchase.billTotal).toFixed(2) : parseFloat(0).toFixed(2)}</Table.Cell>
            <Table.Cell>{props.purchase.totalRejected ? parseFloat(props.purchase.totalRejected).toFixed(3) : parseFloat(0).toFixed(3)}</Table.Cell>
            <Table.Cell>{props.purchase.totalSour ? parseFloat(props.purchase.totalSour).toFixed(3) : parseFloat(0).toFixed(3)}</Table.Cell>
            <Table.Cell>
                <>
                    <Link to={"/purchase/edit/" + props.purchase.id}>Edit</Link><span>&nbsp;&nbsp;</span>
                    {/* <span style={{ cursor: 'pointer', color: 'blue' }} onClick={() => editPurchaseFromList(props.purchase.id)}>Edit&nbsp;&nbsp;</span> */}
                    <span style={{ cursor: 'pointer', color: 'blue' }} onClick={() => printPurchaseFromList(props.purchase.id)}>Print&nbsp;&nbsp;</span>
                    <span style={{ cursor: 'pointer', color: 'red' }} onClick={() => setModalOption({status: true, id: props.purchase.id})}>Delete</span>
                </>
                <>
                {console.log(deletePurchaseId)}
                    { 
                        deletePurchaseId ?
                            <Notification id={deletePurchaseId} notifySelector={select.getNotification} notifyDoneAction={setNotifyDonePurchase} type='delete'/>
                            :
                            null
                    }
                </>
                <Modal open={modalOption.status} size='mini' onClose={() => setModalOption({status: false, id: 0})}>
                    <Modal.Header>Confirmation</Modal.Header>
                    <Modal.Content>
                        <p>Do you want to delete this Purchase?</p>
                    </Modal.Content>
                    <Modal.Actions>
                        <Button onClick={() => deletePurchaseFromList(modalOption.id)} positive labelPosition='right' icon='checkmark' content='Yes'/>
                        <Button onClick={() => setModalOption({status: false, id: 0})} negative labelPosition='right' icon='close' content='No'/>
                    </Modal.Actions>
                </Modal>
            </Table.Cell>
        </Table.Row>
    )
}

const PurchaseList = (props) => {
    // Loading Logic if tasks not fetched
    const purchase = useSelector(state => select.getPurchaseList(state, props))
    const dispatch = useDispatch()
    const addPurchaseClick = () => {
        props.history.push('/purchase/create')
    }


    const [modalState, setModalState] = useState({isOpen: false, keyWord: ''})

    const fetchList = (values, resetForm) => {
        console.log(values)
        // dispatch(fetchPurchase('11/2019-01-01/2020-12-01'))
        if(values.fromDt !== "" && values.toDt !== ""){
            let fromDt = moment(values.fromDt),
                toDt = moment(values.toDt);
            if(toDt >= fromDt){
                let linkCreate = values.party === "" ? 0+"/"+values.fromDt+"/"+values.toDt : 0+"/"+values.party+"/"+values.fromDt+"/"+values.toDt
                dispatch(fetchPurchase(linkCreate))
            }else{
                setModalState({isOpen: true, keyWord: 'datediff'})
            }
        }else{
            setModalState({isOpen: true, keyWord: 'dateblank'})
        }
    }
    return (
        <Container>
            <Grid columns='equal' className="borderButtomTable">
                <Grid.Row>
                    <Grid.Column width={4}>
                        <Header as='h1' className="VerticalAlign" align='left'>Purchase List</Header>
                    </Grid.Column>
                    <Grid.Column width={12} floated={'right'} align='right'>
                        <Formik id="purchaseForm" size="large" width={10}
                            initialValues={purchaseSearch()}
                            onSubmit={(values, { resetForm }) => fetchList(values, resetForm)}
                            render={({ handleSubmit, errors, onChange, values, handleChange, setFieldValue }) => (
                                <Form as={FormikForm} size="small" className="CustomeFormPurchase" width={10} onSubmit={handleSubmit}>
                                    <Form.Group widths={1}>
                                        <Table style={{width: '100%'}} className='purchasetablePop' basic='very'>
                                            <Table.Body>
                                                <Table.Row>
                                                    <Table.Cell style={{width: '20%', padding: '0px 10px'}} textAlign='right'>
                                                        <PartySelect name="party" isLabel={false} placeholder='Supplier' label=" " isSelection={true} type=''/>
                                                    </Table.Cell>
                                                    <Table.Cell style={{width: '15%', padding: '0px 10px'}} textAlign='right'>
                                                        <Field name="fromDt" label=' ' placeholder={'From Date'} component={FormikDateComponent} />
                                                    </Table.Cell>
                                                    <Table.Cell style={{width: '15%', padding: '0px 10px'}} textAlign='right'>
                                                        <Field name="toDt" label=' ' placeholder={'To Date'} component={FormikDateComponent} />
                                                    </Table.Cell>
                                                    <Table.Cell style={{width: '50%', padding: '0px 10px'}} textAlign='right'>
                                                        <Button type="submit" icon labelPosition='right' className="customeBTNListPurchase" color='vk'>
                                                            Go<Icon name='arrow alternate circle right'/>
                                                        </Button>
                                                        <Button type='button' icon labelPosition='right' className="customeBTNListPurchase" color='vk' onClick={() => addPurchaseClick()}>
                                                            Add<Icon name='add' />
                                                        </Button>
                                                        {/* <Button type='button' icon labelPosition='right' className="customeBTNListPurchase" color='vk' onClick={() => addPurchaseClick()}>
                                                            Print<Icon name='print' />
                                                        </Button>
                                                        <Button type='button' icon labelPosition='right' className="customeBTNListPurchase" color='google plus' onClick={() => addPurchaseClick()}>
                                                            Delete<Icon name='trash alternate outline' />
                                                        </Button> */}
                                                    </Table.Cell>
                                                </Table.Row>
                                            </Table.Body>
                                        </Table>
                                    </Form.Group>
                                </Form>
                            )}
                        />
                        <Modal open={modalState.isOpen} size='mini' onClose={() => setModalState({isOpen: false, keyWord: ''})}>
                            <Modal.Header style={{color: 'red'}}>Warning</Modal.Header>
                            <Modal.Content>
                                <p>
                                    {modalState.keyWord === 'dateblank' ? 'From date and to date should not be blank' : (modalState.keyWord === 'datediff' ? 'To date should be greater than from date' : '')}
                                </p>
                            </Modal.Content>
                            <Modal.Actions>
                                <Button onClick={() => setModalState({isOpen: false, keyWord: ''})} positive labelPosition='right' icon='checkmark' content='Okay'/>
                            </Modal.Actions>
                        </Modal>
                    </Grid.Column>
                </Grid.Row>
            </Grid>
            <br />
            <Table selectable basic='very'>
                <TableHeader>
                    <Table.Row>
                        <Table.HeaderCell></Table.HeaderCell>
                        <Table.HeaderCell>Sl. No.</Table.HeaderCell>
                        <Table.HeaderCell>Bill No.</Table.HeaderCell>
                        <Table.HeaderCell>Supplier</Table.HeaderCell>
                        <Table.HeaderCell>Date</Table.HeaderCell>
                        <Table.HeaderCell>Bill Period</Table.HeaderCell>
                        <Table.HeaderCell>Qty</Table.HeaderCell>
                        <Table.HeaderCell>FAT(Kg)</Table.HeaderCell>
                        <Table.HeaderCell>SNF(Kg)</Table.HeaderCell>
                        <Table.HeaderCell>AVG(Kg)</Table.HeaderCell>
                        <Table.HeaderCell>Addi(Rs)</Table.HeaderCell>
                        <Table.HeaderCell>Dedu(Rs)</Table.HeaderCell>
                        <Table.HeaderCell>Total</Table.HeaderCell>
                        <Table.HeaderCell>Reg(Kg)</Table.HeaderCell>
                        <Table.HeaderCell>Sour(Kg)</Table.HeaderCell>
                        <Table.HeaderCell>Action</Table.HeaderCell>
                    </Table.Row>
                </TableHeader>
                <TableBody>
                    {Object.keys(purchase).map((key, index) =>
                        <Purchase purchase={purchase[key]} key={purchase[key].id} index={index} />)}
                </TableBody>
            </Table>
        </Container>
    )
}

export default PurchaseList;
