import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import { register } from '../data/model'
import { postSummaryDetails, postAllPartyDetails, postSinglePartyDetails } from '../data/actions' 
import { Container, Header, Form, Button, Grid, Table, Icon, Modal } from 'semantic-ui-react'
import { Formik, Field, Form as FormikForm } from 'formik'
import { FormikDateComponent, FormikCheckBoxComponent } from '../../../utilities/formUtils'
import MultiPartySelect from '../../party/components/multiPartySelect'
import moment from 'moment'


const RegisterForm = (props) => {

    const dispatch = useDispatch()

    const [modalState, setModalState] = useState({isOpen: false, keyWord: ''})

    const postRegister = (values, resetForm) => {
        if(values.fromDate !== "" && values.toDate !== ""){
            let fromDt = moment(values.fromDate),
                toDt = moment(values.toDate);
            if(toDt >= fromDt){
                if(values.type === "Summary"){
                    let summaryData = values.fromDate+"/"+values.toDate
                    dispatch(postSummaryDetails(summaryData))
                    resetForm()
                }else if(values.type === "Detailed"){
                    if(values.partytype === "All Suppliers"){
                        let allPartyData = values.fromDate+"/"+values.toDate
                        dispatch(postAllPartyDetails(allPartyData))
                        resetForm()
                    }else if(values.partytype === "Selected Supplier"){
                        values.partyid = values.partyid.toString()
                        let singlePartyData = values.partyid+"/"+values.fromDate+"/"+values.toDate
                        dispatch(postSinglePartyDetails(singlePartyData))
                        resetForm()
                    }
                }
            }else{
                setModalState({isOpen: true, keyWord: 'datediff'})
            }
        }else{
            setModalState({isOpen: true, keyWord: 'dateblank'})
        }        
    }

    return (
        <Container>
            <Grid columns='equal' className="borderButtomTable">
                <Grid.Row>
                    <Grid.Column width={8}>
                        <Header as='h1' className="VerticalAlign" align='left'>Milk Purchase Register</Header>
                    </Grid.Column>
                    <Grid.Column width={2} floated={'right'} align='right'></Grid.Column>
                </Grid.Row>
            </Grid>
            <Container className='formContainer'>
                <Formik id="registerForm" size="large" width={5}
                    initialValues={register()}
                    onSubmit={(values, { resetForm }) => postRegister(values, resetForm)}
                    render={({ handleSubmit, errors, onChange, values, handleChange, setFieldValue }) => (
                        <Form as={FormikForm} size="small" className="CustomeForm" width={5} onSubmit={handleSubmit}>
                            <Table>
                                <Table.Body>
                                    {console.log(values)}
                                    <Table.Row>
                                        <Table.Cell style={{width: '20%'}}><label className="boldText">From Date: </label></Table.Cell>
                                        <Table.Cell style={{width: '30%'}}>
                                            <Field name="fromDate" label=' ' component={FormikDateComponent} />
                                        </Table.Cell>
                                        <Table.Cell style={{width: '20%'}}><label className="boldText">To Date: </label></Table.Cell>
                                        <Table.Cell style={{width: '30%'}}>
                                            <Field name="toDate" label=' ' component={FormikDateComponent} />
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell colSpan='2' style={{width: '50%'}}>
                                            <Field name="type" userProps={{id: 'Summary'}} component={FormikCheckBoxComponent} />
                                        </Table.Cell>
                                        <Table.Cell colSpan='2' style={{width: '50%'}}>
                                            <Field name="type" userProps={{id: 'Detailed'}} component={FormikCheckBoxComponent} />
                                        </Table.Cell>
                                    </Table.Row>
                                    {values.type === "Detailed" ? 
                                    <Table.Row>
                                        <Table.Cell colSpan='2' style={{width: '50%'}}>
                                            <Field name="partytype" userProps={{id: 'All Suppliers'}} defaultCheck={true} component={FormikCheckBoxComponent} />
                                        </Table.Cell>
                                        <Table.Cell colSpan='2' style={{width: '50%'}}>
                                            <Field name="partytype" userProps={{id: 'Selected Supplier'}} component={FormikCheckBoxComponent} />
                                        </Table.Cell>
                                    </Table.Row>
                                    : null}
                                    {values.partytype === "Selected Supplier" ? 
                                    <Table.Row>
                                        <Table.Cell style={{width: '20%'}}>
                                            <label className="boldText">Party Name: </label>
                                        </Table.Cell>
                                        <Table.Cell colSpan='3' style={{width: '80%'}}>
                                            <MultiPartySelect name="partyid" isLabel={false} label=" " isSelection={true} type=''/>
                                        </Table.Cell>
                                    </Table.Row>
                                    : null}
                                </Table.Body>
                            </Table>
                            <Button type="submit" icon labelPosition='right' className="CustomeBTN" color='vk' style={{float: 'right'}}>
                                Print
                                <Icon name='print'/>
                            </Button>
                            <br/><br/><br/><br/>
                            <Modal open={modalState.isOpen} size='mini' onClose={() => setModalState({isOpen: false, keyWord: ''})}>
                                <Modal.Header style={{color: 'red'}}>Warning</Modal.Header>
                                <Modal.Content>
                                    <p>
                                        {modalState.keyWord === 'dateblank' ? 'From date and to date should not be blank' : (modalState.keyWord === 'datediff' ? 'To date should be greater than from date' : '')}
                                    </p>
                                </Modal.Content>
                                <Modal.Actions>
                                    <Button onClick={() => setModalState({isOpen: false, keyWord: ''})} positive labelPosition='right' icon='checkmark' content='Okay'/>
                                </Modal.Actions>
                            </Modal>
                        </Form>
                    )}
                />
            </Container>
        </Container>
    )
}

export default RegisterForm;