import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { combinationSchema } from '../data/model'
import { Container, Header, Form, Button, Grid, Table, Icon } from 'semantic-ui-react'
import { Formik, Form as FormikForm } from 'formik'
import TypeSelect from './typeSelect'
import * as select from '../data/selectors'
import { editCombination, setNotifyDoneCombination } from '../data/actions'
import Notification from '../../../utilities/notificationUtils'


const CombinationForm = (props) => {
    
    const combination = useSelector(state => select.getCombination(state, '1'))
    const savedCombinationId = useSelector(state => select.getSavedCombinationId(state, props))
    const dispatch = useDispatch()
    const saveCombination = (values, resetForm) => {
        dispatch(editCombination(values))
    }

    return (
        <Container>
            <Grid columns='equal' className="borderButtomTable">
                <Grid.Row>
                    <Grid.Column width={4}>
                        <Header as='h1' className="VerticalAlign" align='left'>Set Combination</Header>
                    </Grid.Column>
                    <Grid.Column width={6} floated={'right'} align='right'></Grid.Column>
                </Grid.Row>
            </Grid>
            <Container className='formContainer'>
                <Formik id="standardForm" size="large" width={5}
                    initialValues={combination}
                    validationSchema={combinationSchema}
                    onSubmit={(values, { resetForm }, ) => saveCombination(values, resetForm)}
                    render={({ handleSubmit, errors, onChange, values, handleChange, setFieldValue }) => (
                        <Form as={FormikForm} size="small" className="CustomeForm" width={5} onSubmit={handleSubmit}>
                            <Table>
                                <Table.Body>
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}><label className="boldText">Standard Type: </label></Table.Cell>
                                        <Table.Cell colSpan={2} style={{ width: '80%' }}>
                                            <Form.Group widths={2}>
                                                <TypeSelect name="setStandard" isLabel={false} label=" " isSelection={true} type='' />
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{ width: '20%' }}><label className="boldText">Milk Rate Type: </label></Table.Cell>
                                        <Table.Cell colSpan={2} style={{ width: '80%' }}>
                                            <Form.Group widths={2}>
                                                <TypeSelect name="setRate" isLabel={false} label=" " isSelection={true} type='' />
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                </Table.Body>
                            </Table>
                            <Button type="submit" icon labelPosition='right' className="CustomeBTN" color='vk' style={{ float: 'right' }}>
                                Update
                                <Icon name='arrow alternate circle up outline' />
                            </Button>
                            <br /><br /><br /><br />
                            {savedCombinationId && savedCombinationId !== 0 ? 
                            <Notification id={savedCombinationId} notifySelector={select.getNotification} notifyDoneAction={setNotifyDoneCombination} type='save'/>
                            :
                            null}
                        </Form>
                    )}
                />
            </Container>
        </Container>
    )
}

export default CombinationForm;