import { call, takeEvery, all, put, select } from 'redux-saga/effects'
import { getIsGroupFetched } from './selectors'
import { savedGroup, fetchedGroup, fetchedGroupById, deletedGroup } from './actions'
import axios from 'axios';
import { handlError, parseError } from '../../app/serverError'
import URL from '../../../store/path'

//#region Saga for Create Group 

function* createGroup() {
  yield takeEvery('CREATE_GROUP', saveGroup)
}

function* saveGroup(action) {
  const { response, error } = yield call(saveGroupAPI, action.payload)
  if (response) yield put(savedGroup({ data: response.data, msg: response.msg, type: response.type }))
  else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function saveGroupAPI(data) {
  return axios.post(URL + '/groupmaster/create', data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for Edited Group 

function* editGroup() {
  yield takeEvery('EDIT_GROUP', saveEditedGroup)
}

function* saveEditedGroup(action) {
  const { response, error } = yield call(editGroupAPI, action.payload)
  if (response) yield put(savedGroup({ data: response.data, msg: response.msg, type: response.type }))
  else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function editGroupAPI(data) {
  return axios.post(URL + '/groupmaster/update/', data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for Getting Group by ID

function* fetchGroupById() {
  yield takeEvery('FETCH_GROUP_BY_ID', saveFetchedGroupById)
}

function* saveFetchedGroupById(action) {
  const { response, error } = yield call(fetchGroupByIdAPI, action.payload)
  if (response) yield put(fetchedGroupById(response.data))
  else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function fetchGroupByIdAPI(data) {
  let _id = data
  return axios.get(URL + '/groupmaster/fetch/' + _id, data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion

//#region Saga for List Tasks 

function* fetchGroup() {

  yield takeEvery('FETCH_GROUP', requestGroup)

}

function* requestGroup() {
  const isGroupFetched = yield select(getIsGroupFetched);
  if (!isGroupFetched) {
    let { data } = yield call(requestGroupAPI)
    yield put(fetchedGroup(data.data))
  }
}

function requestGroupAPI() {
  return axios.get(URL + '/groupmaster/fetch/all', { crossDomain: true })
}

//#endregion

//#region Saga for Delete Group 

function* deleteGroup() {
  yield takeEvery('DELETE_GROUP', saveDeleteGroup)
}

function* saveDeleteGroup(action) {
  const { response, error } = yield call(deleteGroupAPI, action.payload)
  if (response) yield put(deletedGroup({ id: action.payload.id, msg: response.data.msg, type: response.data.type }))
  else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function deleteGroupAPI(data) {
  return axios.post(URL + '/groupmaster/delete', data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion

const sagaErrorMessage = (error, action) => {
  console.group("Saga Error:" + action.type)
  console.log(error)
  console.groupEnd()
}

export default function* groupSaga() {
  yield all([
    createGroup(),
    fetchGroup(),
    editGroup(),
    deleteGroup(),
    fetchGroupById()
  ])
}  