import { startsWith, cloneDeep } from 'lodash';
import { misc } from './model'

export const getIsFetchingMisc = (state, props) => state.misc.params.isFetching
export const getIsMiscFetched = (state, props) => state.misc.params.miscFetched
export const getSavedMiscId = (state, props) => state.misc.params.savedMiscId
export const getMiscList = (state, prop) => state.misc.byId
export const getMiscListByName = (state, name) => state.misc.byId[name]
export const getNotification = (state, id) => state.misc.notifications[id]

export const getMisc = (state, props) => {
    return misc()
}

export const getMiscParams = (state, props) => {
    const params =  {}

    if(startsWith(state.router.location.pathname,'/misc/create')){ 
        params.title = state.misc.params.createTitle
        params.submitButtonText = state.misc.params.createSubmitButtonText        
    };

    if(startsWith(state.router.location.pathname,'/misc/edit/')){ 
        params.title = state.misc.params.editTitle
        params.submitButtonText = state.misc.params.editSubmitButtonText        
    };

    return params
}

