import { call, takeEvery, all, put, select } from 'redux-saga/effects'
import { getIsPartyFetched } from './selectors'
import { savedParty, fetchedParty, fetchedPartyById, deletedParty } from './actions'
import axios from 'axios';
import { handlError, parseError } from '../../app/serverError'
import URL from '../../../store/path'

//#region Saga for Create Party 

function* createParty() {
    yield takeEvery('CREATE_PARTY',saveParty)
  }

function* saveParty(action){
  const { response, error } = yield call(savePartyAPI, action.payload)
  if (response) yield put (savedParty({ data: response.data, msg: response.msg, type: response.type }))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function savePartyAPI(data) {
    return axios.post(URL+'/partymaster/create', data, {crossDomain: true})
      .then(response => ({ response }))
      .catch(error => ({ error }))
  }

//#endregion


//#region Saga for Edited Party 

function* editParty() {
  yield takeEvery('EDIT_PARTY',saveEditedParty)
}

function* saveEditedParty(action){
  const { response, error } = yield call(editPartyAPI, action.payload)  
  if (response) yield put (savedParty({ data: response.data, msg: response.msg, type: response.type }))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function editPartyAPI(data) {
  return axios.post(URL+'/partymaster/update', data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for Getting Party by ID

function* fetchPartyById() {
  yield takeEvery('FETCH_PARTY_BY_ID',saveFetchedPartyById)
}

function* saveFetchedPartyById(action){
  const { response, error } = yield call(fetchPartyByIdAPI, action.payload)  
  if (response) yield put (fetchedPartyById(response.data))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function fetchPartyByIdAPI(data) {
  let id = data
  return axios.get(URL+'/partymaster/fetch/'+ id, data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion

//#region Saga for List Tasks 

function* fetchParty() {

  yield takeEvery('FETCH_PARTY',requestParty)

}
  
function* requestParty(){
  const isPartyFetched = yield select(getIsPartyFetched);
  if(!isPartyFetched){
    let { data } = yield call(requestPartyAPI)
    yield put (fetchedParty(data.data))
  }
}

function requestPartyAPI(data) {
  return axios.get(URL+'/partymaster/fetch/all', {crossDomain: true})
}

//#endregion

//#region Saga for Delete Party 

function* deleteParty() {
  yield takeEvery('DELETE_PARTY',saveDeleteParty)
}

function* saveDeleteParty(action){
  const { response, error } = yield call(deletePartyAPI, action.payload)
  if (response) yield put (deletedParty({ id: action.payload.id, msg: response.data.msg, type: response.data.type }))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function deletePartyAPI(data) {
  return axios.post(URL+'/partymaster/delete', data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for Post Party Print

function* postPartyPrint() {
  yield takeEvery('PRINT_PARTY', sendPartyPrint)
}

function* sendPartyPrint() {
  yield call(sendPartyPrintAPI)
}

function sendPartyPrintAPI() {
  window.open(URL+'/partyprintlist', '_blank')
}

//#endregion

const sagaErrorMessage = (error, action) => {
    console.party("Saga Error:"+action.type)
    console.log(error)
    console.partyEnd()
}

export default function* partySaga() {
    yield all([
        createParty(),
        fetchParty(),
        editParty(),
        deleteParty(),
        fetchPartyById(),
        postPartyPrint()
    ])
  }  