import { combineReducers } from 'redux'
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr'

// Define a banks schema
const bankSchema = new schema.Entity('banks',{}, { idAttribute: 'id' });
const bankListSchema = [bankSchema];


const defaultBankParams = {
  createTitle: 'Create Bank',
  createSubmitButtonText: 'Create',
  editTitle: 'Update Bank',
  editSubmitButtonText: 'Update',
  bankFetched: false,
  savedBankId: 0
}


function byId(state = {}, action) {
    if (action.type === 'SAVED_BANK') {
      const bankID = action.payload.id;
      return {...state, 
        [bankID]: action.payload
      }
    }
    if (action.type === 'FETCHED_BANK') { 
      const normalizedBank = normalize(action.payload, bankListSchema);
      return merge({}, normalizedBank.entities.banks)
    } 
    if (action.type === 'FETCHED_BANK_BY_ID') { 
      const normalizedBank = normalize(action.payload, bankSchema);
      return merge({},normalizedBank.entities.banks)
    }
    if (action.type === 'DELETED_BANK') {
      if(action.payload.type === "success"){
        const bankID = action.payload.id
        let finalState = {...state}
        delete finalState[bankID]
        return finalState
      }else{
        return state
      }
    } else {
      return state
    }
  }


  function byListId(state = {}, action) {
    if (action.type === 'SAVED_BANK') {
      const bankID = action.payload.id;
      return {...state, 
        [bankID]: action.payload
      }
    }
    if (action.type === 'FETCHED_BANK') { 
        const normalizedBank = normalize(action.payload, bankListSchema);
        return merge({}, normalizedBank.entities.banks)
    } 
    if (action.type === 'DELETED_BANK') {
      if(action.payload.type === "success"){
        const bankID = action.payload.id
        let finalState = {...state}
        delete finalState[bankID]
        return finalState
      }else{
        return state
      }
    }else {
      return state
    }
  }

  

  function notifications(state = {}, action) {
    if (action.type === 'DELETED_BANK') { 
      return {...state, 
        [action.payload.id]: { ...state[action.payload.id],
          delete: { 
            status : action.payload.type === "success" ? 'success' : 'error',
            msg : action.payload.msg
          }
        }
      }
    } 
    if (action.type === 'SAVED_BANK') { 
      return {...state, 
        [action.payload.id]: { ...state[action.payload.id],
          save: { 
            status : action.txn === "success" ? 'success' : 'error',
            msg : action.msg
          }
        }
      }
    } 
    if(action.type === 'NOTIFICATION_DONE_BANK'){
      const {id, type} = action.payload
      // Remove the 'id' element from state
      const { [id]: idValue, ...restOfState } = state;
      // Remove the 'type' from the 'id' element
      const { [type]: removedValue, ...restOfId } = idValue;
      // Merge back together
      const finalState = { ...restOfState, [id]: restOfId };     
      return finalState
  
    } else {
      return state
    }
  }

  function params(state = defaultBankParams, action) {
    if (action.type === 'FETCH_BANK') { 
        return {...state, 
          searchParams: action.payload
        }
    }
    if (action.type === 'SAVED_BANK') { 
      return {...state, 
        savedBankId: action.payload.id
      }
    }
    if(action.type === 'NOTIFICATION_DONE_BANK'){
      return {...state, 
        savedBankId: action.payload.id
      }
    }
    if (action.type === 'FETCHED_BANK') { 
      return {...state, 
        bankFetched: true
      }
    }  
    if (action.type === 'FETCHED_BANK_BY_ID') { 
        return {...state,
          searchParams: {}
        }
    } 
    else {
        return state
    }
  }



const bank = combineReducers({
  notifications,  
  byId,
  byListId,
  params
});


export default bank;
