import * as Yup from 'yup'
import { parseDateFromFormats } from '../../../utilities/parseDate'

export const misc =  () => ({
    "typeName": "",
    "name": "",
    "description": "",
    "id": "",
    "updatedOn": "",
    "value": "",
    "operation": "",
    "operationOn": "",
    "createdOn" : "",
    "status": ""
})


Yup.addMethod(Yup.date, 'format', parseDateFromFormats);
export const miscSchema = Yup.object().shape({

  name: Yup.string()
  .required('This is a Required Field'),

  value: Yup.string()
    .required('This is a Required Field'),

  operationOn: Yup.string()
    .required('This is a Required Field')

});
