import React from 'react'
import { useSelector } from 'react-redux'
import { Field } from 'formik'
import { FormikMultiSelectComponent } from '../../../utilities/formUtils'
import { selectParty } from '../data/selectors'
    

const MultiPartySelect = ({ name, isTxn, isLabel, label, isSelection, setState }, props) => {
    
    const opt = useSelector(state => selectParty(state, props))

    const getSelectedValue = (values) => {
        if(setState)
            setState(values === "" ? 0 : values)
    }

    return (
        <Field name={name} isLabel={isLabel} isTxn={isTxn} label={label} isSelection={isSelection} component={FormikMultiSelectComponent} userProps={{ options: opt, getValue: getSelectedValue }}> </Field>
    )

}


export default MultiPartySelect;