import { startsWith, cloneDeep } from 'lodash';
import { milkRates } from './model'

export const getIsFetchingMilkRates = (state, props) => state.milkRates.params.isFetching
export const getIsMilkRatesFetched = (state, props) => state.milkRates.params.milkRatesFetched
export const getSavedMilkRatesId = (state, props) => state.milkRates.params.savedMilkRatesId
export const getMilkRatesList = (state, prop) => state.milkRates.byId
export const getNotification = (state, id) => state.milkRates.notifications[id]

export const getMilkRates = (state, props) => {
    return milkRates()
}
export const getMilkRatesParams = (state, props) => {
    const params =  {}

    if(startsWith(state.router.location.pathname,'/milkRates/create')){ 
        params.title = state.milkRates.params.createTitle
        params.submitButtonText = state.milkRates.params.createSubmitButtonText        
    };

    if(startsWith(state.router.location.pathname,'/milkRates/edit/')){ 
        params.title = state.milkRates.params.editTitle
        params.submitButtonText = state.milkRates.params.editSubmitButtonText        
    };

    return params
}

