import React from 'react'
import { useSelector } from 'react-redux'
import { Field } from 'formik'
import { FormikSelectComponent } from '../../../utilities/formUtils'
import * as select from '../data/selectors'
    

const TypeSelect = ({ name, isTxn, isLabel, label, isSelection, userProps }, props) => {

    const settings = useSelector(state => select.getStandardList(state, props))
    const opt = [{
        key : "Flush",
        value : "Flush",
        text : "Flush"
    },{
        key : "Lean",
        value : "Lean",
        text : "Lean"
    }]

    const getSelectedValue = (values) => {
        if(userProps && userProps !== undefined && userProps.setField && userProps.setField !== undefined){
            userProps.setField('type', values) 
            userProps.setField('goodFat', settings[values] ? settings[values].goodFat ? settings[values].goodFat : '' : '') 
            userProps.setField('goodSnf', settings[values] ? settings[values].goodSnf ? settings[values].goodSnf : '' : '') 
            userProps.setField('badFat', settings[values] ? settings[values].badFat ? settings[values].badFat : '' : '') 
            userProps.setField('badSnf', settings[values] ? settings[values].badSnf ? settings[values].badSnf : '' : '') 
            userProps.setField('pageName', settings[values] ? settings[values].pageName ? settings[values].pageName : '' : '') 
        }
    }


    return (
        <Field name={name} isLabel={isLabel} isTxn={isTxn} label={label} isSelection={isSelection} component={FormikSelectComponent} userProps={{ options: opt, getValue: getSelectedValue }}> </Field>
    )

}


export default TypeSelect;
