import React from 'react'
import { useSelector } from 'react-redux'
import { Field } from 'formik'
import { FormikSelectComponent } from '../../../utilities/formUtils'
import { selectParty } from '../data/selectors'
    

const PartySelect = ({ name, isTxn, isLabel, label, isSelection, setState, placeholder }, props) => {
    
    const opt = useSelector(state => selectParty(state, props))

    const getSelectedValue = (values) => {
        if(setState)
            setState(values === "" ? 0 : values)
    }
    return (
        <Field name={name} isLabel={isLabel} isTxn={isTxn} label={label} placeholder={placeholder} isSelection={isSelection} component={FormikSelectComponent} userProps={{ options: opt, getValue: getSelectedValue }}> </Field>
    )

}


export default PartySelect;