import { call, takeEvery, all, put } from 'redux-saga/effects'
import { savedFinYear, fetchedFinYear, fetchedFinYearById } from './actions'
import axios from 'axios';
import { handlError, parseError } from '../../app/serverError'
import URL from '../../../store/path'

//#region Saga for Create FinYear 

function* createFinYear() {
    yield takeEvery('CREATE_FINYEAR',saveFinYear)
  }

function* saveFinYear(action){
  const { response, error } = yield call(saveFinYearAPI, action.payload)
  if (response) yield put (savedFinYear({ data: response.data, msg: response.msg, type: response.type }))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function saveFinYearAPI(data) {
    return axios.post(URL+'/sessionperiod/create', data, {crossDomain: true})
      .then(response => ({ response }))
      .catch(error => ({ error }))
  }

//#endregion


//#region Saga for Edited FinYear 

function* editFinYear() {
  yield takeEvery('EDIT_FINYEAR',saveEditedFinYear)
}

function* saveEditedFinYear(action){
  const { response, error } = yield call(editFinYearAPI, action.payload)  
  if (response) yield put (savedFinYear({ data: response.data, msg: response.msg, type: response.type }))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function editFinYearAPI(data) {
  return axios.post(URL+'/sessionperiod/update', data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for Getting FinYear by ID

function* fetchFinYearById() {
  yield takeEvery('FETCH_FINYEAR_BY_ID',saveFetchedFinYearById)
}

function* saveFetchedFinYearById(action){
  const { response, error } = yield call(fetchFinYearByIdAPI, action.payload)  
  if (response) yield put (fetchedFinYearById(response.data))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function fetchFinYearByIdAPI(data) {
  let id = data
  return axios.post(URL+'sessionperiod/fetch/'+id, data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion

//#region Saga for List Tasks 

function* fetchFinYear() {

  yield takeEvery('FETCH_FINYEAR',requestFinYear)

}
  
function* requestFinYear(action){

  let { data } = yield call(requestFinYearAPI, action.payload)
  yield put (fetchedFinYear(data.data))

}

function requestFinYearAPI(data) {
  return axios.get(URL+'/sessionperiod/fetch/all', data,  {crossDomain: true})
}

//#endregion

const sagaErrorMessage = (error, action) => {
    console.group("Saga Error:"+action.type)
    console.log(error)
    console.groupEnd()
}

export default function* finYearSaga() {
    yield all([
        createFinYear(),
        fetchFinYear(),
        editFinYear(),
        fetchFinYearById()
    ])
  }  