import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux'
import { getVdetailsObj } from '../data/selectors'
import { Form, Button, Modal, Table, Checkbox } from 'semantic-ui-react';
import { Formik, Field, Form as FormikForm } from 'formik';
import { FormikAmountComponent, FormikTextAreaComponent, FormikDateComponent } from '../../../utilities/formUtils'
import TypeTxnSelect from './typeTxnSelect'
import ShiftSelect from './shiftSelect'
import QualitySelect from './qualitySelect'
import store from 'store2'
import { getMilkRatesList } from '../../milkRates/data/selectors'
import { getStandardList } from '../../standard/data/selectors'
import { getCombinationById } from '../../combination/data/selectors'

import moment from 'moment'

const AddQuickVdetails = ({ setItemObject, setNextDate, nextDate, index, voucherObj, name, setField, values, ...props }) => {
    
    const [displayStatus, setDisplayStatus] = useState(false)
    const [modalState, setModalState] = useState({ isOpen: false, keyWord: '' })
    const [netQty, setNetQty] = useState(vdetails ? vdetails.qty === "" ? 0 : vdetails.qty : 0)
    const [isAuto, setIsAuto] = useState(false)
    const [isCLRhas, setIsCLRhas] = useState(false)
    const [popNextDate, setPopNextDate] = useState(nextDate)

    const combination = useSelector(state => getCombinationById(state, 1))
    const vdetails = useSelector(state => getVdetailsObj(state, voucherObj, index, store.get("setIndex"), combination))
    const milkRates = useSelector(state => getMilkRatesList(state, props))
    const standard = useSelector(state => getStandardList(state, props))

    useEffect(() => {
        if (index === "y") {
            setNetQty(vdetails ? vdetails.qty === "" ? 0 : vdetails.qty : 0)
            setIsCLRhas(vdetails ? vdetails.clrP === "" || vdetails.clrP === 0 ? false : true : false)
        }
    }, [vdetails])

    const saveItems = (value, resetForm) => {
        let flag = false,
            qty = value.qty === undefined ? 0 : value.qty === "" ? 0 : value.qty,
            rejectQty = value.rejectedQty === undefined ? 0 : value.rejectedQty === "" ? 0 : value.rejectedQty,
            sourQty = value.sourQty === undefined ? 0 : value.sourQty === "" ? 0 : value.sourQty,
            milkPrice = value.milkPrice === undefined ? 0 : value.milkPrice === "" ? 0 : value.milkPrice
        if (qty === 0 && rejectQty === 0 && sourQty === 0) {
            flag = false
            setModalState({ isOpen: true, keyWord: 'anyqty' })
        } else {
            if (qty !== 0) {
                if (milkPrice === 0) {
                    flag = false
                    setModalState({ isOpen: true, keyWord: 'calculate' })
                } else {
                    flag = true
                }
            } else {
                flag = true
            }
        }
        if (flag) {
            if (index === '') {
                let new_date = moment(value.date, "YYYY-MM-DD").add('days', 1),
                    day = new_date.format('DD'),
                    month = new_date.format('MM'),
                    year = new_date.format('YYYY')
                setItemObject(values, value, setField)
                if(isAuto){
                    store.session('getShift', value.shift)
                }else{
                    store.session('getShift', value.shift === "Morning" ? "Evening" : "Morning")
                }
                resetForm()
                setNextDate(year+'-'+month+'-' +day)
                setPopNextDate(year+'-'+month+'-' +day)
            } else {
                setItemObject(values, value, setField, store.get("setIndex"))
                setDisplayStatus(false)
            }
        }
    }

    const closeWindow = () => {
        setDisplayStatus(false)
        if(isAuto === false){
            store.session('getShift', null)
        }
    }
 
    const onChangeQty = (values, setField) => {
        setNetQty(values)
        if (values === "" || values === 0) {
            setField('clrP', '')
            setField('fatP', '')
            setField('snfP', '')
            setField('fatQuality', '')
            setField('snfQuality', '')
            setField('fatRate', '')
            setField('snfRate', '')
            setField('kgFat', '')
            setField('kgSnf', '')
            setField('fatAmount', '')
            setField('snfAmount', '')
            setField('milkRate', '')
            setField('milkPrice', '')
        }
    }

    const onChangeCLR = (values, setField) => {
        if (values === "" || values === 0) {
            setIsCLRhas(false)
        } else {
            setIsCLRhas(true)
            setField('snfP', '')

        }
    }

    const getOpenPopup = () => {
        setNetQty(vdetails ? vdetails.qty === "" ? 0 : vdetails.qty : 0)
        setIsCLRhas(vdetails ? vdetails.clrP === "" || vdetails.clrP === 0 ? false : true : false)
        let getAuto = store.session('isAuto') && store.session('isAuto') !== undefined ? store.session('isAuto') : false
        setIsAuto(getAuto)
        store.session('isAuto', getAuto)
        let shiftGet = index === "" ? store.session('getShift') && store.session('getShift') !== undefined ? (getAuto ? store.session('getShift') : (store.session('getShift') === "Morning" ? "Evening" : "Morning")) : 'Morning' : vdetails.shift
        store.session('getShift', shiftGet)
        setPopNextDate(nextDate)
        if (index === "y") {
            if (store.get("setIndex") !== undefined && store.get("setIndex") !== null) {
                setDisplayStatus(true)
            }
        } else {
            setDisplayStatus(true)
        }
    }

    const setAutoTiming = (values, ischeck) => {
        setIsAuto(ischeck)
        store.session('isAuto', ischeck)
        store.session('getShift', values.shift)
    }

    const calculateRate = (setField, values) => {
        if (values.setStandard !== "" && values.setRate !== "") {
            if (values.qty !== "" && values.qty !== 0) {
                if (values.fatP !== "" && values.fatP !== 0) {
                    if ((values.snfP !== undefined && values.snfP !== "" && values.snfP !== 0 && isCLRhas === false) || (isCLRhas === true && values.clrP !== undefined && values.clrP !== '' && values.clrP !== 0)) {
                        let getStandard = values.setStandard
                        let getMilkRate = values.setRate
                        let standardObj = standard[getStandard]
                        let milkRateObj = milkRates[getMilkRate]
                        let qtyGet = values.qty
                        let fat = values.fatP
                        let clr = values.clrP
                        let snf = isCLRhas === false ? values.snfP : ((parseFloat(clr) / 4) + (0.25 * parseFloat(fat)) + 0.44)
                        if (isCLRhas) {
                            setField('snfP', snf)
                        }
                        let fatType = parseFloat(fat) >= parseFloat(standardObj.goodFat) ? "Good" : "Bad"
                        let snfType = parseFloat(snf) >= parseFloat(standardObj.goodSnf) ? "Good" : "Bad"
                        setField('fatQuality', fatType)
                        setField('snfQuality', snfType)
                        let fatRate = fatType === "Good" ? milkRateObj.goodFat : milkRateObj.badFat
                        let snfRate = fatType === "Good" ? milkRateObj.goodSnf : milkRateObj.badSnf
                        setField('fatRate', fatRate)
                        setField('snfRate', snfRate)
                        let kgFat = (parseFloat(qtyGet) * parseFloat(fat)) / 100
                        let kgSnf = (parseFloat(qtyGet) * parseFloat(snf)) / 100
                        setField('kgFat', kgFat)
                        setField('kgSnf', kgSnf)
                        let fatAmt = parseFloat(kgFat) * parseFloat(fatRate)
                        let snfAmt = parseFloat(kgSnf) * parseFloat(snfRate)
                        setField('fatAmount', fatAmt)
                        setField('snfAmount', snfAmt)
                        let totalRs = parseFloat(fatAmt) + parseFloat(snfAmt)
                        let milkRatePerKg = parseFloat(totalRs) / parseFloat(qtyGet)
                        setField('milkRate', milkRatePerKg)
                        setField('milkPrice', totalRs)
                    } else {
                        setModalState({ isOpen: true, keyWord: 'clrsnf' })
                    }
                } else {
                    setModalState({ isOpen: true, keyWord: 'fatP' })
                }
            } else {
                setModalState({ isOpen: true, keyWord: 'anyqty' })
            }
        } else {
            setModalState({ isOpen: true, keyWord: 'setDD' })
        }
    }

    return (
        <Modal open={displayStatus} closeOnEscape={false} closeOnDimmerClick={false} trigger={<Button type="button" className="CustomeBTNWithoutMargin" color='vk' style={{ float: 'right' }} onClick={() => getOpenPopup()}>{index === '' ? 'Add' : 'Edit'}</Button>} onClose={() => setDisplayStatus(false)}>
            <Modal.Header>Add Details</Modal.Header>
            <Modal.Content>
                <Formik id="vdForm" size="large" width={5}
                    initialValues={vdetails}
                    // validationSchema={itemsQuickSchema}
                    onSubmit={(values, { resetForm }) => saveItems(values, resetForm)}
                    render={({ handleSubmit, onChange, values, handleChange, errors, setFieldValue }) => (
                        <Form key={index} as={FormikForm} size="small" className="CustomeFormPurchase" width={5} onSubmit={handleSubmit}>
                            <Table style={{ width: '100%' }} className='purchasetablePop'>
                                <Table.Body>
                                    <Table.Row className="VInputPanel">
                                        <Table.Cell style={{ width: '15%' }}>
                                            <label className="boldText">Standard</label>
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '35%' }}>
                                            <Form.Group widths={2}>
                                                <TypeTxnSelect name="setStandard" isLabel={false} label=" " isSelection={true} type='' setField={setFieldValue} />
                                            </Form.Group>
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '15%' }}>
                                            <label className="boldText">Rate Name</label>
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '35%' }}>
                                            <Form.Group widths={2}>
                                                <TypeTxnSelect name="setRate" isLabel={false} label=" " isSelection={true} type='' setField={setFieldValue} />
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row className="VInputPanel">
                                        <Table.Cell style={{ width: '15%' }}>
                                            <label className="boldText">Supply Date</label>
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '35%' }}>
                                            <Form.Group widths={2}>
                                                <Field name="date" label=' ' component={FormikDateComponent} setDateManually={popNextDate}/>
                                            </Form.Group>
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '15%' }}>
                                            <Checkbox label='Auto' checked={isAuto ? true : false} onChange={(e, { checked }) => setAutoTiming(values, checked)} />
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '35%' }}>
                                            <ShiftSelect name="shift" isLabel={false} label=" " isSelection={true} type='' setField={setFieldValue} />
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row className="VInputPanel">
                                        <Table.Cell colSpan='4' style={{ width: '100%', padding: '0px' }}>
                                            <Table style={{ width: '100%' }} className='purchasetablePop'>
                                                <Table.Body>
                                                    <Table.Row className="VInputPanel">
                                                        <Table.Cell style={{ width: '15%' }}>
                                                            <label className="boldText">Qyantity</label>
                                                        </Table.Cell>
                                                        <Table.Cell style={{ width: '25%' }}>
                                                            <Form.Group widths={2}>
                                                                <Field name="qty" label=' ' component={FormikAmountComponent} onChangeFunc={onChangeQty} />
                                                            </Form.Group>
                                                        </Table.Cell>
                                                        <Table.Cell style={{ width: '15%' }}>
                                                            <label className="boldText">Quantity(R)</label>
                                                        </Table.Cell>
                                                        <Table.Cell style={{ width: '15%' }}>
                                                            <Field name="rejectedQty" label=' ' component={FormikAmountComponent} isReadonly={netQty === "" || netQty === 0 ? false : true} />
                                                        </Table.Cell>
                                                        <Table.Cell style={{ width: '15%' }}>
                                                            <label className="boldText">Quantity(S)</label>
                                                        </Table.Cell>
                                                        <Table.Cell style={{ width: '15%' }}>
                                                            <Field name="sourQty" label=' ' component={FormikAmountComponent} isReadonly={netQty === "" || netQty === 0 ? false : true} />
                                                        </Table.Cell>
                                                    </Table.Row>
                                                </Table.Body>
                                            </Table>
                                        </Table.Cell>
                                    </Table.Row>
                                </Table.Body>
                            </Table>
                            <Table style={{ width: '100%', margin: '0px' }}>
                                <Table.Body>
                                    <Table.Row className="VInputPanel">
                                        <Table.Cell style={{ width: '5%' }}>
                                            <label className="boldText">CLR</label>
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '15%' }}>
                                            <Field name="clrP" label=' ' component={FormikAmountComponent} onChangeFunc={onChangeCLR} isReadonly={netQty === "" || netQty === 0 ? true : false} />
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <label className="boldText">Quality</label>
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <label className="boldText">Rate / Kg</label>
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <label className="boldText">Total Kg</label>
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <label className="boldText">Amount</label>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row className="VInputPanel">
                                        <Table.Cell style={{ width: '5%' }}>
                                            <label className="boldText">Fat</label>
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '15%' }}>
                                            <Field name="fatP" label=' ' component={FormikAmountComponent} isReadonly={netQty === "" || netQty === 0 ? true : false} />
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <QualitySelect name="fatQuality" isLabel={false} label=" " isSelection={true} type='' disabled={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <Field name="fatRate" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <Field name="kgFat" label=' ' component={FormikAmountComponent} decimal={3} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <Field name="fatAmount" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row className="VInputPanel">
                                        <Table.Cell style={{ width: '5%' }}>
                                            <label className="boldText">SNF</label>
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '15%' }}>
                                            <Field name="snfP" label=' ' component={FormikAmountComponent} isReadonly={netQty === "" || netQty === 0 ? true : (isCLRhas === true ? true : false)} />
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <QualitySelect name="snfQuality" isLabel={false} label=" " isSelection={true} type='' disabled={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <Field name="snfRate" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <Field name="kgSnf" label=' ' component={FormikAmountComponent} decimal={3} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <Field name="snfAmount" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row className="VInputPanel">
                                        <Table.Cell colSpan='2' style={{ width: '20%' }}>
                                            <center><Button type="button" className="CustomeBTNWithoutMargin" color='vk' disabled={netQty === "" || netQty === 0 ? true : false} onClick={() => { calculateRate(setFieldValue, values) }}>Calc Rate</Button></center>
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <label className="boldText">Milk Rate / Kg</label>
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <Field name="milkRate" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <label className="boldText">Total Rs.</label>
                                        </Table.Cell>
                                        <Table.Cell style={{ width: '20%' }}>
                                            <Field name="milkPrice" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row className="VInputPanel">
                                        <Table.Cell style={{ width: '5%' }}>
                                            <label className="boldText">Remarks</label>
                                        </Table.Cell>
                                        <Table.Cell colSpan='3' style={{ width: '55%' }}>
                                            <Field name="remarks" label=' ' component={FormikTextAreaComponent} userProps={{ maxLength: 100, displayCount: false, height: 1 }} focus={true} />
                                        </Table.Cell>
                                        <Table.Cell colSpan='2' style={{ width: '40%' }}>
                                            <div style={{float: 'right'}}>
                                                <Button type='button' onClick={handleSubmit} size="medium" color='vk' className="CustomeBTNWithoutMargin"> {index === '' ? 'Add' : 'Update'} </Button>
                                                <Button type='button' onClick={() => closeWindow()} size="medium" color='vk' className="CustomeBTNWithoutMargin"> Close </Button>
                                            </div>
                                        </Table.Cell>
                                    </Table.Row>
                                </Table.Body>
                            </Table>
                            <Modal open={modalState.isOpen} size='mini' onClose={() => setModalState({ isOpen: false, keyWord: '' })}>
                                <Modal.Header style={{ color: 'red' }}>Warning</Modal.Header>
                                <Modal.Content>
                                    <p>
                                        {modalState.keyWord === 'calculate' ? 'Please enter details and calculate milk price' : (modalState.keyWord === 'anyqty' ? 'Please enter quantity' : (modalState.keyWord === "setDD" ? 'Please select Standard and Rate Name' : (modalState.keyWord === "fatP" ? 'Please enter FAT' : (modalState.keyWord === "clrsnf" ? 'Please enter CLR or SNF' : ''))))}
                                    </p>
                                </Modal.Content>
                                <Modal.Actions>
                                    <Button onClick={() => setModalState({ isOpen: false, keyWord: '' })} positive labelPosition='right' icon='checkmark' content='Okay' />
                                </Modal.Actions>
                            </Modal>
                        </Form>
                    )}
                />
            </Modal.Content>
        </Modal>
    )
}

export default AddQuickVdetails;