import { combineReducers } from 'redux'
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr'

// Define a partys schema
const partySchema = new schema.Entity('partys',{}, { idAttribute: 'id' });
const partyListSchema = [partySchema];


const defaultPartyParams = {
  createTitle: 'Create Party',
  createSubmitButtonText: 'Create',
  editTitle: 'Update Party',
  editSubmitButtonText: 'Update',
  partyFetched: false,
  savedPartyId: 0
}


function byId(state = {}, action) {
    if (action.type === 'SAVED_PARTY') {
      const partyID = action.payload.id;
      return {...state, 
        [partyID]: action.payload
      }
    }
    if (action.type === 'FETCHED_PARTY_BY_ID') { 
      const normalizedParty = normalize(action.payload, partySchema);
      return merge({},normalizedParty.entities.partys)
    }   
    if (action.type === 'FETCHED_PARTY') {  
        const normalizedParty = normalize(action.payload, partyListSchema);
        return merge({},normalizedParty.entities.partys)
    }   
    if (action.type === 'DELETED_PARTY') {
      if(action.payload.type === "success"){
        const partyID = action.payload.id
        let finalState = {...state}
        delete finalState[partyID]
        return finalState
      }else{
        return state
      }
    } else {
      return state
    }
  }


  function byListId(state = {}, action) {
    if (action.type === 'SAVED_PARTY') {
      const partyID = action.payload.id;
      return {...state, 
        [partyID]: action.payload
      }
    }
    if (action.type === 'FETCHED_PARTY') { 
        const normalizedParty = normalize(action.payload, partyListSchema);
        return merge({},normalizedParty.entities.partys)
    }  
    if (action.type === 'DELETED_PARTY') {
      if(action.payload.type === "success"){
        const partyID = action.payload.id
        let finalState = {...state}
        delete finalState[partyID]
        return finalState
      }else{
        return state
      }
    } else {
      return state
    }
  }

  

  function notifications(state = {}, action) {
    if (action.type === 'DELETED_PARTY') { 
      return {...state, 
        [action.payload.id]: { ...state[action.payload.id],
          delete: { 
            status : action.payload.type === "success" ? 'success' : 'error',
            msg : action.payload.msg
          }
        }
      }
    } 
    if (action.type === 'SAVED_PARTY') { 
      return {...state, 
        [action.payload.id]: { ...state[action.payload.id],
          save: { 
            status : action.txn === "success" ? 'success' : 'error',
            msg : action.msg
          }
        }
      }
    } 
    if(action.type === 'NOTIFICATION_DONE_PARTY'){
      const {id, type} = action.payload
      // Remove the 'id' element from state
      const { [id]: idValue, ...restOfState } = state;
      // Remove the 'type' from the 'id' element
      const { [type]: removedValue, ...restOfId } = idValue;
      // Merge back together
      const finalState = { ...restOfState, [id]: restOfId };     
      return finalState
  
    } else {
      return state
    }
  }

  function params(state = defaultPartyParams, action) {
    if (action.type === 'FETCH_PARTY') { 
        return {...state, 
          searchParams: action.payload
        }
    } 
    if (action.type === 'SAVED_PARTY') { 
        return {...state, 
          savedPartyId: action.payload.id
        }
    } 
    if (action.type === 'NOTIFICATION_DONE_PARTY') { 
      return {...state, 
        savedPartyId: action.payload.id
      }
    } 
    if (action.type === 'FETCHED_PARTY') { 
      return {...state, 
        partyFetched: true
      }
    } 
    if (action.type === 'FETCHED_PARTY_BY_ID') { 
        return {...state,
          searchParams: {}
        }
    } 
    else {
        return state
    }
  }



const party = combineReducers({
    byId,
    byListId,
    params,
    notifications
});


export default party;
