import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { getPurchase, getPurchaseParams, getSavedPurchaseId, getNotification } from '../data/selectors'
import { createPurchase, editPurchase, setNotifyDonePurchase } from '../data/actions' 
import { Container, Header, Form, Button, Grid, Table, Icon, Modal } from 'semantic-ui-react'
import { Formik, Field, FieldArray, Form as FormikForm } from 'formik'
import { FormikInputComponent, FormikDateComponent, FormikTextAreaComponent, FormikAmountComponent } from '../../../utilities/formUtils'
import Notification from '../../../utilities/notificationUtils'
import PartySelect from '../../party/components/partySelect'
import PartyDisplayView from "../../party/components/partyDisplayView"
import AddQuickVdetails from './addQuickVdetails'
import VdetailsNonEditable from './vdetailsNonEditable'
import store from 'store2'
import AddNotesCommon from './addNotePopUpCommon'
import AddHeadLoad from './addHeadLoadPopup'
import { getMiscListByName } from '../../misc/data/selectors'
import moment from 'moment'


const PurchaseForm = (props) => {

    const purchase = useSelector(state => getPurchase(state, props))
    const savedPurchaseId = useSelector(state => getSavedPurchaseId(state, props))
    const params = useSelector(state => getPurchaseParams(state, props))
    const commisionMisc = useSelector(state => getMiscListByName(state, 'Commision'))
    const sourMilkMisc = useSelector(state => getMiscListByName(state, 'Sour Milk'))

    const dispatch = useDispatch()

    const [modalState, setModalState] = useState({isOpen: false, keyWord: ''})
    const [nextDate, setNextDate] = useState()

    const [partyId, setPartyId] = useState(0)

    useEffect(() => {
        store.remove("setIndex")
    }, [])

    const savePurchase = (values, resetForm) => {
        if(values.billNo !== undefined && values.billNo !== '' && values.billNo !== 0){
            if(values.billDate !== undefined && values.billDate !== '' && values.billDate !== 0){
                if(values.periodFromDate !== undefined && values.periodFromDate !== '' && values.periodFromDate !== 0){
                    if(values.periodToDate !== undefined && values.periodToDate !== '' && values.periodToDate !== 0){
                        let fromDt = moment(values.periodFromDate),
                            toDt = moment(values.periodToDate);
                        if(toDt >= fromDt){
                            if(values.partyM !== undefined && values.partyM !== '' && values.partyM !== 0){
                                if(Object.keys(values.purchaseVoucherDetails).length > 0){
                                    values.partyMaster = {id: values.partyM}
                                    delete values.partyM
                                    if (props.match.path === '/purchase/create') {
                                        dispatch(createPurchase(values))
                                        resetForm()
                                        setNextDate(null)
                                    }
                                    if (props.match.path === '/purchase/edit/:id') {
                                        dispatch(editPurchase(values))
                                        props.history.push(`/purchase/list`)
                                    }
                                }else{
                                    setModalState({isOpen: true, keyWord: 'Please enter purchase details row'})
                                }
                            }else{
                                setModalState({isOpen: true, keyWord: 'Please select Supplier'})
                            }
                        }else{
                            setModalState({isOpen: true, keyWord: 'Period To Date should be greater than Period From Date'})
                        }
                    }else{
                        setModalState({isOpen: true, keyWord: 'Please enter Period To Date'})
                    }
                }else{
                    setModalState({isOpen: true, keyWord: 'Please enter Period From Date'})
                }
            }else{
                setModalState({isOpen: true, keyWord: 'Please enter Bill Date'})
            }
        }else{
            setModalState({isOpen: true, keyWord: 'Please enter Bill Number'})
        }
    }

    function getVdArr (setField) {
        return <FieldArray name='purchaseVoucherDetails' component={VdetailsNonEditable} setField={setField}/>
    }

    const setItemObjectAdd = (values, value, setField) => {
        values.purchaseVoucherDetails.push(value)
        setField('purchaseVoucherDetails', values.purchaseVoucherDetails)
        callSetAllData(setField, values)
    }

    const setItemObjectEdit = (values, value, setField, index) => {
        values.purchaseVoucherDetails[index] = value
        setField('purchaseVoucherDetails', values.purchaseVoucherDetails)
        callSetAllData(setField, values)
    }

    const removeObject = (e, setField, values) => {
        if(store.get("setIndex") !== null && store.get("setIndex") !== undefined && store.get("setIndex") !== ''){
            delete values.purchaseVoucherDetails[store.get("setIndex")]
            setField('purchaseVoucherDetails', values.purchaseVoucherDetails)
            callSetAllData(setField, values)
            store.remove("setIndex")
        }
    }

    const callSetAllData = (setField, values) => {
        let totalRejectedQty = 0
        let totalSourQty = 0
        let totalKgFat = 0
        let totalKgSnf = 0
        let totalFatAmt = 0
        let totalSnfAmt = 0
        let totalMilkprice = 0
        let ratePerKg = 0
        let totalQty = 0
        let totalGoodFat = 0
        let totalGoodSnf = 0
        let totalBadFat = 0
        let totalBadSnf = 0
        let pDetails = values.purchaseVoucherDetails
        if(Object.keys(pDetails).length > 0){
            for(let i = 0; i < Object.keys(pDetails).length; i++){
                if(pDetails[i]){
                    totalRejectedQty = parseFloat(totalRejectedQty) + parseFloat(pDetails[i].rejectedQty === "" ? 0 : pDetails[i].rejectedQty)
                    totalSourQty = parseFloat(totalSourQty) + parseFloat(pDetails[i].sourQty === "" ? 0 : pDetails[i].sourQty)
                    totalKgFat = parseFloat(totalKgFat) + parseFloat(pDetails[i].kgFat === "" ? 0 : pDetails[i].kgFat)
                    totalKgSnf = parseFloat(totalKgSnf) + parseFloat(pDetails[i].kgSnf === "" ? 0 : pDetails[i].kgSnf)
                    totalFatAmt = parseFloat(totalFatAmt) + parseFloat(pDetails[i].fatAmount === "" ? 0 : pDetails[i].fatAmount)
                    totalSnfAmt = parseFloat(totalSnfAmt) + parseFloat(pDetails[i].snfAmount === "" ? 0 : pDetails[i].snfAmount)
                    totalMilkprice = parseFloat(totalMilkprice) + parseFloat(pDetails[i].milkPrice === "" ? 0 : pDetails[i].milkPrice)
                    totalQty = parseFloat(totalQty) + parseFloat(pDetails[i].qty === "" ? 0 : pDetails[i].qty)
                    totalGoodFat = parseFloat(totalGoodFat) + parseFloat(pDetails[i].fatQuality === "Good" ? pDetails[i].kgFat : 0)
                    totalGoodSnf = parseFloat(totalGoodSnf) + parseFloat(pDetails[i].snfQuality === "Good" ? pDetails[i].kgSnf : 0)
                    totalBadFat = parseFloat(totalBadFat) + parseFloat(pDetails[i].fatQuality === "Bad" ? pDetails[i].kgFat : 0)
                    totalBadSnf = parseFloat(totalBadSnf) + parseFloat(pDetails[i].snfQuality === "Bad" ? pDetails[i].kgSnf : 0)

                }
            }
            ratePerKg = parseFloat(totalMilkprice) / parseFloat(totalQty)
        }
        setField('totalRejected', totalRejectedQty)
        setField('totalSour', totalSourQty)
        setField('totalKgfat', totalKgFat)
        setField('totalKgsnf', totalKgSnf)
        setField('totalFatAmount', totalFatAmt)
        setField('totalSnfAmount', totalSnfAmt)
        setField('totalQty', totalQty)
        setField('totalMilkprice', totalMilkprice)
        setField('totalAvgRateKg', ratePerKg)
        setField('totalAvgKg', totalQty)
        setField('totalGoodFat', totalGoodFat)
        setField('totalGoodSnf', totalGoodSnf)
        setField('totalBadFat', totalBadFat)
        setField('totalBadSnf', totalBadSnf)
        values.totalRejected = totalRejectedQty
        values.totalSour = totalSourQty
        values.totalKgfat = totalKgFat
        values.totalKgsnf = totalKgSnf
        values.totalFatAmount = totalFatAmt
        values.totalSnfAmount = totalSnfAmt
        values.totalQty = totalQty
        values.totalMilkprice = totalMilkprice
        values.totalAvgRateKg = ratePerKg
        values.totalAvgKg = totalQty
        values.totalGoodFat = totalGoodFat
        values.totalGoodSnf = totalGoodSnf
        values.totalBadFat = totalBadFat
        values.totalBadSnf = totalBadSnf
        nowSetTotalValue(values, setField)
    }

    const totalAdditionAndDeduction = (val, index, setField) => {
        let purchaseV = val
        let commissionValue = purchaseV.commissionValue && purchaseV.commissionValue !== "" ? purchaseV.commissionValue : 0
        let lateArrivalValue = purchaseV.lateArrivalValue && purchaseV.lateArrivalValue !== "" ? purchaseV.lateArrivalValue : 0
        let mtevalue = purchaseV.mtevalue && purchaseV.mtevalue !== "" ? purchaseV.mtevalue : 0
        let cattleFeedValue = purchaseV.cattleFeedValue && purchaseV.cattleFeedValue !== "" ? purchaseV.cattleFeedValue : 0
        let splIncentiveValue = purchaseV.splIncentiveValue && purchaseV.splIncentiveValue !== "" ? purchaseV.splIncentiveValue : 0
        let headLoadValue = purchaseV.headLoadValue && purchaseV.headLoadValue !== "" ? purchaseV.headLoadValue : 0
        let vaccineValue = purchaseV.vaccineValue && purchaseV.vaccineValue !== "" ? purchaseV.vaccineValue : 0
        let fodderValue = purchaseV.fodderValue && purchaseV.fodderValue !== "" ? purchaseV.fodderValue : 0
        let testEquipMentValue = purchaseV.testEquipMentValue && purchaseV.testEquipMentValue !== "" ? purchaseV.testEquipMentValue : 0
        let sourMilkValue = purchaseV.sourMilkValue && purchaseV.sourMilkValue !== "" ? purchaseV.sourMilkValue : 0
        let cowLoanValue = purchaseV.cowLoanValue && purchaseV.cowLoanValue !== "" ? purchaseV.cowLoanValue : 0
        let shareValue = purchaseV.shareValue && purchaseV.shareValue !== "" ? purchaseV.shareValue : 0
        let arrearValue = purchaseV.arrearValue && purchaseV.arrearValue !== "" ? purchaseV.arrearValue : 0
        let addAdvanceValue = purchaseV.addAdvanceValue && purchaseV.addAdvanceValue !== "" ? purchaseV.addAdvanceValue : 0
        let deductAdvanceValue = purchaseV.deductAdvanceValue && purchaseV.deductAdvanceValue !== "" ? purchaseV.deductAdvanceValue : 0
        let deductOthersValue = purchaseV.deductOthersValue && purchaseV.deductOthersValue !== "" ? purchaseV.deductOthersValue : 0
        let adjustmentValue = purchaseV.adjustmentValue && purchaseV.adjustmentValue !== "" ? purchaseV.adjustmentValue : 0
        let addOthersValue = purchaseV.addOthersValue && purchaseV.addOthersValue !== "" ? purchaseV.addOthersValue : 0
        let totalAddition = parseFloat(commissionValue) + parseFloat(lateArrivalValue) + parseFloat(splIncentiveValue) + parseFloat(headLoadValue) + parseFloat(testEquipMentValue) + parseFloat(sourMilkValue) + parseFloat(arrearValue) + parseFloat(addAdvanceValue) + parseFloat(adjustmentValue) + parseFloat(addOthersValue)
        let totalDeduction = parseFloat(mtevalue) + parseFloat(cattleFeedValue) + parseFloat(vaccineValue) + parseFloat(fodderValue) + parseFloat(cowLoanValue) + parseFloat(shareValue) + parseFloat(deductAdvanceValue) + parseFloat(deductOthersValue)
        setField('totalAdditions', totalAddition)
        setField('totalDeductions', totalDeduction)
        val.totalAdditions = totalAddition
        val.totalDeductions = totalDeduction
        nowSetTotalValue(val, setField)
    }

    const additionOnChange = (val, index, setField) => {
        nowSetTotalValue(val, setField)
    }

    const nowSetTotalValue = (val, setField) => {
        let milkPrice = val.totalMilkprice !== "" ? val.totalMilkprice : 0
        let totalAdditions = val.totalAdditions !== "" ? val.totalAdditions : 0
        let totalDeductions = val.totalDeductions !== "" ? val.totalDeductions : 0
        let netAmount = ((parseFloat(milkPrice) + parseFloat(totalAdditions)) - parseFloat(totalDeductions))
        setField('netAmount', netAmount)
        let autoAdj = parseFloat(netAmount).toFixed(0)
        let getAdj = parseFloat(netAmount) - parseFloat(autoAdj)
        let billTotal = 0
        let adjustment = 0
        if(parseFloat(getAdj) > 0){
            billTotal = autoAdj
            adjustment = parseFloat(netAmount) - parseFloat(billTotal)
        }else{
            billTotal = parseFloat(autoAdj)
            adjustment = parseFloat(billTotal) - parseFloat(netAmount)
        }
        setField('billTotal', billTotal)
        setField('adjustment', adjustment)
        val.netAmount = netAmount
        val.billTotal = billTotal
        val.adjustment = adjustment
    }

    const computeCommission = (values, setField) => {
        let commisionValue = 0
        if(commisionMisc){
            let commissionVal = commisionMisc.value
            if(commisionMisc.operationOn === "QTY(KG)"){
                let qty = values.totalQty === "" ? 0 : values.totalQty
                commisionValue = parseFloat(qty) * parseFloat(commissionVal)
            }else if(commisionMisc.operationOn === "Milk Price"){
                let milkP = values.totalMilkprice === "" ? 0 : values.totalMilkprice
                commisionValue = parseFloat(milkP) * parseFloat(commissionVal)
            }else if(commisionMisc.operationOn === "FAT/SNF(KG)"){
                let kgFatSNF = parseFloat(values.totalKgfat) + parseFloat(values.totalKgsnf)
                commisionValue = parseFloat(kgFatSNF) * parseFloat(commissionVal)
            } 
        }
        setField('commissionValue', commisionValue)
        values.commissionValue = commisionValue
        totalAdditionAndDeduction(values, 0, setField)
    }

    
    const computeHeadLoad = (values, setField) => {
        console.log("values...................", values)
        let qty = values.totalQty === "" ? 0 : values.totalQty
        let headLoadValue = parseFloat(qty) * parseFloat(values.headLoadCalVal !== null && values.headLoadCalVal !== "" ? values.headLoadCalVal : 0)
        setField('headLoadValue', headLoadValue)
        values.headLoadValue = headLoadValue
        totalAdditionAndDeduction(values, 0, setField)
    }

    
    const computeSourMilk = (values, setField) => {
        let sourMilkValue = 0
        if(sourMilkMisc){
            let sourMilkVal = sourMilkMisc.value
            let qty = values.totalSour === "" ? 0 : values.totalSour
            sourMilkValue = parseFloat(qty) * parseFloat(sourMilkVal)
        }
        setField('sourMilkValue', sourMilkValue)
        values.sourMilkValue = sourMilkValue
        totalAdditionAndDeduction(values, 0, setField)
    }
    return (
        <Container>
            <Grid columns='equal' className="borderButtomTable">
                <Grid.Row>
                    <Grid.Column width={4}>
                        <Header as='h1' className="VerticalAlign" align='left'>{params.title}</Header>
                    </Grid.Column>
                    <Grid.Column width={6} floated={'right'} align='right'></Grid.Column>
                </Grid.Row>
            </Grid>
            <Container className='txnContainer'>
                <Formik id="purchaseForm" size="large" width={5}
                    initialValues={purchase}
                    onSubmit={(values, { resetForm }) => savePurchase(values, resetForm)}
                    render={({ handleSubmit, errors, onChange, values, handleChange, setFieldValue }) => (
                        <Form as={FormikForm} size="small" className="CustomeFormPurchase" width={5} onSubmit={handleSubmit}>
                            <Table style={{width: '100%'}} className='purchasetable' basic='very'>
                                <Table.Body>
                                    <Table.Row>
                                        <Table.Cell style={{width: '5%'}}><label className="boldText">Bill Id </label></Table.Cell>
                                        <Table.Cell style={{width: '10%'}}>
                                            <label className='boldLabel'>Auto</label>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '5%'}}><label className="boldText">Bill No </label></Table.Cell>
                                        <Table.Cell style={{width: '15%'}}>
                                            <Form.Group widths={2}>
                                                <Field name="billNo" label=' ' component={FormikInputComponent} />
                                            </Form.Group>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '5%'}}><label className="boldText">Date </label></Table.Cell>
                                        <Table.Cell style={{width: '15%'}}>
                                            <Form.Group widths={2}>
                                                <Field name="billDate" label=' ' component={FormikDateComponent} />
                                            </Form.Group>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '5%'}}><label className="boldText">Period </label></Table.Cell>
                                        <Table.Cell style={{width: '15%'}}>
                                            <Form.Group widths={2}>
                                                <Field name="periodFromDate" label=' ' component={FormikDateComponent} />
                                            </Form.Group>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '5%'}}><center><label className="boldText">to</label></center></Table.Cell>
                                        <Table.Cell style={{width: '15%'}}>
                                            <Form.Group widths={2}>
                                                <Field name="periodToDate" label=' ' component={FormikDateComponent} />
                                            </Form.Group>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '5%'}}></Table.Cell>
                                    </Table.Row>
                                </Table.Body>
                            </Table>
                            <Table style={{width: '100%'}} className='purchasetable' basic='very'>
                                <Table.Body>
                                    <Table.Row>
                                        <Table.Cell style={{width: '10%'}}><label className="boldText">Supplier </label></Table.Cell>
                                        <Table.Cell style={{width: '21%'}}>
                                            <PartySelect name="partyM" isLabel={false} label=" " isSelection={true} setState={setPartyId} userProps={{setField: setFieldValue, setName: 'partyM'}} type=''/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '5%'}}>
                                            {partyId !== 0 ? 
                                            <PartyDisplayView partyId={partyId}/>
                                            :
                                            <Button type='button' icon size='small' color='vk' title='Add Bank' style={{visibility: 'hidden'}}><Icon name='eye'/></Button>
                                            }
                                        </Table.Cell>
                                        <Table.Cell style={{width: '6%'}}>
                                            <label className="boldText">Rejected</label>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '14%'}}>
                                            <Field name="totalRejected" label=' ' component={FormikAmountComponent} isReadonly={true}/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '6%'}}>
                                            <label className="boldText">Sour</label>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '14%'}}>
                                            <Field name="totalSour" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{width: '8%'}}>
                                            <AddQuickVdetails setItemObject={setItemObjectAdd} index={''}  setField={setFieldValue} values={values} setNextDate={setNextDate} nextDate={nextDate ? nextDate : values.periodFromDate}/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '8%'}}>
                                            <AddQuickVdetails setItemObject={setItemObjectEdit} index={'y'} voucherObj={values} setField={setFieldValue} values={values}/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '8%'}}>
                                            <Button type="button" className="CustomeBTNWithoutMargin" color='google plus' style={{float: 'right'}} onClick={(e) => removeObject(e, setFieldValue, values)}>Delete</Button>
                                        </Table.Cell>
                                    </Table.Row>
                                </Table.Body>
                            </Table>
                            <Table style={{width: '100%'}} className='purchasetable'>
                                <Table.Body>
                                    <Table.Row>
                                        <Table.Cell style={{width: '100%', padding: '0px'}}>
                                            <div className='outScrollX' style={{width: '100%', height: '150px'}}>
                                                <div className='outScrollY' style={{width: '100%', height: '150px'}}>                                                    
                                                    <Form.Group>
                                                        {getVdArr()}
                                                    </Form.Group>
                                                </div>
                                            </div>
                                        </Table.Cell>
                                    </Table.Row>
                                </Table.Body>
                            </Table>
                            <Table style={{width: '100%'}} className='purchasetable' fixed>
                                <Table.Body>
                                    <Table.Row>
                                        <Table.Cell style={{width: '7%'}}>
                                            <label className="boldText">Fat(Kg)</label>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '8%'}}>
                                            <Field name="totalKgfat" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <label className="boldText">SNF(Kg)</label>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '8%'}}>
                                            <Field name="totalKgsnf" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <label className="boldText">Fat(Rs.)</label>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '8%'}}>
                                            <Field name="totalFatAmount" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <label className="boldText">SNF(Rs.)</label>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '8%'}}>
                                            <Field name="totalSnfAmount" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <label className="boldText">QTY(KG)</label>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '8%'}}>
                                            <Field name="totalQty" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{width: '10%'}}>
                                            <label className="boldText">Milk Price</label>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '15%'}}>
                                            <Field name="totalMilkprice" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                    </Table.Row>
                                </Table.Body>
                            </Table>
                            <Table style={{width: '100%'}} fixed>
                                <Table.Body>
                                    <Table.Row>
                                        <Table.Cell colSpan='6' style={{width: '50%'}} className='rightBorder'>
                                            <center><label className="boldText">Addition</label></center>
                                        </Table.Cell>
                                        <Table.Cell colSpan='6' style={{width: '50%'}}>
                                            <center><label className="boldText">Deduction</label></center>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{width: '14%'}}>
                                            Commision
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <Field name="commissionValue" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell textAlign='right' style={{width: '4%'}}>
                                            <Button type="button" icon className='iconButtonCustom' color='vk' onClick={() => computeCommission(values, setFieldValue)}>
                                                <Icon name='question'/>
                                            </Button>&nbsp;
                                            <AddNotesCommon name='commissionNarration' setField={setFieldValue} setValueFor={values.commissionNarration === "" ? (commisionMisc ? commisionMisc.description+" "+'' : '') : values.commissionNarration}/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '14%'}}>
                                            Late Arrival
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <Field name="lateArrivalValue" label=' ' component={FormikAmountComponent} compute={totalAdditionAndDeduction}/>
                                        </Table.Cell>                                        
                                        <Table.Cell textAlign='right' style={{width: '4%'}} className='rightBorder'>
                                            <AddNotesCommon name='lateArrivalNarration' setField={setFieldValue} setValueFor={values.lateArrivalNarration}/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '14%'}}>
                                            M.T.E
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <Field name="mtevalue" label=' ' component={FormikAmountComponent} compute={totalAdditionAndDeduction} />
                                        </Table.Cell>                                        
                                        <Table.Cell textAlign='right' style={{width: '4%'}}>
                                            <AddNotesCommon name='mtenarration' setField={setFieldValue} setValueFor={values.mtenarration}/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '14%'}}>
                                            Cattle Feed
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <Field name="cattleFeedValue" label=' ' component={FormikAmountComponent} compute={totalAdditionAndDeduction} />
                                        </Table.Cell>
                                        <Table.Cell textAlign='right' style={{width: '4%'}}>
                                            <AddNotesCommon name='cattleFeedNarration' setField={setFieldValue} setValueFor={values.cattleFeedNarration}/>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{width: '14%'}}>
                                            S.Incentive
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <Field name="splIncentiveValue" label=' ' component={FormikAmountComponent} compute={totalAdditionAndDeduction} />
                                        </Table.Cell>
                                        <Table.Cell textAlign='right' style={{width: '4%'}}>
                                            <AddNotesCommon name='splIncentiveNarration' setField={setFieldValue} setValueFor={values.splIncentiveNarration}/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '14%'}}>
                                            Head Load
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <Field name="headLoadValue" label=' ' component={FormikAmountComponent} isReadonly={true}/>
                                        </Table.Cell>                                        
                                        <Table.Cell textAlign='right' style={{width: '4%'}} className='rightBorder'>
                                            <AddHeadLoad name='headLoadCalVal' setField={setFieldValue} setValueFor={values.headLoadCalVal} afterWork={computeHeadLoad} sendValue={values}/>
                                            &nbsp;
                                            <AddNotesCommon name='headLoadNarration' setField={setFieldValue} setValueFor={values.headLoadNarration}/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '14%'}}>
                                            Viccine
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <Field name="vaccineValue" label=' ' component={FormikAmountComponent} compute={totalAdditionAndDeduction} />
                                        </Table.Cell>                                        
                                        <Table.Cell textAlign='right' style={{width: '4%'}}>
                                            <AddNotesCommon name='vaccineNarration' setField={setFieldValue} setValueFor={values.vaccineNarration}/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '14%'}}>
                                            Fodder Seed
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <Field name="fodderValue" label=' ' component={FormikAmountComponent} compute={totalAdditionAndDeduction} />
                                        </Table.Cell>
                                        <Table.Cell textAlign='right' style={{width: '4%'}}>
                                            <AddNotesCommon name='fodderNarration' setField={setFieldValue} setValueFor={values.fodderNarration}/>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{width: '14%'}}>
                                            Testing Eqp
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <Field name="testEquipMentValue" label=' ' component={FormikAmountComponent} compute={totalAdditionAndDeduction} />
                                        </Table.Cell>
                                        <Table.Cell textAlign='right' style={{width: '4%'}}>
                                            <AddNotesCommon name='testEquipMentNarration' setField={setFieldValue} setValueFor={values.testEquipMentNarration}/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '14%'}}>
                                            Sour Milk
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <Field name="sourMilkValue" label=' ' component={FormikAmountComponent} isReadonly={true}/>
                                        </Table.Cell>                                        
                                        <Table.Cell textAlign='right' style={{width: '4%'}} className='rightBorder'>
                                            <Button type="button" icon className='iconButtonCustom' color='vk' onClick={() => computeSourMilk(values, setFieldValue)}>
                                                <Icon name='question'/>
                                            </Button>&nbsp;
                                            <AddNotesCommon name='sourMilkNarration' setField={setFieldValue} setValueFor={values.sourMilkNarration === "" ? (sourMilkMisc ? sourMilkMisc.description+" "+values.totalSour : values.totalSour) : values.sourMilkNarration}/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '14%'}}>
                                            Cow Loan
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <Field name="cowLoanValue" label=' ' component={FormikAmountComponent} compute={totalAdditionAndDeduction} />
                                        </Table.Cell>                                        
                                        <Table.Cell textAlign='right' style={{width: '4%'}}>
                                            <AddNotesCommon name='cowLoanNarration' setField={setFieldValue} setValueFor={values.cowLoanNarration}/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '14%'}}>
                                            Share
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <Field name="shareValue" label=' ' component={FormikAmountComponent} compute={totalAdditionAndDeduction} />
                                        </Table.Cell>
                                        <Table.Cell textAlign='right' style={{width: '4%'}}>
                                            <AddNotesCommon name='shareNarration' setField={setFieldValue} setValueFor={values.shareNarration}/>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{width: '14%'}}>
                                            Arrear
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <Field name="arrearValue" label=' ' component={FormikAmountComponent} compute={totalAdditionAndDeduction} />
                                        </Table.Cell>
                                        <Table.Cell textAlign='right' style={{width: '4%'}}>
                                            <AddNotesCommon name='arrearNarration' setField={setFieldValue} setValueFor={values.arrearNarration}/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '14%'}}>
                                            Advance
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <Field name="addAdvanceValue" label=' ' component={FormikAmountComponent} compute={totalAdditionAndDeduction} />
                                        </Table.Cell>                                        
                                        <Table.Cell textAlign='right' style={{width: '4%'}} className='rightBorder'>
                                            <AddNotesCommon name='addAdvanceNarration' setField={setFieldValue} setValueFor={values.addAdvanceNarration}/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '14%'}}>
                                            Advance
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <Field name="deductAdvanceValue" label=' ' component={FormikAmountComponent} compute={totalAdditionAndDeduction} />
                                        </Table.Cell>                                        
                                        <Table.Cell textAlign='right' style={{width: '4%'}}>
                                            <AddNotesCommon name='deductAdvanceNarration' setField={setFieldValue} setValueFor={values.deductAdvanceNarration}/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '14%'}}>
                                            Other
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <Field name="deductOthersValue" label=' ' component={FormikAmountComponent} compute={totalAdditionAndDeduction} />
                                        </Table.Cell>
                                        <Table.Cell textAlign='right' style={{width: '4%'}}>
                                            <AddNotesCommon name='deductOthersNarration' setField={setFieldValue} setValueFor={values.deductOthersNarration}/>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{width: '14%'}}>
                                            Adjustment
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <Field name="adjustmentValue" label=' ' component={FormikAmountComponent} compute={totalAdditionAndDeduction} />
                                        </Table.Cell>
                                        <Table.Cell textAlign='right' style={{width: '4%'}}>
                                            <AddNotesCommon name='adjustmentNarration' setField={setFieldValue} setValueFor={values.adjustmentNarration}/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '14%'}}>
                                            Other
                                        </Table.Cell>
                                        <Table.Cell style={{width: '7%'}}>
                                            <Field name="addOthersValue" label=' ' component={FormikAmountComponent} compute={totalAdditionAndDeduction} />
                                        </Table.Cell>                                        
                                        <Table.Cell textAlign='right' style={{width: '4%'}} className='rightBorder'>
                                            <AddNotesCommon name='addOthersNarration' setField={setFieldValue} setValueFor={values.addOthersNarration}/>
                                        </Table.Cell>
                                        <Table.Cell colSpan='6' style={{width: '50%'}}></Table.Cell>
                                    </Table.Row>
                                </Table.Body>
                            </Table>                            
                            <Table style={{width: '100%'}} fixed>
                                <Table.Body>
                                    <Table.Row>
                                        <Table.Cell style={{width: '6%'}}>
                                            Rate/Kg
                                        </Table.Cell>
                                        <Table.Cell style={{width: '15%'}}>
                                            <Field name="totalAvgRateKg" label=' ' component={FormikAmountComponent} isReadonly={true}/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '5%'}}>
                                            Good
                                        </Table.Cell>
                                        <Table.Cell style={{width: '12%'}}>
                                            <Field name="totalGoodFat" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{width: '12%'}}>
                                            <Field name="totalGoodSnf" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{width: '10%'}}>
                                            Additions
                                        </Table.Cell>
                                        <Table.Cell style={{width: '15%'}}>
                                            <Field name="totalAdditions" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{width: '10%'}}>
                                            Net Amount
                                        </Table.Cell>
                                        <Table.Cell style={{width: '15%'}}>
                                            <Field name="netAmount" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{width: '6%'}}>
                                            <label className="boldText">Avg(KG)</label>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '15%'}}>
                                            <Field name="totalAvgKg" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{width: '5%'}}>
                                            Bad
                                        </Table.Cell>
                                        <Table.Cell style={{width: '12%'}}>
                                            <Field name="totalBadFat" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{width: '12%'}}>
                                            <Field name="totalBadSnf" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{width: '10%'}}>
                                            Deductions
                                        </Table.Cell>
                                        <Table.Cell style={{width: '15%'}}>
                                            <Field name="totalDeductions" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                        <Table.Cell style={{width: '10%'}}>
                                            Adjustments
                                        </Table.Cell>
                                        <Table.Cell style={{width: '15%'}}>
                                            <Field name="adjustment" label=' ' component={FormikAmountComponent} compute={additionOnChange} isReadonly={true}/>
                                        </Table.Cell>
                                    </Table.Row>
                                    <Table.Row>
                                        <Table.Cell style={{width: '6%'}}>
                                            Notes
                                        </Table.Cell>
                                        <Table.Cell colSpan='6' style={{width: '69%'}}>
                                            <Field name="notes" label=' ' component={FormikTextAreaComponent} userProps={{maxLength: 300, displayCount: false, height: 1}} focus={true}/>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '10%'}}>
                                            <label className="boldText">Bill Total</label>
                                        </Table.Cell>
                                        <Table.Cell style={{width: '15%'}}>
                                            <Field name="billTotal" label=' ' component={FormikAmountComponent} isReadonly={true} />
                                        </Table.Cell>
                                    </Table.Row>
                                </Table.Body>
                            </Table>
                            <div className="stickeyDiv">
                                <Table className="purchasetable" style={{ width: '100%' }} fixed>
                                    <Table.Body>
                                        <Table.Row>
                                            <Table.Cell>
                                                <Button type="submit" icon labelPosition='right' className="CustomeBTNWithoutMargin" color='vk' style={{float: 'right'}}>
                                                    {params.submitButtonText}
                                                    <Icon name={props.match.path === '/purchase/create' ? 'add circle' : 'arrow alternate circle up outline'} />
                                                </Button>
                                            </Table.Cell>
                                        </Table.Row>
                                    </Table.Body>
                                </Table>
                            </div>
                            {console.log("text----------------"+params.submitButtonText)}
                            {savedPurchaseId && savedPurchaseId !== 0 ? 
                            <Notification id={savedPurchaseId} notifySelector={getNotification} notifyDoneAction={setNotifyDonePurchase} type='save'/>
                            :
                            null}
                            <Modal open={modalState.isOpen} size='mini' onClose={() => setModalState({isOpen: false, keyWord: ''})}>
                                <Modal.Header style={{color: 'red'}}>Warning</Modal.Header>
                                <Modal.Content>
                                    <p>
                                        {modalState.keyWord}
                                    </p>
                                </Modal.Content>
                                <Modal.Actions>
                                    <Button onClick={() => setModalState({isOpen: false, keyWord: ''})} positive labelPosition='right' icon='checkmark' content='Okay'/>
                                </Modal.Actions>
                            </Modal>
                        </Form>
                    )}
                />
            </Container>
        </Container>
    )
}

export default PurchaseForm;