import { call, takeEvery, all, put } from 'redux-saga/effects'
import { savedStandard, fetchedStandard, fetchedStandardById } from './actions'
import axios from 'axios';
import { handlError, parseError } from '../../app/serverError'
import URL from '../../../store/path'

//#region Saga for Create Standard 

function* createStandard() {
    yield takeEvery('CREATE_STANDARD',saveStandard)
  }

function* saveStandard(action){
  const { response, error } = yield call(saveStandardAPI, action.payload)
  if (response) yield put (savedStandard({ data: response.data, msg: response.msg, type: response.type }))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function saveStandardAPI(data) {
    return axios.post(URL+'/standard/add', data, {crossDomain: true})
      .then(response => ({ response }))
      .catch(error => ({ error }))
  }

//#endregion


//#region Saga for Edited Standard 

function* editStandard() {
  yield takeEvery('EDIT_STANDARD',saveEditedStandard)
}

function* saveEditedStandard(action){
  const { response, error } = yield call(editStandardAPI, action.payload)  
  if (response) yield put (savedStandard({ data: response.data, msg: response.msg, type: response.type }))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function editStandardAPI(data) {
  return axios.post(URL+'/stdmilkrate/update', data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for Getting Standard by ID

function* fetchStandardById() {
  yield takeEvery('FETCH_STANDARD_BY_ID',saveFetchedStandardById)
}

function* saveFetchedStandardById(action){
  const { response, error } = yield call(fetchStandardByIdAPI, action.payload)  
  if (response) yield put (fetchedStandardById(response.data))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function fetchStandardByIdAPI(data) {
  let _id = data
  return axios.post(URL+'/standard/'+ _id, data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion

//#region Saga for List Tasks 

function* fetchStandard() {

  yield takeEvery('FETCH_STANDARD',requestStandard)

}
  
function* requestStandard(action){

  let { data } = yield call(requestStandardAPI, action.payload)
  yield put (fetchedStandard(data.data))

}

function requestStandardAPI(data) {
  return axios.get(URL+'/stdmilkrate/fetchbypagename/'+ data, data, {crossDomain: true})
}

//#endregion

const sagaErrorMessage = (error, action) => {
    console.group("Saga Error:"+action.type)
    console.log(error)
    console.groupEnd()
}

export default function* standardSaga() {
    yield all([
        createStandard(),
        fetchStandard(),
        editStandard(),
        fetchStandardById()
    ])
  }  