import { call, takeEvery, all, put } from 'redux-saga/effects'
import { savedPurchase, fetchedPurchase, fetchedPurchaseById, deletedPurchase, updatedPurchase } from './actions'
import axios from 'axios';
import { handlError, parseError } from '../../app/serverError'
import URL from '../../../store/path'

//#region Saga for Create Purchase 

function* createPurchase() {
  yield takeEvery('CREATE_PURCHASE', savePurchase)
}

function* savePurchase(action) {
  const { response, error } = yield call(savePurchaseAPI, action.payload)
  if (response) yield put(savedPurchase({ data: response.data, msg: response.msg, type: response.type }))
  else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function savePurchaseAPI(data) {
  console.log("--------call saving time----------")
  return axios.post(URL + '/purchasetxn/create', data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for Edited Purchase 

function* editPurchase() {
  yield takeEvery('EDIT_PURCHASE', saveEditedPurchase)
}

function* saveEditedPurchase(action) {
  const { response, error } = yield call(editPurchaseAPI, action.payload)
  console.log(response)
  if (response) yield put(updatedPurchase({ data: response.data, msg: response.msg, type: response.type, removeid: action.payload.id }))
  else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function editPurchaseAPI(data) {
  return axios.post(URL + '/purchasetxn/update', data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for Getting Purchase by ID

function* fetchPurchaseById() {
  yield takeEvery('FETCH_PURCHASE_BY_ID', saveFetchedPurchaseById)
}

function* saveFetchedPurchaseById(action) {
  const { response, error } = yield call(fetchPurchaseByIdAPI, action.payload)
  if (response) yield put(fetchedPurchaseById(response.data))
  else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function fetchPurchaseByIdAPI(data) {
  let id = data
  return axios.get(URL + '/purchasetxn/fetch/' + id, data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion

//#region Saga for List Tasks 

function* fetchPurchase() {

  yield takeEvery('FETCH_PURCHASE', requestPurchase)

}

function* requestPurchase(action) {
  let { data } = yield call(requestPurchaseAPI, action.payload)
  yield put(fetchedPurchase(data.data))
}

function requestPurchaseAPI(data) {
  return axios.get(URL + '/purchasetxn/fetch/'+data, { crossDomain: true })
}

//#endregion

//#region Saga for Delete Purchase 

function* deletePurchase() {
  yield takeEvery('DELETE_PURCHASE', saveDeletePurchase)
}

function* saveDeletePurchase(action) {
  const { response, error } = yield call(deletePurchaseAPI, action.payload)
  if (response) yield put(deletedPurchase({ id: action.payload.id, msg: response.data.msg, type: response.data.type }))
  else {
    yield put(handlError(action, parseError(error)))
    sagaErrorMessage(error, action)
  }
}

function deletePurchaseAPI(data) {
  return axios.post(URL + '/purchasetxn/delete', data, { crossDomain: true })
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion


//#region Saga for Post Purchase Print

function* postPurchasePrint() {
  yield takeEvery('PRINT_PURCHASE', sendPurchasePrint)
}

function* sendPurchasePrint(action) {
  yield call(sendPurchasePrintAPI, action.payload)
}

function sendPurchasePrintAPI(data) {
  window.open(URL+'/invoice/print/'+data, '_blank')
}

//#endregion


const sagaErrorMessage = (error, action) => {
  console.group("Saga Error:" + action.type)
  console.log(error)
  console.groupEnd()
}

export default function* purchaseSaga() {
  yield all([
    createPurchase(),
    fetchPurchase(),
    editPurchase(),
    fetchPurchaseById(),
    deletePurchase(),
    postPurchasePrint()
  ])
}  