import * as type from './types'

export function createBank(bank) {
    console.log("----------------------call create bank")
    return {
        type: type.CREATE_BANK,
        payload: bank,
        txn: 'initiated',
    };
}

export function savedBank(bank) {
    console.log("----------------------call saved bank")
    return {
        type: type.SAVED_BANK,
        payload: bank.data.data,
        txn: bank.data.type,
        msg: bank.data.msg,
        diff: true
    };
}

export function editBank(bank) {
    return {
        type: type.EDIT_BANK,
        payload: bank,
        txn: 'initiated',
    };
}

export function fetchBank() {
    return {
        type: type.FETCH_BANK
    };
}

export function fetchedBank(bank) {
    return {
        type: type.FETCHED_BANK,
        payload: bank
    };
}


export function fetchBankById(bank) {
    return {
        type: type.FETCH_BANK_BY_ID,
        payload: bank
    };
}

export function fetchedBankById(bank) {
    return {
        type: type.FETCHED_BANK_BY_ID,
        payload: bank
    };
}


export function setNotifyDoneBank(props) {
    return {
        type: type.NOTIFICATION_DONE_BANK,
        payload: props
    };
}


export function deleteBank(bank) {
    return {
        type: type.DELETE_BANK,
        payload: bank
    };
}

export function deletedBank(bank) {
    return {
        type: type.DELETED_BANK,
        payload: bank
    };
}