import { createSelector } from 'reselect'
import { startsWith, cloneDeep } from 'lodash'
import { group } from './model'

export const getIsFetchingGroup = (state, props) => state.group.params.isFetching
export const getIsGroupFetched = (state, props) => state.group.params.groupFetched
export const getSavedGroupId = (state, props) => state.group.params.savedGroupId
export const getGroupList = (state, prop) => state.group.byId
export const getGroupById = (state, id) => state.group.byId[id]
export const getNotification = (state, id) => state.group.notifications[id]

export const getGroup = (state, props) => {
    if (props.match.path === '/group/create') {
        return group()
    }
    if (props.match.path === '/group/edit/:id') {
        let _id = props.match.params.id
        let obj = cloneDeep(state.group.byId[_id]);
        return obj
    }
}
export const getGroupParams = (state, props) => {
    const params = {}
    if (props.match.path === '/group/create') {
        params.title = state.group.params.createTitle
        params.submitButtonText = state.group.params.createSubmitButtonText
    } else if (props.match.path === '/group/edit/:id') {
        params.title = state.group.params.editTitle
        params.submitButtonText = state.group.params.editSubmitButtonText
    }
    return params
}

export const selectGroup = createSelector(
    getGroupList,
    group => {
        const keys = Object.keys(group)
        const obj = keys.map((key) => { return { key: key, value: parseInt(key, 10), text: group[key].groupName } })
        return obj
    }
)