import { combineReducers } from 'redux'
import { merge } from 'lodash'
import { normalize, schema } from 'normalizr'

// Define a groups schema
const groupSchema = new schema.Entity('groups',{}, { idAttribute: 'id' });
const groupListSchema = [groupSchema];


const defaultGroupParams = {
  createTitle: 'Create Group',
  createSubmitButtonText: 'Create',
  editTitle: 'Update Group',
  editSubmitButtonText: 'Update',
  groupFetched: false,
  savedGroupId: 0
}


function byId(state = {}, action) {
    if (action.type === 'SAVED_GROUP') {
      const groupID = action.payload.id;
      return {...state, 
        [groupID]: action.payload
      }
    }
    if (action.type === 'FETCHED_GROUP') { 
      const normalizedGroup = normalize(action.payload, groupListSchema);
      return merge({},normalizedGroup.entities.groups)
    } 
    if (action.type === 'FETCHED_GROUP_BY_ID') { 
      const normalizedGroup = normalize(action.payload, groupSchema);
      return merge({},normalizedGroup.entities.groups)
    }
    if (action.type === 'DELETED_GROUP') {
      if(action.payload.type === "success"){
        const groupID = action.payload.id
        let finalState = {...state}
        delete finalState[groupID]
        return finalState
      }else{
        return state
      }
    } else {
      return state
    }
  }


  function byListId(state = {}, action) {
    if (action.type === 'SAVED_GROUP') {
      const groupID = action.payload.id
      return {...state, 
        [groupID]: action.payload
      }
    }
    if (action.type === 'FETCHED_GROUP') { 
        const normalizedGroup = normalize(action.payload, groupListSchema);
        return merge({},normalizedGroup.entities.groups)
    } 
    if (action.type === 'DELETED_GROUP') {
      if(action.payload.type === "success"){
        const groupID = action.payload.id
        let finalState = {...state}
        delete finalState[groupID]
        return finalState
      }else{
        return state
      }
    } else {
      return state
    }
  }

  

  function notifications(state = {}, action) {
    if (action.type === 'DELETED_GROUP') { 
      return {...state, 
        [action.payload.id]: { ...state[action.payload.id],
          delete: { 
            status : action.payload.type === "success" ? 'success' : 'error',
            msg : action.payload.msg
          }
        }
      }
    } 
    if (action.type === 'SAVED_GROUP') { 
      return {...state, 
        [action.payload.id]: { ...state[action.payload.id],
          save: { 
            status : action.txn === "success" ? 'success' : 'error',
            msg : action.msg
          }
        }
      }
    } 
    if(action.type === 'NOTIFICATION_DONE_GROUP'){
      const {id, type} = action.payload
      // Remove the 'id' element from state
      const { [id]: idValue, ...restOfState } = state;
      // Remove the 'type' from the 'id' element
      const { [type]: removedValue, ...restOfId } = idValue;
      // Merge back together
      const finalState = { ...restOfState, [id]: restOfId };     
      return finalState
  
    } else {
      return state
    }
  }

  function params(state = defaultGroupParams, action) {
    if (action.type === 'FETCH_GROUP') { 
        return {...state, 
          searchParams: action.payload
        }
    } 
    if (action.type === 'SAVED_GROUP') { 
        return {...state, 
          savedGroupId: action.payload.id
        }
    } 
    if (action.type === 'NOTIFICATION_DONE_GROUP') { 
      return {...state, 
        savedGroupId: action.payload.id
      }
    } 
    if (action.type === 'FETCHED_GROUP') { 
      return {...state, 
        groupFetched: true
      }
    } 
    if (action.type === 'FETCHED_GROUP_BY_ID') { 
        return {...state,
          searchParams: {}
        }
    } 
    else {
        return state
    }
  }



const group = combineReducers({
    byId,
    byListId,
    params,
    notifications
});


export default group;
