import React from 'react'
import { Field } from 'formik'
import { FormikSelectComponent } from '../../../utilities/formUtils'
    

const TypeTxnSelect = ({ name, isTxn, isLabel, label, isSelection, setField }, props) => {

    const opt = [{
        key : "Flush",
        value : "Flush",
        text : "Flush"
    },{
        key : "Lean",
        value : "Lean",
        text : "Lean"
    }]

    const getSelectedValue = (values) => {
        setField(name, values)
    }


    return (
        <Field name={name} isLabel={isLabel} isTxn={isTxn} label={label} isSelection={isSelection} component={FormikSelectComponent} userProps={{ options: opt, getValue: getSelectedValue }}> </Field>
    )

}


export default TypeTxnSelect;
