import * as type from './types'

export function createStandard(standard) {
    return {
        type : type.CREATE_STANDARD,
        payload : standard,
        txn : 'initiated',
    };
}

export function savedStandard(standard) {
    return {
        type : type.SAVED_STANDARD,
        payload : standard.data.data,
        txn: standard.data.type,
        msg: standard.data.msg,
        diff : true
    };
}

export function editStandard(standard) {
    return {
        type : type.EDIT_STANDARD,
        payload : standard,
        txn : 'initiated',
    };
}

export function fetchStandard(params) {
    return {
        type : type.FETCH_STANDARD,
        payload : params
    };
}

export function fetchedStandard(standard) {
    return {
        type : type.FETCHED_STANDARD,
        payload : standard
    };
}


export function fetchStandardById(standard) {
    return {
        type : type.FETCH_STANDARD_BY_ID,
        payload : standard
    };
}

export function fetchedStandardById(standard) {
    return {
        type : type.FETCHED_STANDARD_BY_ID,
        payload : standard
    };
}

export function setNotifyDoneStandard(props) {
    return {
        type : type.NOTIFICATION_DONE_STANDARD,
        payload: props
    };
}