import { call, takeEvery, all, put, select } from 'redux-saga/effects'
import { savedMisc, fetchedMisc, fetchedMiscById } from './actions'
import axios from 'axios';
import { handlError, parseError } from '../../app/serverError'
import URL from '../../../store/path'

//#region Saga for Edited Misc 

function* editMisc() {
  yield takeEvery('EDIT_MISC',saveEditedMisc)
}

function* saveEditedMisc(action){
  const { response, error } = yield call(editMiscAPI, action.payload)  
  if (response) yield put (savedMisc({ data: response.data, msg: response.msg, type: response.type }))
  else {
    yield put (handlError(action, parseError(error)))
    sagaErrorMessage(error,action)  
  }
}

function editMiscAPI(data) {
  return axios.post(URL+'/miscsettings/update', data, {crossDomain: true})
    .then(response => ({ response }))
    .catch(error => ({ error }))
}

//#endregion

//#region Saga for List Tasks 

function* fetchMisc() {

  yield takeEvery('FETCH_MISC',requestMisc)

}
  
function* requestMisc(action){

  let { data } = yield call(requestMiscAPI, action.payload)
  yield put (fetchedMisc(data.data))

}

function requestMiscAPI(data) {
  return axios.get(URL+'/miscsettings/fetch/all', data, {crossDomain: true})
}

//#endregion

const sagaErrorMessage = (error, action) => {
    console.group("Saga Error:"+action.type)
    console.log(error)
    console.groupEnd()
}

export default function* miscSaga() {
    yield all([
        fetchMisc(),
        editMisc()
    ])
  }  