import { all } from 'redux-saga/effects'
import partySaga from '../modules/party/data/sagas'
import bankSaga from '../modules/bank/data/sagas'
import groupSaga from '../modules/group/data/sagas'
import finYearSaga from '../modules/finyear/data/sagas'
import standardSaga from '../modules/standard/data/sagas'
import milkRatesSaga from '../modules/milkRates/data/sagas'
import purchaseSaga from '../modules/purchase/data/sagas'
import miscSaga from '../modules/misc/data/sagas'
import registerSaga from '../modules/register/data/sagas'
import combinationSaga from '../modules/combination/data/sagas'

export default function* rootSaga() {
    yield all([
      finYearSaga(),
      partySaga(),
      bankSaga(),
      groupSaga(),
      standardSaga(),
      milkRatesSaga(),
      purchaseSaga(),
      miscSaga(),
      registerSaga(),
      combinationSaga()
    ])
  } 