import { createSelector } from 'reselect'
import { cloneDeep } from 'lodash'
import { purchase, purchaseDetails } from './model'
import store from 'store2'

export const getIsFetchingPurchase = (state, props) => state.purchase.params.isFetching
export const getIsPurchaseFetched = (state, props) => state.purchase.params.purchaseFetched
export const getSavedPurchaseId = (state, props) => state.purchase.params.savedPurchaseId
export const getPurchaseList = (state, prop) => state.purchase.byListId
export const getPurchaseById = (state, id) => state.purchase.byListId[id]
export const getNotification = (state, id) => state.purchase.notifications[id]

export const getPurchase = (state, props) => {
    if(props.match.path==='/purchase/create'){
        return purchase()
    }
    if(props.match.path==='/purchase/edit/:id'){
        let _id = props.match.params.id
        let obj = cloneDeep(state.purchase.byListId[_id])
        let partyM = obj.partyMaster ? obj.partyMaster.id : 0
        obj.partyM = partyM
        return obj
    }
}

export const getVdetailsObj = (state, object, index, getIndex, combination) => {
    if(index === ""){
        let obj = purchaseDetails()
        let shift = store.session('getShift')
        obj.shift = shift
        if(combination){
            obj.setStandard = combination.setStandard
            obj.setRate = combination.setRate
        }
        return obj
    }else{
        let obj = object && index !== "" && getIndex !== undefined && getIndex !== null && object.purchaseVoucherDetails ? cloneDeep(object.purchaseVoucherDetails[getIndex]) : {};
        return obj
    }
}

export const getPurchaseParams = (state, props) => {
    const params =  {}
    if(props.match.path==='/purchase/create'){
        params.title = state.purchase.params.createTitle
        params.submitButtonText = state.purchase.params.createSubmitButtonText        
    }else if(props.match.path==='/purchase/edit/:id'){
        params.title = state.purchase.params.editTitle
        params.submitButtonText = state.purchase.params.editSubmitButtonText        
    }
    return params
}

export const selectPurchase = createSelector(
    getPurchaseList,
    purchase => { 
        const keys = Object.keys(purchase)
        const obj = keys.map( (key) => { return { key : key, value : parseInt(key, 10), text : purchase[key].purchaseName }})
        return obj
    }
)