import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import { createGroup } from '../data/actions' 
import { groupSchema, group } from '../data/model'
import { Modal, Form, Button, Table, Icon } from 'semantic-ui-react'
import { Formik, Field, Form as FormikForm } from 'formik'
import { FormikInputComponent } from '../../../utilities/formUtils'

// import UserDropdown from '../../users/components/userDropdown'

const GroupPopup = ({ name, setFieldVal , ...props}) => {

    const [displayStatus, setDisplayStatus] = useState(false)
    const dispatch = useDispatch()

    const saveGroup = (values) => {
        dispatch(createGroup(values))
        setDisplayStatus(false)
    }

    return (
        <Modal size='tiny' open={displayStatus} trigger={<Button type='button' icon size='small' color='vk' title='Add Group' onClick={()=>setDisplayStatus(true)}><Icon name='add circle'/></Button>} onClose={() => setDisplayStatus(false)}>
            <Modal.Header>Create Group</Modal.Header>
            <Modal.Content>
                <Formik id="groupPopupForm" size="large" width={5}
                    initialValues={group()}
                    validationSchema={groupSchema}
                    onSubmit={(values) => saveGroup(values)}
                    render={({ handleSubmit, errors, onChange, values, handleChange, setFieldValue }) => (
                        <Form as={FormikForm} size="small" className="CustomeForm" width={5} onSubmit={handleSubmit}>
                            <Table>
                                <Table.Body>
                                    <Table.Row>
                                        <Table.Cell style={{width: '25%'}}><label className="boldText">Group Name: </label></Table.Cell>
                                        <Table.Cell style={{width: '75%'}}>
                                            <Form.Group widths={2}>
                                                <Field name="groupName" label=' ' component={FormikInputComponent} />
                                            </Form.Group>
                                        </Table.Cell>
                                    </Table.Row>
                                </Table.Body>
                            </Table>
                            <Button type='button' onClick={handleSubmit} icon labelPosition='right' className="CustomeBTN" color='vk' style={{float: 'right'}}>
                                Create
                                <Icon name='add circle' />
                            </Button>
                            <br/><br/><br/><br/>
                        </Form>
                    )}
                />
            </Modal.Content>
        </Modal>
    )
}

export default GroupPopup;