import * as type from './types'

export function savedMisc(misc) {
    return {
        type : type.SAVED_MISC,
        payload : misc.data.data,
        txn: misc.data.type,
        msg: misc.data.msg,
        diff : true
    };
}

export function editMisc(misc) {
    return {
        type : type.EDIT_MISC,
        payload : misc,
        txn : 'initiated',
    };
}

export function fetchMisc(params) {
    return {
        type : type.FETCH_MISC,
        payload : params
    };
}

export function fetchedMisc(misc) {
    return {
        type : type.FETCHED_MISC,
        payload : misc
    };
}


export function setNotifyDoneMisc(props) {
    return {
        type : type.NOTIFICATION_DONE_MISC,
        payload: props
    };
}